import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox} from 'antd';
import moment from 'moment';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';
import MoneyField from '../../common/fields/money';
import PhoneField from '../../common/fields/phone';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('changePassModal', ()=>{
        let formValues={};
        this.setState({visible:true,formValues});
      
    });
  	this.state={
      formValues:{},
      visible:false
  	};
  }

  handleOk=()=>{
    util.validateFields.call(this,values=>{
      util.ajaxJson(urlPrefix+'SysUserFramework?opt=changePwd', {...values}, res=>{
        if(res.success){
          message.success(`修改成功!`);
          this.handleCancel();
        }
      });
    });
  }

  handleCancel=()=>{
    this.setState({visible:false});
  }

  render() {
    let {visible,formValues} = this.state;
  	return (<Modal title={`系统用户修改密码`} className="modal-form" visible={visible} width={600} destroyOnClose onCancel={this.handleCancel} maskClosable={false} footer = {
        [<Button key="1" type="primary" size="large" onClick={this.handleOk}>
            修改
         </Button>,
         <Button key="2" type="ghost" size="large" onClick={this.handleCancel}>
            取消
         </Button>]
      }>
          <Form className="change-pass-modal">
            <Form.Item className="memberName-f" label="密码" required>
                <Input {...util.getField.call(this,'pass',{rules:[{required:true,message:'必填'}]})} />
            </Form.Item>
            
          </Form>
      </Modal>);
  }
}

export default BizComponent;