import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox} from 'antd';
import moment from 'moment';
import './index.less';
import {util,emitter} from '../../../common/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';
import MoneyField from '../../common/fields/money';
import {imagePrefix} from '../../common/fields/upload';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('memberRechargeModal', (rec)=>{
        let formValues={billType:rec.billType,gmtCreate:moment()};
        this.setState({visible:true,formValues,rec});
      
    });
  	this.state={
      formValues:{},
      rec:{},
      visible:false
  	};
  }

  handleOk=()=>{
    util.validateFields.call(this,values=>{
      let {rec} = this.state;
      if(rec.billType!=values.billType){
        Modal.confirm({
            title:"消费类型变更确认",
            content:`消费类型已从【${rec.billType}】变成了【${values.billType}】，${rec.billType=='按次'?`原先的剩余次数【${rec.remainTimes}】将会被清零！`:'计费周期将重新开始计算'}请确认无误！`,
            onOk:()=>this.doRecharge(rec,values)
        });
      }else{
        this.doRecharge(rec,values);
      }
    });
  }
  doRecharge=(rec,values)=>{
    util.ajaxJson(urlPrefix+'MemberOperation?opt=memberRecharge', {...rec,...values}, res=>{
        if(res.success){
          message.success(`充值成功!`);
          this.handleCancel();
        }
    });
  }

  handleCancel=()=>{
    emitter.emit(`queryMemberTable`, undefined, true);
    this.setState({visible:false});
  }

  getDelayTime=rec=>{
    let expMoment=moment(rec.expireTime);
    if(rec.billType=='包月'){
      expMoment.add(1,'month');
    }else if(rec.billType=='包半年'){
      expMoment.add(6,'month');
    }else if(rec.billType=='包年'){
      expMoment.add(12,'month');
    }
    return expMoment.valueOf();
  }

  render() {
    let {visible,formValues,rec} = this.state;
  	return (<Modal title={`会员充值续费`} className="modal-form" visible={visible} width={1200} destroyOnClose onCancel={this.handleCancel} maskClosable={false} footer = {
        [<Button key="1" type="primary" size="large" onClick={this.handleOk}>
            充值
         </Button>,
         <Button key="2" type="ghost" size="large" onClick={this.handleCancel}>
            取消
         </Button>]
      }>
          <Form className="member-consume-modal">
            <Form.Item className="memberName-f" label="会员名">
                {rec.name}
                {rec.avatar&&<img alt="" src={imagePrefix()+rec.avatar}></img>}
            </Form.Item>
            <Form.Item className="" label="手机号">
                {rec.phone}
            </Form.Item>
            <Form.Item className="amount-f" label="充值金额(元)" required>
              <MoneyField form={this} name="amount"/>
            </Form.Item>
            <Form.Item className="billType-f" label="充值类型" required>
              <Select {...util.getField.bind(this)('billType',{rules:[{required:true,message:'必填'}]})}>
                {[{key:'按次',text:'按次'},{key:'包月',text:'包月'},{key:'包半年',text:'包半年'},{key:'包年',text:'包年'}].map(it=>{
                  return <Select.Option key={it.key} value={it.key}>{it.text}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            {formValues.billType!='按次'&&<><Form.Item className="" label="原先到期时间">{util.formatDate(rec.expireTime,'yyyy-MM-dd')}</Form.Item>
              <Form.Item className="" label="充值后到期时间">{util.formatDate(this.getDelayTime(rec),'yyyy-MM-dd')}</Form.Item>
            </>}
            {formValues.billType=='按次'&&<Form.Item className="" label="充值次数" required>
            <InputNumber {...util.getField.call(this,'rechargeTimes',{rules:[{required:true,message:'必填'}]})} min={1} precision={0}/>
            </Form.Item>}
            {rec.billType=='按次'&&<Form.Item className="amount-f" label="剩余次数" required>
              {rec.remainTimes} {!isNaN(formValues.rechargeTimes)&&formValues.rechargeTimes>0&&<span>+ {formValues.rechargeTimes} = {rec.remainTimes+formValues.rechargeTimes}</span>}
            </Form.Item>}
            <Form.Item className="" label="充值时间" required>
                <DatePicker showTime {...util.getField.call(this,'gmtCreate')} />
            </Form.Item>
          </Form>
      </Modal>);
  }
}

export default BizComponent;