import React, { Component } from 'react';
import {message,Input,Modal,Button,Form,Select,Collapse,Popconfirm,Tooltip,Popover,DatePicker,InputNumber,Tag } from 'antd';
import { emitter, util } from '../../../common/common';
import {allRoles,isAdmin,allStaffs,rooms,coaches} from '../../../entry/index/routes/IndexPage';
export default class BizComponent extends Component {
    constructor(...args) {
        super(...args);
    }
    render() {
        let {name,form,required,onChange,showPrice}=this.props;
        return <>
            <Select {...util.getField.bind(form)('coachId',{onChange,rules:[{required,message:'必填'}]})}>
                {coaches.map(it=>{
                  return <Select.Option key={it.id} value={it.id+''}>{it.name} {showPrice&&<span>[{it.unitPrice}元/小时]</span>}</Select.Option>;
                })}
              </Select>
        </>;
    }
}