import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Upload, Popconfirm} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,urls,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';
import AddMemberModal from './addMemberModal';
import ModifyMemberModal from './modifyMemberModal';
import MemberConsumeModal from './memberConsumeModal';
import RechargeMemberModal from './rechargeMemberModal';
import {imagePrefix} from '../../common/fields/upload';
import MemberRechargeBillListDrawer from './memberRechargeBillListDrawer';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    this.config = {
      title:'会员管理',
      key:'Member',
      dialog:{width:1060},
      queryCond:{
        conditions:[[{key:'phone'},{key:'name'},{key:'operator'}]],
        extButtons:()=>{
          let extBtns=[<Button key="1" type="primary" size="small" className="search-button" onClick={()=>emitter.emit('addMemberModal')}><Icon type="plus" />新建会员</Button>,urls.map(it=>it.code).indexOf('downloadMember')>-1&&<Popconfirm key="export" title="确定要导出吗？" onConfirm={()=>window.location.href=urlPrefix+'download/MemberOperation?opt=downloadMember'}><Button size="small" className="search-button"><Icon type="download" />导出</Button></Popconfirm>,
          urls.map(it=>it.code).indexOf('uploadMember')>-1&&<Upload key="file" name="file" className="search-button" action={urlPrefix+'upload/MemberOperation?opt=uploadMember'} fileList={this.state.fileList} withCredentials onChange={res=>{
            if (res.file.status=='done') {
              if(res.file.response.success){
                message.success(res.file.response.data);
                emitter.emit(`queryMemberTable`);
              }else{
                message.error(`文件导入失败：${res.file.response.errMsg}`, 20);
              }
            } else if(res.file.status=='error') {
              message.error(`文件上传错误`);
            }
            let {fileList}=res;
            if(fileList&&fileList.length>1)fileList.splice(0,1);
            this.setState({fileList});
          }}>
            <Tooltip title="一旦选中文件后即开始导入并生效，请确保excel文件数据的准确性！"><Button size="small" type="danger"><Icon type="upload" />导入</Button></Tooltip>
          </Upload>];
          return extBtns.filter(it=>!!it);
        },
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          this.queryParam=queryParam;
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInForm:true,
          notInTable:true,
          label:'id'
        },{
          key:'no',
          label:'序号'
        },{
          key:'name',
          width:'10%',
          rules:[{required: true, message: '必填'}],
          render:(v,r)=>{
            if(r.avatar){
              return <Tooltip overlayClassName="avatar-tooltip" title={<div className="avatar-image-c" style={{backgroundImage:`url(${imagePrefix()+r.avatar})`}} />}><a>{v}</a></Tooltip>
            }
            return v;
          },
          label:'会员名'
        },{
          key:'phone',
          width:'10%',
          props:{
            maxLength:11
          },
          rules:[{required: true, message: '必填'},{validator:v=>{
            return /^1[0-9]{10}$/.test(v);
          },message:'必须为1开头的11位手机号'}],
          label:'手机号'
        },{
          key:'amount',
          width:'10%',
          rules:[{required: true, message: '必填'}],
          render:(v,r)=><a onClick={()=>emitter.emit('show-member-recharge-bill',r)}>{v}</a>,
          label:'充值金额'
        },{
          key:'billType',
          width:'10%',
          rules:[{required: true, message: '必填'}],
          enums:[{key:'按次',text:'按次'},{key:'包月',text:'包月'},{key:'包半年',text:'包半年'},{key:'包年',text:'包年'}],
          label:'卡计费类型'
        },{
          key:'remainTimes',
          width:'6%',
          label:'剩余次数'
        },{
          key:'memberType',
          width:'6%',
          label:'会员类型'
        },{
          key:'operator',
          width:'6%',
          label:'经办人'
        },{
          key:'applyTime',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'生效时间'
        },{
          key:'expireTime',
          notInForm:true,
          width:'8%',
          render:(v,r)=>util.formatDate(v,'yyyy-MM-dd'),
          label:'失效时间'
        },{
          key:'gmtCreate',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'创建时间'
        }
      ],
      operationAddon:rec=>{
        let useEmpty=false;
        if(rec.billType=='按次'){
          if(+rec.remainTimes<=0)
            useEmpty=true;
        }else{
          if(rec.expireTime&&rec.expireTime<new Date().getTime()){
            useEmpty=true;
          }
        }
        return (<span><a disabled={useEmpty} onClick={()=>{
          emitter.emit('memberConsumeModal',rec);
        }}>消费</a><a onClick={()=>{
          emitter.emit('memberRechargeModal',rec);
        }}>充值</a><a onClick={()=>{
          emitter.emit('modifyMemberModal',rec);
        }}>修改</a>{!rec.recordCount&&<Popconfirm title="确定删除会员？" onConfirm={()=>{
          util.ajaxJson(urlPrefix+'MemberOperation?opt=deleteMember',{id:rec.id},res=>{
            if(res.success){
              message.success('删除会员成功');
              emitter.emit(`queryMemberTable`, undefined, true);
            }
          });
        }}><a>删除</a></Popconfirm>}</span>);
      },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {};
        },
        beforeModify:formData=>{
          return formData;
        },
        beforeSubmit:formData=>{
          formData.ext=JSON.stringify({staffs:formData.staffs});
          delete formData.staffs;
          return formData;
        }
      },
      table:{
        bordered:true,
        size:'small',
        expandedRowKeys:()=>this.state.expandedRowKeys,
        expandIcon:(pops)=>{
          let {expandedRowKeys}=this.state;
          let expanded=expandedRowKeys.indexOf(pops.record.id)>-1;
          if(!pops.record.recordCount)return;
          return <Icon type={expanded?"minus-square":"plus-square"} onClick={()=>{
            let {expandedRowKeys}=this.state;
            if(expanded){
              expandedRowKeys=[];
            }else{
              expandedRowKeys=[];
              expandedRowKeys.push(pops.record.id);
            }
            this.setState({expandedRowKeys},this.refreshExpandRow);
          }}/>
        },
        expandedRowRender:(record, index, indent, expanded)=>{
          let {recDs}=this.state;
          return <Table className="member-rec-table" columns={[
          {key:'gmtCreate',dataIndex:'gmtCreate',title:'消费时间',render:v=>util.formatDate(v)},
          {key:'consumeTimes',dataIndex:'consumeTimes',title:'次数'},
          {key:'consumeType',dataIndex:'consumeType',title:'消费类型'},
          {key:'roomId',dataIndex:'roomId',title:'球馆',render:v=>{
            let room=rooms.filter(it=>it.id==v)[0];
            if(room)return room.name;
          }},
          {key:'operator',dataIndex:'operator',title:'经办人'},
          {key:'opt',dataIndex:'opt',title:'操作',render:(v,r)=><Popconfirm title="确定要删除？" onConfirm={()=>{
            util.ajaxJson(urlPrefix+'MemberOperation?opt=deleteMemberBill',{memberId:record.id,id:r.id},res=>{
              if(res.success){
                message.success('删除流水成功');
                this.refreshExpandRow();
              }
            });
          }}><a>删除</a></Popconfirm>}]} rowKey={r=>r.id} dataSource={recDs.data} pagination={recDs.totalItem<=10?false:{total:recDs.totalItem}} size="small"/>;
        },
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.list.forEach(it=>{
          });
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'MemberOperation?opt=queryMember'},
        // updateInterface:{func:urlPrefix+'MemberOperation?opt=updateMember'},
        // deleteInterface:{func:urlPrefix+'MemberOperation?opt=deleteMember',params:{id:'id'}}
      }
    };
    emitter.on('refresh-memeber-expand',this.refreshExpandRow);
    this.state={
      fileList:[],
      expandedRowKeys:[],
      recDs:{}
    };
  }

  refreshExpandRow=()=>{
    let {expandedRowKeys}=this.state;
    if(expandedRowKeys&&expandedRowKeys.length>0){
      util.ajaxJson(urlPrefix+'MemberOperation?opt=queryMemberBill',{memberId:expandedRowKeys[0]},res=>{
        this.setState({recDs:res.data});
      });
    }
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
       <AddMemberModal />
       <ModifyMemberModal />
       <MemberConsumeModal />
       <RechargeMemberModal />
       <MemberRechargeBillListDrawer />
      </div>
    );
  }
}