import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Tabs, Form, Select, Popconfirm, InputNumber} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';

import QueryCond from '../../../components/common/table/queryCond';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on(`menu-clicked-sys/config`,()=>{
      this.initTenantConfig();
    });
    this.initTenantConfig()
    this.state={
        config:{},
        formValues:{}
    };
  }
  initTenantConfig=()=>{
    util.ajaxJson(urlPrefix+'SysUserFramework?opt=getTenantConfig',{},res=>{
        let config=res.data||{};
        if(!config.billCates)config.billCates=['会员开户','学员开户','学员充值'];
        if(!config.courseTypes)config.courseTypes=['乒乓'];
        if(!config.majorCoachRate)config.majorCoachRate=55;
        if(!config.coachRate)config.coachRate=50;
        let formValues={...config};
        this.setState({config,formValues});
    });
  }

  render() {
    let {config}=this.state;
    return (
      <div className="sys-config-c">
       <Tabs defaultActiveKey="1">
            <Tabs.TabPane tab="属性配置" key="1">
                <Form.Item label="课程类型" required>
                    <Select mode="tags" placeholder="请输入需要新增的课程类型供选择" {...util.getField.call(this,'courseTypes')}>
                        {config.courseTypes&&config.courseTypes.map(it=><Select.Option key={it} value={it}>{it}</Select.Option>)}
                    </Select>
                </Form.Item>
                <Form.Item label="账务分类" required>
                    <Select mode="tags" placeholder="请输入需要新增的账务分类供选择" {...util.getField.call(this,'billCates')}>
                        {config.billCates&&config.billCates.map(it=><Select.Option key={it} value={it}>{it}</Select.Option>)}
                    </Select>
                </Form.Item>
                <Form.Item label="教练分账比例" required>
                  <div>主管教练：<InputNumber {...util.getField.call(this,'majorCoachRate',{rules:[{required:true,message:'必填'}]})} precision={0} min={1} max={100}/>%</div>
                  <div>普通教练：<InputNumber {...util.getField.call(this,'coachRate',{rules:[{required:true,message:'必填'}]})} precision={0} min={1} max={100}/>%</div>
                </Form.Item>
            </Tabs.TabPane>
        </Tabs>
        <Popconfirm title="请您确认配置的准确性，保存后系统将直接生效，确定无误？" onConfirm={()=>{
          let {config,formValues}=this.state;
          config = {...config,...formValues};
            util.ajaxJson(urlPrefix+'SysUserFramework?opt=saveTenantConfig',{config},res=>{
                if(res.success){
                    message.success('保存系统配置成功！');
                    emitter.emit('init-user-info');
                }
            });
        }}><Button type="primary">保存</Button></Popconfirm>
      </div>
    );
  }
}