import React, {Component, PropTypes} from 'react';
import {Select,message,Radio,Popover,DatePicker } from 'antd';
import moment from 'moment';
import {util,emitter} from '../../common/common';
import {prefix,domain,productDomain,productPrefix} from '../../interfaces/interfaces';
import './index.less';

export default class BizComp extends Component {
  static defaultProps = {
  };
  constructor(...args) {
    super(...args);
    this.state={
      value:this.props.value,
      customRangeVisible:false
    };
  }

  componentDidUpdate(preProps){
		if(this.props.value!=preProps.value){
			this.setState({value:this.props.value});
		}
	}

  onRadioChange=e=>{
    let value=e;
    if(e&&e.target)value=e.target.value;
    let {rangeValue}=this.state;
    let thisRangeValue,now=Date.now(),customRangeVisible=false;
    if(value=='0')thisRangeValue=[util.formatDate(now,'yyyy-MM-dd')+' 00:00:00.000',util.formatDate(now,'yyyy-MM-dd')+' 23:59:59.999'];
    if(value=='1')thisRangeValue=[util.formatDate(now-86400000,'yyyy-MM-dd')+' 00:00:00.000',util.formatDate(now-86400000,'yyyy-MM-dd')+' 23:59:59.999'];
    if(value=='30')thisRangeValue=[moment(moment().month(moment().month()).startOf('month').valueOf()).format('YYYY-MM-DD HH:mm:ss'),moment(moment().month(moment().month()).endOf('month').valueOf()).format('YYYY-MM-DD HH:mm:ss')];
    if(value=='-30')thisRangeValue=[moment(moment().month(moment().month()-1).startOf('month').valueOf()).format('YYYY-MM-DD HH:mm:ss'),moment(moment().month(moment().month()-1).endOf('month').valueOf()).format('YYYY-MM-DD HH:mm:ss')];
    if(value=='100'){
      customRangeVisible=true;
      rangeValue=undefined;
    }
    this.props.onChange(thisRangeValue, value);
    this.setState({value,rangeValue,customRangeVisible});
  }
  
  onRangeChange=rangeValue=>{
    this.props.onChange([rangeValue[0].startOf('day').format('YYYY-MM-DD HH:mm:ss'),rangeValue[1].endOf('day').format('YYYY-MM-DD HH:mm:ss')],'100');
    this.setState({rangeValue,customRangeVisible:false});
  }

  render(){
    let {value,rangeValue,customRangeVisible}=this.state;
    let {validValues,modeType}=this.props;
    let customTimeShow;
    if(value=='30')customTimeShow=<>{moment(moment().month(moment().month()).startOf('month').valueOf()).format('YYYY-MM-DD')} ~ {moment(moment().month(moment().month()).endOf('month').valueOf()).format('YYYY-MM-DD')}</>;
    if(value=='-30')customTimeShow=<>{moment(moment().month(moment().month()-1).startOf('month').valueOf()).format('YYYY-MM-DD')} ~ {moment(moment().month(moment().month()-1).endOf('month').valueOf()).format('YYYY-MM-DD')}</>;
    if(value=='100'&&rangeValue&&rangeValue.length>0)customTimeShow=<>{rangeValue[0].format('YYYY-MM-DD')} ~ {rangeValue[1].format('YYYY-MM-DD')}</>;
    return (
        <>
        {modeType=='select'&&<Select style={{position:'relative'}} size="small" value={value} onChange={this.onRadioChange}>
            {(!validValues||validValues.indexOf('-1')>-1) && <Select.Option value="-1">全部</Select.Option>}
            {(!validValues||validValues.indexOf('0')>-1) && <Select.Option value="0">今天</Select.Option>}
            {(!validValues||validValues.indexOf('1')>-1) && <Select.Option value="1">昨天</Select.Option>}
            {(!validValues||validValues.indexOf('30')>-1) && <Select.Option value="30">本月</Select.Option>}
            {(!validValues||validValues.indexOf('-30')>-1) && <Select.Option value="-30">上月</Select.Option>}
            <Select.Option value="100" onClick={()=>this.setState({customRangeVisible:true})}>自定义</Select.Option>
            
        </Select>}
        {modeType=='select'&&<Popover title="请选择自定义时间段" visible={customRangeVisible} content={<DatePicker.RangePicker value={this.state.rangeValue} onChange={this.onRangeChange} disabledDate={current=>current && current > moment().endOf('day')}/>} trigger="click">  
        </Popover>}
        {modeType!='select'&&<Radio.Group style={{position:'relative'}} size="small" value={value} onChange={this.onRadioChange}>
            {(!validValues||validValues.indexOf('-1')>-1) && <Radio.Button value="-1">全部</Radio.Button>}
            {(!validValues||validValues.indexOf('0')>-1) && <Radio.Button value="0">今天</Radio.Button>}
            {(!validValues||validValues.indexOf('1')>-1) && <Radio.Button value="1">昨天</Radio.Button>}
            {(!validValues||validValues.indexOf('30')>-1) && <Radio.Button value="30">本月</Radio.Button>}
            {(!validValues||validValues.indexOf('-30')>-1) && <Radio.Button value="-30">上月</Radio.Button>}
            <Popover title="请选择自定义时间段" visible={customRangeVisible} content={<DatePicker.RangePicker value={this.state.rangeValue} onChange={this.onRangeChange} disabledDate={current=>current && current > moment().endOf('day')}/>} trigger="click">
              <Radio.Button value="100" onClick={()=>this.setState({customRangeVisible:true})}>自定义</Radio.Button>
            </Popover>
        </Radio.Group>}
        <span className="date-pick-tips">{customTimeShow}</span>
        </>);
  }
}