import React, { Component } from 'react';
import copy from 'copy-to-clipboard';
import { emitter, util } from '../../../common/common';
import {message,Input,Modal,Button,Form,Select,Collapse,Popconfirm,Tooltip,Popover,DatePicker,InputNumber,Tag } from 'antd';
import './index.less';
import Area,{getDisplayText} from './area';
import { pca, pcaa } from 'area-data';
import { urlPrefix } from '../../../interfaces/interfaces';
import SaveParams from '../../../components/pops/saveParams';

export default class BizComponent extends Component {
    constructor(...args) {
        super(...args);
        emitter.on('do-indicates-search',formValues=>{
            this.setState({formValues},()=>{
                this.doSearch(null,true);
            });
        });
        this.state = {
            formValues:{
                // company_name:'丝芳'
            },
            disableSearch:false
        };
        
    }

    getIndicates=()=>{// 上层组件调用获取组件的数据
        let {formValues}=this.state;
        let {indicates}=this.props;
        let indicatesMap={};
        for(let k in formValues){
            let indicate=indicates.filter(it=>it.key==k)[0];
            let idx;
            if((idx=k.indexOf('__'))>-1){
                let newK=k.substring(0,idx);
                indicate=indicates.filter(it=>it.key==newK)[0];
                if(!indicate)continue;
                indicatesMap[newK]=[formValues[newK+'__start']||null,formValues[newK+'__end']||null];
                indicatesMap[newK+'--'+indicate.type]=true;
            }else if(Array.isArray(formValues[k])){
                if(!indicate)continue;
                indicatesMap[k]=formValues[k].map(it=>{
                    if(it&&it.valueOf)return it.valueOf();
                    return it;
                });
                indicatesMap[k+'--'+indicate.type]=true;
            }else{
                if(formValues[k]===''||formValues[k]===null||formValues[k]===undefined)continue;
                if(['prov','city','county'].indexOf(k)>-1){
                    let text;
                    if(k=='prov'){
                        text=pca['86'][formValues[k]];
                    }else if(k=='city'){
                        text=pcaa[formValues.prov][formValues[k]];
                    }else{
                        text=pcaa[formValues.city][formValues[k]];
                    }
                    indicatesMap[k]=text;
                }else{
                    if(!indicate)continue;
                    indicatesMap[k]=formValues[k];
                    indicatesMap[k+'--'+indicate.type]=true;
                }   
            }
        }
        return indicatesMap;
    }

    onChange=e=>{
        this.setState({disableSearch:false});
        this.props.onChange(e);
    }
    doSearch=(e,reserveBtn)=>{
        this.props.onSearch({queryCond:this.getIndicates()});
        this.setState({disableSearch:!reserveBtn});
    }

    showSearchConds=()=>{
        let {indicates}=this.props;
        let {formValues}=this.state;
        return <ul>{indicates.filter(it=>(!Array.isArray(formValues[it.key])&&formValues[it.key])||formValues[it.key+'__start']||formValues[it.key+'__end']||(Array.isArray(formValues[it.key])&&formValues[it.key].length>0)).map((indicate,idx)=><li key={'s-i-'+idx}>
            <Tag color="geekblue" closable onClose={(e)=>{
                let {formValues}=this.state;
                if(indicate.type=='Area'){
                    formValues['prov']=undefined;
                    formValues['city']=undefined;
                    formValues['county']=undefined;
                }else if(formValues[indicate.key]){
                    formValues[indicate.key]=undefined;
                }else{
                    formValues[indicate.key+'__start']=undefined;
                    formValues[indicate.key+'__end']=undefined;
                }
                this.setState({formValues,disableSearch:false});
                e.stopPropagation();
                e.preventDefault();
            }}>{this.getTagContent(indicate,formValues)}</Tag>
        </li>)}<SaveParams right={80} text="搜索条件" storageItemKey="customer-search" value={formValues} onLoad={values=>{
            this.setState({formValues:values,disableSearch:false});
        }} onSave={this.props.onSaveCond}/><Popconfirm title="确定要清除？" okText="确定" cancelText="取消" onConfirm={()=>{
            this.setState({formValues:{},disableSearch:false});
        }}><a className="clear-search-cond">清除搜索条件</a></Popconfirm></ul>;
    }
    getTagContent=(indicate,formValues)=>{
        let {placeholder,type,key,enums,showTime}=indicate;
        let formValue=formValues[key];
        if(type=='Input'){
            return <span>{placeholder}：{formValue}</span>;
        }else if(type=='Select'){
            if(indicate.multi){
                return <span>{placeholder}：{formValue.map(it=>enums.filter(e=>e.key==it)[0].text).join(',')}</span>;
            }
            return <span>{placeholder}：{(enums.filter(it=>it.key==formValue)[0]||{}).text}</span>;
        }else if(type=='RangePicker'){
            return <span>{placeholder}：{util.formatDate(formValue[0].valueOf(),showTime?undefined:'yyyy-MM-dd')} ~ {util.formatDate(formValue[1].valueOf(),showTime?undefined:'yyyy-MM-dd')}</span>;
        }else if(type=='Range'){
            return <span>{placeholder}：{formValues[key+'__start']} ~ {formValues[key+'__end']}</span>;
        }else if(type=='Area'){
            return <span>{placeholder}：{getDisplayText(formValues.prov,formValues.city,formValues.county)}</span>;
        }
        
    }

    onCollapse=(keys)=>{
        if(this.props.onCollapse){
            this.props.onCollapse(!keys || keys.length==0);
        }
    }
   
    render() {
        let {indicates}=this.props;
        let {formValues,disableSearch}=this.state;
        return <div className="indicates-c" ref={el=>{
                    this.indicatesUi = el;
                }}>
                <Collapse onChange={this.onCollapse} defaultActiveKey={['1']}>
                    <Collapse.Panel header="搜索条件" key="1">
                    <Form.Item label="搜索指标" className="search-label">
                        <ul>
                            {indicates.map((indicate,idx)=><li key={'indi-it'+idx} className={indicate.type}>
                                {indicate.type=='Input'&&<Input onPressEnter={this.doSearch} {...util.getField.call(this,indicate.key,{onChange:this.onChange})} placeholder={indicate.placeholder} size="small">
                                </Input>}
                                {indicate.type=='Select'&&<Tooltip title={indicate.placeholder} mouseEnterDelay={0.8}><Select {...util.getField.call(this,indicate.key,{onChange:e=>{
                                    if(indicate.onChange)indicate.onChange(e,this);
                                    this.onChange(e);
                                }})} mode={indicate.multi?'multiple':undefined} placeholder={indicate.placeholder} allowClear size="small" {...indicate.props}>
                {indicate.enums.map((enumItem,idx)=><Select.Option key={'enum'+idx} value={enumItem.key}>{enumItem.text}</Select.Option>)}
                                </Select></Tooltip>}
                                {indicate.type=='RangePicker'&&<Tooltip title={indicate.placeholder} mouseEnterDelay={0.8}><DatePicker.RangePicker {...util.getField.call(this,indicate.key,{onChange:this.onChange,mode:'RangePickerDate'})} showTime={indicate.showTime} placeholder={[indicate.placeholder,indicate.placeholder]} size="small">
                                </DatePicker.RangePicker></Tooltip>}
                                {indicate.type=='Range'&&<div className="range-c"><Tooltip title={indicate.placeholder+'起始值'} mouseEnterDelay={0.8}><InputNumber onPressEnter={this.doSearch} {...util.getField.call(this,indicate.key+'__start',{onChange:this.onChange})} placeholder={indicate.placeholder} size="small" precision={indicate.precision}>
                                </InputNumber></Tooltip><i>~</i><Tooltip title={indicate.placeholder+'截止值'} mouseEnterDelay={0.8}><InputNumber {...util.getField.call(this,indicate.key+'__end',{onChange:this.onChange})} placeholder={indicate.placeholder} size="small" precision={indicate.precision}>
                                </InputNumber></Tooltip></div>}
                                {indicate.type=='Area'&&<Area context={this} onChange={this.onChange} placeholder={indicate.placeholder} size="small">
                                </Area>}
                            </li>)}
                        </ul>
                    </Form.Item>
                    <Button type="primary" size="small" className="search-btn" onClick={this.doSearch} disabled={disableSearch}>搜索</Button>
                    <Form.Item label="当前搜索条件" className="search-label">{this.showSearchConds()}</Form.Item>
                    </Collapse.Panel>
                </Collapse>
            </div>;
    }

}