import React, { Component } from 'react';
import {message,Input,Modal,Button,Form,Select,Collapse,Popconfirm,Tooltip,Popover,DatePicker,InputNumber,Tag } from 'antd';
import { emitter, util } from '../../../common/common';
export default class BizComponent extends Component {
    constructor(...args) {
        super(...args);
    }
    render() {
        let {name,form,required}=this.props;
        return <>
            <Form.Item className="" label="培训类型" required={required}>
              <Select {...util.getField.bind(form)('type',{rules:[{required,message:'必填'}]})}>
                  {[{key:'乒乓',text:'乒乓'},{key:'羽毛球',text:'羽毛球'}].map(it=>{
                    return <Select.Option key={it.key} value={it.key}>{it.text}</Select.Option>;
                  })}
                </Select>
            </Form.Item>
            <Form.Item className="" label="培训子类" required={required}>
              <Select {...util.getField.bind(form)('subType',{rules:[{required,message:'必填'}]})}>
                  {['基础','多球','实战'].map(it=>{
                    return <Select.Option key={it} value={it}>{it}</Select.Option>;
                  })}
                </Select>
            </Form.Item>
        </>;
    }
}