import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Upload, Popconfirm} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,urls,allStaffs,rooms, coaches} from '../../../entry/index/routes/IndexPage';
import GameModal from './gameModal';
export let projects=['乒乓'];
export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);

    this.config = {
      title:'比赛管理',
      key:'Game',
      dialog:{width:1060},
      queryCond:{
        conditions:[[{key:'startTime',label:'开始时间',type:'datetime'},{key:'endTime',label:'截止时间',type:'datetime'}]],
        extButtons:()=>{
          let extBtns=[<Button key="1" type="primary" size="small" className="search-button" onClick={()=>emitter.emit('gameModal',undefined,'add')}><Icon type="plus" />新建比赛</Button>];
          return extBtns.filter(it=>!!it);
        },
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInForm:true,
          notInTable:true,
          label:'id'
        },{
          key:'name',
          width:'10%',
          rules:[{required: true, message: '必填'}],
          label:'名称'
        },{
            key:'gameDesc',
            width:'30%',
            label:'比赛描述'
        },{
            key:'project',
            width:'6%',
            enums:projects,
            label:'比赛项目'
        },{
          key:'rule',
          width:'20%',
          label:'比赛规则'
        },{
          key:'status',
          width:'6%',
          enums:[{key:0,text:'未完成'},{key:1,text:'已完成'}],
          label:'状态'
        },{
          key:'time',
          width:'10%',
          render:(v,r)=>util.formatDate(r.startTime),
          label:'比赛时间'
        },{
          key:'gmtModified',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'修改时间'
        }
      ],
      operationAddon:rec=>{
        let useEmpty=false;
        if(rec.amount<=0){
          useEmpty=true;
        }
        return (<span style={{display:rec.status==1?'none':undefined}}><a onClick={()=>{
          emitter.emit('gameModal',rec,'modify');
        }}>修改</a>{!rec.recordCount&&<Popconfirm title="确定删除比赛？" onConfirm={()=>{
          util.ajaxJson(urlPrefix+'GameOperation?opt=deleteGame',{id:rec.id},res=>{
            if(res.success){
              message.success('删除成功');
              emitter.emit(`queryGameTable`, undefined, true);
            }
          });
        }}><a>删除</a></Popconfirm>}</span>);
      },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {};
        },
        beforeModify:formData=>{
          return formData;
        },
        beforeSubmit:formData=>{
          formData.ext=JSON.stringify({staffs:formData.staffs});
          delete formData.staffs;
          return formData;
        }
      },
      table:{
        bordered:true,
        size:'small',
        expandedRowKeys:()=>this.state.expandedRowKeys,
        expandIcon:(pops)=>{
          let {expandedRowKeys}=this.state;
          let expanded=expandedRowKeys.indexOf(pops.record.id)>-1;
          if(!pops.record.recordCount)return;
          return <Icon type={expanded?"minus-square":"plus-square"} onClick={()=>{
            let {expandedRowKeys}=this.state;
            if(expanded){
              expandedRowKeys=[];
            }else{
              expandedRowKeys=[];
              expandedRowKeys.push(pops.record.id); 
            }
            this.setState({expandedRowKeys},this.refreshExpandRow);
          }}/>
        },
        expandedRowRender:(record, index, indent, expanded)=>{
          let {recDs}=this.state;
          return <Table className="student-rec-table" columns={[
          {key:'studyTime',dataIndex:'studyTime',title:'训练时间',render:v=>util.formatDate(v)},
          {key:'coachId',dataIndex:'coachId',title:'教练',render:v=>coaches.filter(it=>it.id==v)[0].name},
          {key:'consumeTimes',dataIndex:'consumeTimes',title:'课时数'},
          {key:'consumeAmount',dataIndex:'consumeAmount',title:'消费金额(元)'},
          {key:'operator',dataIndex:'operator',title:'经办人'},
          {key:'opt',dataIndex:'opt',title:'操作',render:(v,r)=><Popconfirm title="确定要删除？" onConfirm={()=>{
            util.ajaxJson(urlPrefix+'TrainOperation?opt=deleteStudentRecord',{studentId:record.id,id:r.id},res=>{
              if(res.success){
                message.success('删除流水成功');
                this.refreshExpandRow();
              }
            });
          }}><a>删除</a></Popconfirm>}]} rowKey={r=>r.id} dataSource={recDs.data} pagination={recDs.totalItem<=10?false:{total:recDs.totalItem}} size="small"/>;
        },
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.list.forEach(it=>{
          });
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'GameOperation?opt=queryGame'}
      }
    };
    this.state={
      fileList:[],
      expandedRowKeys:[],
      recDs:{totalItem:0}
    };
  }

  refreshExpandRow=()=>{
    let {expandedRowKeys}=this.state;
    if(expandedRowKeys&&expandedRowKeys.length>0){
      util.ajaxJson(urlPrefix+'GameOperation?opt=queryBallMate',{memberId:expandedRowKeys[0]},res=>{
        this.setState({recDs:res.data});
      });
    }
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
       <GameModal />
      </div>
    );
  }
}