import React, { Component } from 'react';
import {message,Input,Modal,Button,Form,Select,Collapse,Popconfirm,Tooltip,Popover,DatePicker,InputNumber,Tag } from 'antd';
import { emitter, util } from '../../../common/common';
import './index.less';
export default class BizComponent extends Component {
    constructor(...args) {
        super(...args);
    }
    amountTags=[{color:'blue',value:50},{color:'cyan',value:100},{color:'green',value:200},{color:'gold',value:500},{color:'orange',value:1000}];
    render() {
        let {name,form,hideLabel}=this.props;
        let {formValues}=form.state;
        return <>
            <InputNumber {...util.getField.call(form,name?name:'amount',{rules:[{required:true,message:'必填'}]})} min={0} precision={0}/>
            {!hideLabel&&this.amountTags.map(it=><Tag className="money-tag" key={'tag'+it.value} color={it.color} onClick={()=>{
            let {formValues}=form.state;
            if(isNaN(formValues.amount))formValues.amount=0;
            formValues.amount=formValues.amount+it.value;
            form.setState({formValues},()=>util.validateFields.call(form,()=>{},['amount']));
            }}>{it.value}</Tag>)}
            {formValues.amount>0&&util.convertToChinaNum(formValues.amount)&&<span className="amount-tip">{util.convertToChinaNum(formValues.amount)}元</span>}
        </>;
    }
}