import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on(`menu-clicked-operate/memberBill`,()=>{
      emitter.emit('queryMemberBillTable');
    });
    this.config = {
      title:'会员流水',
      key:'MemberBill',
      dialog:{width:1060},
      queryCond:{
        conditions:[[{key:'memberName'},{key:'phone'}]],
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          this.queryParam=queryParam;
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInTable:true,
          label:'id'
        },{
          key:'memberName',
          width:'10%',
          label:'会员名'
        },{
          key:'phone',
          width:'10%',
          label:'手机号'
        },{
          key:'operator',
          width:'10%',
          label:'操作人'
        },{
          key:'consumeType',
          width:'10%',
          label:'消费类型'
        },{
          key:'consumeTimes',
          width:'10%',
          label:'消费次数'
        },{
          key:'gmtCreate',
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'操作时间'
        },{
          key:'roomId',
          width:'10%',
          enums:rooms.map(it=>({key:it.id+'',text:it.name})),
          label:'球馆'
        }
      ],
      // operationAddon:record=>{
      //   return (<span><a onClick={()=>{
          
      //   }}>设计</a></span>);
      // },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {status:'0'};
        },
        beforeModify:formData=>{
          return formData;
        },
        beforeSubmit:this.getFormData
      },
      table:{
        bordered:true,
        lazyInit:true,
        size:'small',
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'MemberOperation?opt=queryMemberBill'},
      }
    };
    this.state={
    };
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
      </div>
    );
  }
}