import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon, DatePicker, Popconfirm} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import BillModal,{bodyDomains} from './billModal';
import EditableField from '../../../components/pops/editableField';
import DatePick from '../../../components/select/datePick';
import {allRoles,tenantConfig,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on(`menu-clicked-bill/queryBill`,()=>{
      emitter.emit('queryBillTable');
    });
    this.config = {
      title:'账单管理',
      key:'Bill',
      dialog:{width:1060},
      queryCond:{
        conditions:[[{key:'io'},{key:'domain'},{key:'cate'}],[{key:'subCate'},{key:'relInfo'},{key:'time',label:'时间范围',type:'custom',getComponent:(props,context)=>{
          return <DatePick onChange={rangeValue=>{
            props.onChange(rangeValue);
          }}/>;
        }}]],
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          if(queryParam.time&&queryParam.time.length>0){
            queryParam.startTime=queryParam.time[0];
            queryParam.endTime=queryParam.time[1];
          }
          return queryParam;
        },
        extButtons:()=>{
          let extBtns=[<Button key="1" type="primary" size="small" className="search-button" onClick={()=>emitter.emit('billModal',undefined,{manual:true})}><Icon type="plus" />记账</Button>];
          let {ioMap}=this.state;
          if(ioMap){
            let i=ioMap['收入'],o=ioMap['支出'];
            if(i)extBtns.push(<span key="shouru" className="bill-sum-tips i-color">收入：{i.sumAmount}元，共{i.count}笔</span>);
            if(o)extBtns.push(<span key="zhichu" className="bill-sum-tips o-color">支出：{o.sumAmount}元，共{o.count}笔</span>);
          }
          return extBtns.filter(it=>!!it);
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInTable:true,
          label:'id'
        },{
          key:'billTime',
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'记账时间'
        },{
          key:'io',
          width:'6%',
          enums:['收入','支出'],
          render:(v,r)=><span className={r.io=='收入'?'i-color':'o-color'}>{v}</span>,
          label:'出入账'
        },{
          key:'amount',
          width:'8%',
          render:(v,r)=><span className={r.io=='收入'?'i-color':'o-color'}>{v}</span>,
          label:'金额(元)'
        },{
          key:'domain',
          width:'6%',
          enums:bodyDomains,
          label:'域'
        },{
          key:'cate',
          width:'10%',
          enums:tenantConfig.billCates,
          label:'分类'
        },{
          key:'subCate',
          width:'10%',
          label:'子分类'
        },{
          key:'targetId',
          width:'10%',
          label:'消费主体'
        },{
          key:'reason',
          width:'10%',
          label:'事项'
        },{
          key:'relInfo',
          width:'10%',
          render:(v,r)=>{
            return <EditableField value={v} onBlur={(v)=>{
              util.ajaxJson(urlPrefix+'BillOperation?opt=updateRelInfo',{id:r.id,relInfo:v},res=>{
                if(res.success){
                  message.success('修改备注成功');
                  r.relInfo=v;
                }
              });
            }}/>
          },
          label:'备注'
        },{
          key:'status',
          width:'4%',
          enums:[{key:1,text:'生效'},{key:0,text:'作废'}],
          label:'状态'
        },{
          key:'mode',
          width:'4%',
          label:'模式'
        },{
          key:'operator',
          width:'6%',
          label:'操作人'
        }
      ],
      operationAddon:rec=>{
        return (<span><a onClick={()=>{
          emitter.emit('billModal',rec);
        }}>调账</a><Popconfirm title="确定要删除账单吗？" onConfirm={()=>{
          util.ajaxJson(urlPrefix+'BillOperation?opt=removeBill',{id:rec.id},res=>{
            if(res.success){
              message.success('删除成功');
              emitter.emit('queryBillTable', undefined, true);
            }
          });
        }}><a>删除</a></Popconfirm></span>);
      },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {status:'0'};
        },
        beforeModify:formData=>{
          return formData;
        },
        beforeSubmit:this.getFormData
      },
      table:{
        bordered:true,
        lazyInit:true,
        size:'small',
        showTotal:true,
        resultHandler:(result,queryParam)=>{
          this.setState({ioMap:result.data.ioMap});
          result.list=result.data.data;
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'BillOperation?opt=queryBill'}
      }
    };
    this.state={
    };
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
       <BillModal/>
      </div>
    );
  }
}