import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox, Row, Col} from 'antd';
import moment from 'moment';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms,coaches,tenantConfig} from '../../../entry/index/routes/IndexPage';
import Coach from '../../common/fields/coach';
import SportType from '../../common/fields/sportType';
import SelectTable from '../../../components/select/selectTable';
import {imagePrefix} from '../../common/fields/upload';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('studentRecordModal', (rec)=>{
        let formValues={roomId:rooms.length>0?rooms[0].id+'':undefined,consumeTimes:1,studyTime:moment(),type:'乒乓',subType:'多球'};
        this.setState({visible:true,formValues,rec});
      
    });
  	this.state={
      formValues:{},
      rec:{courses:[]},
      visible:false
  	};
  }

  handleOk=()=>{
    util.validateFields.call(this,values=>{
      let {rec} = this.state;
      util.ajaxJson(urlPrefix+'TrainOperation?opt=studentConsume', {...rec,...values}, res=>{
        if(res.success){
          message.success(`消费成功!`);
          this.handleCancel();
        }
      });
    });
  }

  handleCancel=()=>{
    emitter.emit(`refresh-student-table`);
    this.setState({visible:false});
  }

  onCoachChange=(v)=>{
    let coach=coaches.filter(it=>it.id==v)[0];
    if(!coach)return;
    let {formValues}=this.state;
    let times=formValues.consumeTimes||1;
    formValues.consumeAmount=times*coach.unitPrice;
    this.setState({formValues});
  }

  render() {
    let {visible,formValues,rec} = this.state;
    let isMainCoach=false;
    if(rec.courses){
      isMainCoach=rec.courses.filter(it=>it.coachId==formValues.coachId&&formValues.courseId==it.id).length>0;
      rec.courses.forEach(r=>{
        r.coursename=`${r.coachName}的课(${r.type}) ${r.remark?('['+r.remark+']'):''} ${r.gmtCreate&&('<'+util.formatDate(r.gmtCreate,'yy年MM月dd号开课')+'>')}`;
      });
    }
    let coachRate=isMainCoach?tenantConfig.majorCoachRate:tenantConfig.coachRate;
    let targetCourse=rec.courses.filter(it=>formValues.courseId==it.id+'')[0] || {};
    let targetCoach=coaches.filter(it=>it.id==targetCourse.coachId)[0];
  	return (<Modal title={`学员训练登记`} className="modal-form" visible={visible} width={1200} destroyOnClose getContainer={document.querySelector('.Student-container')} onCancel={this.handleCancel} maskClosable={false} footer = {
        [<Button key="1" type="primary" size="large" onClick={this.handleOk}>
            消费
         </Button>,
         <Button key="2" type="ghost" size="large" onClick={this.handleCancel}>
            取消
         </Button>]
      }>
          <Form className="student-consume-modal">
            <Row>
              <Col span={8}>
                <Form.Item className="memberName-f" label="学员名">
                    {rec.name}
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item className="" label="手机号">
                    {rec.phone}
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item className="" label="余额">
                    {rec.amount}
                    {+rec.amount<+formValues.consumeAmount&&<span className="red-tips">消费后余额不足，将欠费{+formValues.consumeAmount-rec.amount}元！</span>}
                </Form.Item>
              </Col>
            </Row>
            <SportType form={this} required/>
            <Form.Item className="" label="课程" required>
              <SelectTable {...util.getField.call(this,'courseId',{onChange:v=>{
                  let {formValues}=this.state;
                  let {coachId}=rec.courses.filter(it=>it.id+''==v)[0];
                  formValues.coachId=coachId+'';
                  this.onCoachChange(formValues.coachId);
                },rules:[{required:true,message:'必填'}]})} placeholder="请选择该学员的课程" valueKey="id" nameKey="coachName" optionLabelProp="coursename" columns={[
                            {key:'coachName',title:'教练',width:'80px'},
                            {key:'amount',title:'金额(元)',width:'80px',render:(v,r)=>`${r.remainAmount}/${v}`},
                            {key:'times',title:'课时数',width:'80px',render:(v,r)=>`${r.remainTimes}/${v}`},
                            {key:'lastTime',title:'上次训练时间',width:'90px',render:v=>util.formatDate(v)},
                            {key:'remark',title:'课程备注',width:'100px'},
                    ]} data={rec.courses} searchKey="coachName" allowClear={false}/>
            </Form.Item>
            <Form.Item className="" label={(isMainCoach?'主管':'')+"教练"} required>
              <Coach form={this} required onChange={this.onCoachChange} showPrice/>
            </Form.Item>
            <Form.Item className="consume-times-f" label="课时数" required>
              <InputNumber {...util.getField.call(this,'consumeTimes',{rules:[{required:true,message:'必填'},{validator:v=>{
                if(targetCourse.remainTimes!==undefined && +targetCourse.remainTimes < +v){
                  return false;
                }
                return true;
              },message:'课时数不足'}]})} min={0.5} precision={1} step={0.5}/>
              {targetCourse&&targetCoach&&targetCoach.name&&targetCourse.remainTimes!==undefined&&<span className="course-tip">{targetCoach.name}的课 &nbsp;&nbsp;剩余课时数：{targetCourse.remainTimes} 剩余金额：{targetCourse.remainAmount}元</span>}
              
            </Form.Item>
            <Form.Item className="" label="消费金额(元)" required>
              <InputNumber {...util.getField.call(this,'consumeAmount',{rules:[{required:true,message:'必填'},{validator:v=>{
                // return +v<=rec.amount;
                return true;
              },message:'余额不足'}]})} min={1} precision={0}/>
              {formValues.consumeAmount&&<span className="course-tip">待分账金额（{isMainCoach?'主管':''}教练）：{util.round(+formValues.consumeAmount*(coachRate/100))}元({coachRate}%)<a onClick={()=>{
                emitter.emit('main-clickmenu',{key:'sys/config'});
                // this.setState({visible:});
              }}>设置</a></span>}
            </Form.Item>
            <Form.Item className="" label="训练时间" required>
              <DatePicker {...util.getField.call(this,'studyTime',{rules:[{required:true,message:'必填'}]})}/>
            </Form.Item>
            <Form.Item className="room-f" label="球馆">
              <Select {...util.getField.bind(this)('roomId',{})}>
                {rooms.map(it=>{
                  return <Select.Option key={it.id} value={it.id+''}>{it.name}</Select.Option>;
                })}
              </Select>
              {rec.avatar&&<img alt="" src={imagePrefix()+rec.avatar}></img>}
            </Form.Item>
            
          </Form>
      </Modal>);
  }
}

export default BizComponent;