import React,{Component} from 'react';
import {
  Popconfirm,
  message
} from 'antd';
import {emitter,util} from '../../../common/common';
import Grid from './grid';
export default class BizTable extends Component {
  constructor(...args) {
    super(...args);
    this.columns = this.props.config.fields.filter(f=>!f.notInTable).map(f=>{
      let column = {key:f.key,title:f.label,width:f.width};
      if(f.type=='datetime'){
        column.render= (text,record) => util.formatDate(text);
      }
      if(f.enums){
        const enumObj = {};
        let {enums}=f;
        if(typeof enums=='function'){
          enums=enums();
        }
        enums.forEach(e=>{
          let key,value;
          key=Array.isArray(e)?e[0]:(typeof e == 'string' ? e : e.key);
          value=Array.isArray(e)?e[1]:(typeof e == 'string' ? e : e.text);
          enumObj[key]=value;
        });
        column.render= (text,record,idx) => {
          if(f.render){
            return f.render(text,record,idx,enumObj[text]);
          }else{
            if(Array.isArray(text)){
              return text.map(it=>enumObj[it]).join(',');
            }
            return enumObj[text];
          }
        };
      }else{
        if(f.render){
          column.render = f.render.bind(f);
        }
      }
      return column;
    });
    if(this.props.config.table&&this.props.config.table.rowKey){
      this.rowKey = this.props.config.table.rowKey;
    }else{
      this.rowKey = r=>r[this.props.config.fields.filter(f=>f.rowKey)[0].key];
    }
    
    let {updateInterface,deleteInterface} = this.props.config.api;
    let canDelete = !!deleteInterface;
    if(updateInterface || deleteInterface || this.props.config.operationAddon || this.props.config.operationPrefix){
      this.columns.push({
          title: '操作',
          key: 'operation',
          render: (text, record, idx) => {
            if(this.props.config.table&&this.props.config.table.canModify){// 是否可修改
              updateInterface = this.props.config.table.canModify(record);
            }
            if(this.props.config.table&&this.props.config.table.canDelete){// 是否可删除
              canDelete = this.props.config.table.canDelete(record);
            }
            return (
              <div className="opt-container">
                {this.props.config.operationPrefix&&this.props.config.operationPrefix(record)}
                {updateInterface&&<a onClick={()=>{
                  emitter.emit(`show${this.props.config.key}Dialog`, record, 'modify');
                }}>修改</a>}
                {canDelete&&<Popconfirm title={'确定要删除？'} onConfirm={()=>{
                  let params={};
                  for(let k in deleteInterface.params){
                    params[k] = record[deleteInterface.params[k]];
                  }
                  if(this.props.config.table&&this.props.config.table.beforeDelete){
                    params = this.props.config.table.beforeDelete(params,record);
                  }
                  util.ajaxJson(deleteInterface.func,params,(result)=>{
                    if(result.success){
                      message.success('删除成功！');
                    }
                    emitter.emit(`query${this.props.config.key}Table`,undefined,true);
                  });
                }}>
                  <a>删除</a>
                </Popconfirm>}
                {this.props.config.operationAddon&&this.props.config.operationAddon(record, idx)}
              </div>
            );
        }
      });
    }

    if(this.props.config.operationPreAddon){
      this.columns.splice(0,0,{
        title: '操作',
        key: 'preOperation',
        render: (text, record) => {
          return (
                <div className="pre-opt-container">
                {this.props.config.operationPreAddon(record)}
                </div>);
        }
      });
    }
    
    this.state = {
    };
  }
  render() {
    return (<Grid rowKey={this.rowKey} ajax={this.props.config.api.queryInterface.func} beforeQuery={this.props.config.queryCond&&this.props.config.queryCond.beforeQuery} queryEmitter={`query${this.props.config.key}Table`} columns={this.columns} {...this.props.config.table}/>);
  }
}