import React,{Component} from 'react';
import {message} from 'antd';
import {domain} from '../interfaces/interfaces';
import {Modal} from 'antd';
import {util,emitter} from './common';
import {tenantId,name,phone} from '../entry/index/routes/IndexPage';
export let ws = null;

function handleMsg(event){
    if(event.data){
        if(event.data=='q'||event.data=='p'){
            emitter.emit('refresh-verify-code');
            return;
        }
        let dataObj=JSON.parse(event.data);
        if(dataObj.type=='refreshConfirm'){
            Modal.confirm({
                title:'页面刷新提醒确认？',
                content:<div>检测到应用有更新，更新时间【{util.formatDate(dataObj.time)}】，请及时刷新页面！
                        <div>提示：刷新前未保存数据，刷新后将丢失</div></div>,
                okText:"刷新",
                cancelText:"取消",
                onOk:()=>{
                    window.location.reload();
                }
            });
        }
    }
}

let heartbeatInterval;

function createWebSocket(url) {
    try {
        if ('WebSocket' in window) {
            ws = new window.WebSocket(url);
        } else if ('MozWebSocket' in window) {
            ws = new window.MozWebSocket(url);
        } else {
            message.error("当前浏览器不支持websocket协议,建议使用现代浏览器");
        }
        initEventHandle(url);
        if(heartbeatInterval){
            clearInterval(heartbeatInterval);
            heartbeatInterval=undefined;
        }
        heartbeatInterval=setInterval(()=>{
            if(window._stopWebsocketHeartBeat){
                if(heartbeatInterval){
                    clearInterval(heartbeatInterval);
                    heartbeatInterval=undefined;
                }
            }
            ws.send('p');
        }, 55000);
    } catch (e) {
        reconnect(url);
    }
}
function initEventHandle(url) {
    ws.onclose = function (err) {
        console.log('onClose', err);
        reconnect(url);
    };
    ws.onerror = function (err) {
        console.log('onErr', err);
        reconnect(url);
    };
    ws.onopen = function () {
    };
    ws.onmessage = function (event) {    //如果获取到消息，心跳检测重置
        handleMsg(event);
    };
}
function reconnect(url) {
    if (reconnect.lockReconnect) return;
    reconnect.lockReconnect = true;
    setTimeout(function () {     //没连接上会一直重连，设置延迟避免请求过多
        createWebSocket(url);
        reconnect.lockReconnect = false;
    }, 2000);
}       

emitter.on('init-websocket',()=>{
    let code=encodeURIComponent(util.mixStr(JSON.stringify({tenantId,name,phone})));
    let url=`ws${window.location.href.indexOf('https')==0?'s':''}://${domain}/connectWs/${code}`;
    createWebSocket(url);
    
});
