import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select, Form} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin} from '../../../entry/index/routes/IndexPage';
import ChangePass from './changePass';
export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    this.config = {
      title:'人员组织',
      key:'sysUser',
      dialog:{width:1060},
      queryCond:{
      conditions:[[{key:'name'},{key:'phone'},{key:'role',type:'custom',getComponent:(props)=>(<Select {...props} mode={undefined}>{allRoles.map(it=><Select.Option key={it.id+''} value={it.id+''}>{it.name}</Select.Option>)}</Select>)}]],
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          this.queryParam=queryParam;
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInForm:true,
          notInTable:true,
          label:'id'
        },{
          key:'name',
          width:'10%',
          label:'姓名'
        },{
          key:'phone',
          width:'10%',
          props:{maxLength:11},
          rules:[{required: true, message: '必填'},{validator:v=>{
            return /^1[0-9]{10}$/.test(v);
          },message:'必须为1开头的11位手机号'}],
          label:'手机号'
        },{
          key:'role',
          width:'20%',
          enums:allRoles.map(it=>({key:it.id,text:it.name})),
          rules:[{required: true, message: '必填'}],
          multi:true,
          label:'角色'
        },{
          key:'status',
          width:'10%',
          enums:[{key:'1',text:'生效'},{key:'0',text:'失效'}],
          label:'状态'
        },{
          key:'gmtModified',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'修改时间'
        },{
          key:'gmtCreate',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'创建时间'
        }
      ],
      operationAddon:rec=>{
        return (<span><a onClick={()=>emitter.emit('changePassModal', rec)}>改密码</a></span>);
      },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {status:'1'};
        },
        beforeModify:formData=>{
          formData.status=formData.status+'';
          return formData;
        },
        beforeSubmit:formData=>{
          if(formData.role&&formData.role.length>0){
            if(Array.isArray(formData.role)){
              formData.role=','+formData.role.join(',')+',';
            }
          }else{
            formData.role=undefined;
          }
          return formData;
        }
      },
      table:{
        bordered:true,
        size:'small',
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.list.forEach(it=>{
            if(it.role){
              it.role=it.role.split(',').filter(it=>!!it);
            }
          });
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'SysUserFramework?opt=querySysUser'},
        updateInterface:isAdmin?{func:urlPrefix+'SysUserFramework?opt=updateSysUser'}:undefined,
        insertInterface:isAdmin?{func:urlPrefix+'SysUserFramework?opt=insertSysUser'}:undefined,
        deleteInterface:isAdmin?{func:urlPrefix+'SysUserFramework?opt=deleteSysUser',params:{id:'id'}}:undefined
      }
    };
    this.state={
      formValues:{}
    };
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
       <ChangePass/>
      </div>
    );
  }
}