import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox, Row} from 'antd';
import moment from 'moment';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms,coaches} from '../../../entry/index/routes/IndexPage';
import MoneyField from '../../common/fields/money';
import PhoneField from '../../common/fields/phone';
import Coach from '../../common/fields/coach';
import SelectTable from '../../../components/select/selectTable';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('rechargeStudentModal', (rec,course)=>{
      let courseId,remark,canSelectCourse=true;
      if(course){// 指定课程的
        courseId=course.id+'';
        remark=course.remark;
        canSelectCourse=false;
      }
      let formValues={roomId:rec.roomId+'',remark,courseId,gmtCreate:moment()};
      this.setState({visible:true,formValues,rec,canSelectCourse});
      
    });
  	this.state={
      formValues:{},
      rec:{},
      courseInfo:{
        course:{}
      },
      visible:false
  	};
  }

  handleOk=()=>{
    let {rec,formValues}=this.state;
    let {courseId}=formValues;
    util.validateFields.call(this,values=>{
      util.ajaxJson(urlPrefix+'TrainOperation?opt=rechargeCourse', {rec:values,courseId,studentId:rec.id}, res=>{
        if(res.success){
          message.success(`课程充值成功!`);
          this.handleCancel();
        }
      });
    });
    
  }

  handleCancel=()=>{
    let {rec,formValues}=this.state;
    let {courseId}=formValues;
    emitter.emit(`queryStudentTable`, undefined, true);
    if(!this.state.canSelectCourse)emitter.emit('show-course-list-drawer',rec,courseId);
    this.setState({visible:false});
  }

  render() {
    let {visible,formValues,rec} = this.state;
    let course={},{courseId}=formValues;
    if(rec.courses){
      rec.courses.forEach(r=>{
        if(r.id==courseId)course=r;
        r.coursename=`${r.coachName}的课(${r.type}) ${r.remark?('['+r.remark+']'):''} ${r.gmtCreate&&('<'+util.formatDate(r.gmtCreate,'yy年MM月dd号开课')+'>')}`;
      });
    }
  	return (<Modal title={`学员课程充值`} className="modal-form" visible={visible} width={1200} destroyOnClose onCancel={this.handleCancel} maskClosable={false} footer = {
        [<Button key="1" type="primary" size="large" onClick={this.handleOk}>
            充值
         </Button>,
         <Button key="2" type="ghost" size="large" onClick={this.handleCancel}>
            取消
         </Button>]
      }>
          <Form className="add-member-modal">
            <Row className="FBH">
              <Form.Item className="" label="学员手机号" required>
                  {rec.phone}
              </Form.Item>
              <Form.Item className="memberName-f" label="学员名" required>
                  {rec.name}
              </Form.Item>
            </Row>
            <Row className="FBH">
              <Form.Item className="" label="课程余额(元)" required>
                  {course.remainAmount}/{course.amount}
              </Form.Item>
              <Form.Item className="" label="课程剩余次数" required>
                  {course.remainTimes}/{course.times}
              </Form.Item>
            </Row>
            <Form.Item className="" label="课程" required>
              <SelectTable disabled={!this.state.canSelectCourse} {...util.getField.call(this,'courseId',{onChange:v=>{
                  },rules:[{required:true,message:'必填'}]})} placeholder="请选择该学员的课程" valueKey="id" nameKey="coachName" optionLabelProp="coursename" columns={[
                              {key:'coachName',title:'教练',width:'80px'},
                              {key:'amount',title:'金额(元)',width:'80px',render:(v,r)=>`${r.remainAmount}/${v}`},
                              {key:'times',title:'课时数',width:'80px',render:(v,r)=>`${r.remainTimes}/${v}`},
                              {key:'lastTime',title:'上次训练时间',width:'90px',render:v=>util.formatDate(v)},
                              {key:'remark',title:'课程备注',width:'100px'},
                      ]} data={rec.courses} searchKey="coachName" allowClear={false}/>
            </Form.Item>
            <Form.Item className="amount-f" label="充值金额(元)" required>
              <MoneyField form={this} name="amount"/>
            </Form.Item>
            
            <Form.Item className="" label="课时数" required>
              <InputNumber {...util.getField.call(this,'times',{rules:[{required:true,message:'必填'}]})} min={1} precision={0}/>
            </Form.Item>
            <Form.Item className="" label="课程备注">
                <Input {...util.getField.call(this,'remark',{})} />
            </Form.Item>
            <Form.Item className="" label="球馆" required>
              <Select {...util.getField.bind(this)('roomId',{rules:[{required:true,message:'必填'}]})}>
                {rooms.map(it=>{
                  return <Select.Option key={it.id} value={it.id+''}>{it.name}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            
          </Form>
      </Modal>);
  }
}

export default BizComponent;