import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox} from 'antd';
import moment from 'moment';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';
import {imagePrefix} from '../../common/fields/upload';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('memberConsumeModal', (rec)=>{
        let formValues={roomId:rooms.length>0?rooms[0].id+'':undefined,consumeTimes:1,gmtCreate:moment()};
        this.setState({visible:true,formValues,rec});
      
    });
  	this.state={
      formValues:{},
      rec:{},
      visible:false
  	};
  }

  handleOk=()=>{
    util.validateFields.call(this,values=>{
      let {rec} = this.state;
      util.ajaxJson(urlPrefix+'MemberOperation?opt=memberConsume', {...rec,...values}, res=>{
        if(res.success){
          message.success(`消费成功!`);
          emitter.emit('refresh-memeber-expand');
          this.handleCancel();
        }
      });
    });
  }

  handleCancel=()=>{
    emitter.emit(`queryMemberTable`, undefined, true);
    this.setState({visible:false});
  }

  render() {
    let {visible,formValues,rec} = this.state;
  	return (<Modal title={`会员消费`} className="modal-form" visible={visible} width={1200} destroyOnClose onCancel={this.handleCancel} maskClosable={false} footer = {
        [<Button key="1" type="primary" size="large" onClick={this.handleOk}>
            消费
         </Button>,
         <Button key="2" type="ghost" size="large" onClick={this.handleCancel}>
            取消
         </Button>]
      }>
          <Form className="member-consume-modal">
            <Form.Item className="memberName-f" label="会员名">
                {rec.name}
                {rec.avatar&&<img alt="" src={imagePrefix()+rec.avatar}></img>}
            </Form.Item>
            <Form.Item className="" label="手机号">
                {rec.phone}
            </Form.Item>
            <Form.Item className="" label="会员类型">
                {rec.memberType}
            </Form.Item>
            {rec.billType=='按次'&&<Form.Item className="amount-f" label="剩余次数" required>
              {rec.remainTimes}
            </Form.Item>}
            {rec.billType=='按次'&&<Form.Item className="amount-f" label="消费次数" required>
              <InputNumber {...util.getField.call(this,'consumeTimes',{rules:[{required:true,message:'必填'},{validator:v=>{
                return (+v<=rec.remainTimes);
              },message:'次数不足'}]})} min={1} precision={0}/>
            </Form.Item>}
            <Form.Item className="" label="消费时间" required>
                <DatePicker showTime {...util.getField.call(this,'gmtCreate')} />
            </Form.Item>
            <Form.Item className="" label="球馆">
              <Select {...util.getField.bind(this)('roomId',{})}>
                {rooms.map(it=>{
                  return <Select.Option key={it.id} value={it.id+''}>{it.name}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            
          </Form>
      </Modal>);
  }
}

export default BizComponent;