import React,{Component} from 'react';
import {message,Input,Modal,Button,Form,Select,Carousel} from 'antd';
import './index.less';
import {util,emitter,minAjax} from '../../../../common/common';
import {prefix,productPrefix,isProductUrl, urlPrefix} from '../../../../interfaces/interfaces';
import {allMetaData} from '../../../../components/select';

export default class TestModal extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('goto-login', ({config,body})=>{
      let verifyId=util.guid();
      let firstLogin=config.url&&config.url.indexOf('SysUserFramework?opt=getUserInfo')>-1;
      let account = window.sessionStorage.getItem('account');
      let pwd = window.sessionStorage.getItem('pwd');
  		this.setState({visible:true,registerTag:undefined,formValues:{account,pwd,verifyCode:window.isProd()?'':'okgo'},url:config.url,config,firstLogin,verifyId});

      // if(body){
      //   let url=JSON.parse(body).url;
      //   let urlPrefix = url.substring(0,url.indexOf('reffer='));
      //   let targetUrl;
      //   if(isProductUrl(config.url)){
      //     targetUrl=productPrefix+'fbi/api/v1/ssoagentlogin/jumplogin?reffer='+encodeURIComponent('http:'+productPrefix.substring(0,productPrefix.length-1)+'?target='+encodeURIComponent(window.location.href));
      //   }else{
      //     targetUrl=urlPrefix+'reffer='+encodeURIComponent(prefix+'fbi/api/v1/index?target='+encodeURIComponent(window.location.href));
      //   }
      //   window.location.href=targetUrl;
      // }
  	});
    emitter.on('refresh-verify-code',()=>{
      if(this.state.visible){
        this.setState({verifyId:util.guid()});
      }
    });
  	this.state={
      formValues:{}
  	};
  }

  handleLogin=()=>{
    let {registerTag} = this.state;
    let validFields;
    if(registerTag){
      validFields = ['name','phone','pass','pwd1','groupIds'];
    }else{
      validFields = ['phone','pass','verifyCode'];
    }
    util.validateFields.call(this,values=>{
      let {verifyId}=this.state;
      let {phone,name,pass,tenantId,verifyCode} = values;
      if(registerTag){
        // util.ajaxJson(this.getPrefix()+'flow/user/register',{account,name,pass,groupIds:JSON.stringify(groupIds)},res=>{
        //   if(res.success && !res.errMsg){
        //     message.success('注册成功!');
        //     window.sessionStorage.setItem('name',name);
        //     window.sessionStorage.setItem('groupIds',JSON.stringify(groupIds));
        //     window.location.reload();
        //   }else{
        //     message.error(res.errMsg);
        //   }
        // });
      }else{
        util.ajaxJson(this.getPrefix()+'flow/biz/login',{phone,pass,tenantId,verifyId,verifyCode,noErrAlert:true},result=>{
          if(result.success && result.data && !result.errMsg){
            if(result.data.length>1){
              let {formValues}=this.state;
              formValues.tenantId=result.data[0].tenantId;
              this.setState({tenants:result.data,formValues});
            }else{
              this.loginGo(result);
            }
          }else{
            message.error(result.errMsg||'登陆失败');
            if(result.errMsg=='验证码错误'){
              this.setState({verifyId:util.guid()});
            }
          }
        });
      }
      
    },validFields);
  }

  loginGo=(result)=>{
    let res=result.data[0];
    window.sessionStorage.setItem('name',res.name);
    window.sessionStorage.setItem('userId',res.id);
    window.sessionStorage.setItem('phone',res.phone);
    emitter.emit('init');
    minAjax(this.state.config);
    this.setState({visible:false,tenants:[]});
  }

  getPrefix=()=>{
    let {url} = this.state;
    if(!url || !productPrefix){
      return prefix;
    }
    if(url.startsWith(productPrefix)){
      return productPrefix;
    }
    return prefix;
  }

  toRegister=()=>{
    this.setState({registerTag:true});
  }

  handleCancel=()=>{
    this.setState({visible:false});
  }

  render() {
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    };
    let {formValues,registerTag,firstLogin,verifyId} = this.state;
  	return (<div>
      {firstLogin&&<div className="first-login-c">
      <div className="logo-text">球馆数字化先锋——球掌柜，掌控全局，运筹帷幄！</div>
        <Carousel autoplay>
          <div className="carousel-item carousel-item-1"> 
          </div>
          <div className="carousel-item carousel-item-2">
            <h3>2</h3>
          </div>
          <div className="carousel-item carousel-item-3">
            <h3>3</h3>
          </div>
          <div className="carousel-item carousel-item-4">
            <h3>4</h3>
          </div>
        </Carousel>
      </div>}
      <Modal mask={!firstLogin} wrapClassName={"login-modal"+(firstLogin?' first-login-modal':'')} title={registerTag?'注册':'欢迎来到球掌柜，请先登陆'} style={{top:'200px'}} visible={this.state.visible} width={400} onCancel={this.handleCancel} maskClosable={false}
          footer={[
            <Button key="login" type="primary" onClick={this.handleLogin}>{registerTag?'注册':'登录'}</Button>,
            <Button key="close" onClick={this.handleCancel}>关闭</Button>
          ]}>  
          <Form id="log-container-form" className="login-container">
            {registerTag&&<Form.Item {...formItemLayout} label="姓名">
              <Input {...util.getField.bind(this)('name',{rules:[{validator:v=>{
                return !!v;
              },message:'必填'}]})}/>
            </Form.Item>}
            <Form.Item {...formItemLayout} label="账号(手机号)" required>
              <Input {...util.getField.bind(this)('phone',{rules:[{validator:v=>{
                return !!v;
              },message:'必填'},{validator:v=>{
                return /^1[0-9]{10}$/.test(v);
              },message:'必须为1开头的11位手机号'}]})}/>
            </Form.Item>
            <Form.Item {...formItemLayout} label="密码" required>
              <Input type="password" {...util.getField.bind(this)('pass',{rules:[{validator:v=>{
                return !!v;
              },message:'必填'}]})} onPressEnter={this.handleLogin}/>
            </Form.Item>
            {registerTag&&<div>
              <Form.Item {...formItemLayout} label="密码确认">
                <Input {...util.getField.bind(this)('pwd1',{rules:[{validator:v=>{
                  return !!v;
                },message:'必填'},{validator:v=>{
                  let {formValues} = this.state;
                  return formValues.pwd === v;
                },message:'前后密码不一致'}]})}/>
              </Form.Item>
            </div>}
            {!registerTag&&<Form.Item {...formItemLayout} label="验证码" className="verify-code" required>
              <Input {...util.getField.call(this,'verifyCode',{rules:[{required:true,message:'必填'}]})} onPressEnter={this.handleLogin}/><img onClick={()=>this.setState({verifyId:util.guid()})} src={prefix+'image/verifyCode?tk='+verifyId} alt=""></img>
            </Form.Item>}
            <div className="register-area">
              {false&&<a onClick={this.toRegister}>注册</a>}
              {registerTag&&<a onClick={()=>{
                this.setState({registerTag:undefined});
              }}>返回</a>}
            </div>


            {this.state.tenants&&this.state.tenants.length>1&&<Form.Item label="请选择企业信息">
              <Select {...util.getField.call(this,'tenantId')} getPopupContainer={()=>document.querySelector('.login-modal')}>
                {this.state.tenants.map(it=><Select.Option key={it.tenantId} value={it.tenantId}>{it.tenantName}</Select.Option>)}
              </Select>
            </Form.Item>}
          </Form>
      </Modal></div>);
  }
}