import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox,Drawer,Collapse, Row, Popconfirm} from 'antd';
import moment from 'moment';
import './index.less';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms,coaches} from '../../../entry/index/routes/IndexPage';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('show-member-recharge-bill', (rec)=>{
        util.ajaxJson(urlPrefix+'BillOperation?opt=queryBill',{billIdPrefixs:['memOpen','memRecharge'].map(it=>it+rec.id)},res=>{
            if(res.success){
                let billList=res.data.data;
                this.setState({visible:true,rec,billList});
            }
        });
        
    });
  	this.state={
      visible:false,
      rec:{}
  	};
  }


  onClose=()=>{
      this.setState({visible:false});
  }

  render() {
    let {visible,rec,billList} = this.state;
  	return (<Drawer title={`会员【${rec.name}】- 充值记录`} className="student-course-drawer" visible={visible} width={800} placement="right" onClose={this.onClose} destroyOnClose >
          <Collapse defaultActiveKey={['1','2','3']}>
            <Collapse.Panel header="充值记录" key="3">
                <Table pagination={false} rowKey={r=>r.id} dataSource={billList} size="small" columns={[
          {key:'billTime',dataIndex:'billTime',title:'充值时间',render:v=>util.formatDate(v)},
          {key:'amount',dataIndex:'amount',title:'充值金额(元)'},
          {key:'reason',dataIndex:'reason',title:'说明'},
          {key:'operator',dataIndex:'operator',title:'经办人'},
          ]}/>
            </Collapse.Panel>
          </Collapse>

          <Button onClick={this.onClose}>关闭</Button>   
      </Drawer>);
  }
}

export default BizComponent;