import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Upload, Popconfirm} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,urls,allStaffs,rooms, coaches} from '../../../entry/index/routes/IndexPage';
import AddStudentModal from './addStudentModal';
import ModifyStudentModal from './modifyStudentModal';
import StudentConsumeModal from './studentConsumeModal';
import CourseListDrawer from './courseListDrawer';
import RechargeStudentModal from './rechargeStudentModal';
import {imagePrefix} from '../../common/fields/upload';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on('refresh-student-table',()=>{
      this.setState({expandedRowKeys:[]},()=>{
        emitter.emit(`queryStudentTable`, undefined, true);
      });
    });
    this.config = {
      title:'学员管理',
      key:'Student',
      dialog:{width:1060},
      queryCond:{
        conditions:[[{key:'phone'},{key:'name'},{key:'coachId',type:'custom',label:'教练',getComponent:props=><Select {...props} allowClear>{coaches.map(it=><Select.Option value={it.id} key={it.id}>{it.name}</Select.Option>)}</Select>}]],
        extButtons:()=>{
          let extBtns=[<Button key="1" type="primary" size="small" className="search-button" onClick={()=>emitter.emit('addStudentModal')}><Icon type="plus" />学员新增</Button>,
          urls.map(it=>it.code).indexOf('downloadStudent')>-1&&<Popconfirm key="export" title="确定要导出吗？" onConfirm={()=>window.location.href=urlPrefix+'download/TrainOperation?opt=downloadStudent'}><Button size="small" className="search-button"><Icon type="download" />导出</Button></Popconfirm>,
          urls.map(it=>it.code).indexOf('uploadStudent')>-1&&<Upload key="file" name="file" className="search-button" action={urlPrefix+'upload/TrainOperation?opt=uploadStudent'} fileList={this.state.fileList} withCredentials onChange={res=>{
            if (res.file.status=='done') {
              if(res.file.response.success){
                message.success(res.file.response.data);
                emitter.emit(`queryStudentTable`);
              }else{
                message.error(`文件导入失败：${res.file.response.errMsg}`, 20);
              }
            } else if(res.file.status=='error') {
              message.error(`文件上传错误`);
            }
            let {fileList}=res;
            if(fileList&&fileList.length>1)fileList.splice(0,1);
            this.setState({fileList});
          }}>
            <Tooltip title="一旦选中文件后即开始导入并生效，请确保excel文件数据的准确性！"><Button size="small" type="danger"><Icon type="upload" />导入</Button></Tooltip>
          </Upload>];
          return extBtns.filter(it=>!!it);
        },
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          this.queryParam=queryParam;
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInForm:true,
          notInTable:true,
          label:'id'
        },{
          key:'no',
          label:'序号'
        },{
          key:'name',
          width:'10%',
          rules:[{required: true, message: '必填'}],
          render:(v,r)=>{
            if(r.avatar){
              return <Tooltip overlayClassName="avatar-tooltip" title={<div className="avatar-image-c" style={{backgroundImage:`url(${imagePrefix()+r.avatar})`}} />}><a>{v}</a></Tooltip>
            }
            return v;
          },
          label:'学员名'
        },{
          key:'phone',
          width:'10%',
          props:{maxLength:11},
          rules:[{required: true, message: '必填'},{validator:v=>{
            return /^1[0-9]{10}$/.test(v);
          },message:'必须为1开头的11位手机号'}],
          label:'手机号'
        },{
          key:'amount',
          width:'10%',
          rules:[{required: true, message: '必填'}],
          label:'账户余额(元)'
        },{
          key:'courses',
          width:'20%',
          notInForm:true,
          render:(v,r)=>{
            if(r.courses){
            return r.courses.map((it,idx)=><Tooltip key={idx+'c'} title={<span>训练项目：{it.type}<br/>缴费金额(元)：{it.amount}</span>}><a className={"course-item"+(it.remainTimes==0?' empty':'')} onClick={()=>emitter.emit('show-course-list-drawer',r,it.id)}>{coaches.filter(c=>it.coachId==c.id)[0].name}({it.remainTimes}/{it.times})</a></Tooltip>);
            }
          },
          label:'私教课程'
        },{
          key:'recordCount',
          width:'6%',
          label:'已训练记录数'
        },{
          key:'operator',
          width:'6%',
          label:'经办人'
        },{
          key:'gmtCreate',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'创建时间'
        },{
          key:'gmtModified',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'修改时间'
        }
      ],
      operationAddon:rec=>{
        let useEmpty=false;
        if(rec.amount<=0){
          // useEmpty=true;
        }
        if(rec.courses){
          let remainCourses=rec.courses.filter(it=>it.remainTimes!==undefined);
          if(remainCourses.length>0&&remainCourses.map(it=>+it.remainTimes).reduce((a,b)=>a+b)==0){
             useEmpty=true;
          }
        }else{
          useEmpty=true;
        }
        return (<span><a disabled={useEmpty} onClick={()=>{
          emitter.emit('studentRecordModal',rec);
        }}>登记训练</a>{rec.courses&&rec.courses.length>0&&<a onClick={()=>{
          emitter.emit('rechargeStudentModal',rec);
        }}>课程充值</a>}<a onClick={()=>{
          emitter.emit('modifyStudentModal',rec);
        }}>修改</a>{!rec.recordCount&&<Popconfirm title="确定删除学员？" onConfirm={async ()=>{
          let doDelete=(ids)=>{
            util.ajaxJson(urlPrefix+'TrainOperation?opt=deleteStudent',{id:rec.id,ids},res=>{
              if(res.success){
                message.success('删除学员成功');
                emitter.emit(`queryStudentTable`, undefined, true);
              }
            });
          }
          let billRes=await util.ajaxJsonAsync(urlPrefix+'BillOperation?opt=queryBill',{domain:'学员',billIdPrefix:'stuOpen'+rec.id+'-'});
          if(!billRes.success)return;
          if(billRes.data.totalItem>0){
            Modal.confirm({
              title:'同步删除账单确认？',
              content:`系统监测到您已有${billRes.data.totalItem}笔学员开课充值账单录入，是否同步删除？`,
              onOk:()=>doDelete(billRes.data.data.map(it=>it.id))
            });
          }else{
            doDelete(billRes.data.data.map(it=>it.id));
          }
        }}><a>删除</a></Popconfirm>}</span>);
      },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {};
        },
        beforeModify:formData=>{
          return formData;
        },
        beforeSubmit:formData=>{
          formData.ext=JSON.stringify({staffs:formData.staffs});
          delete formData.staffs;
          return formData;
        }
      },
      table:{
        bordered:true,
        size:'small',
        expandedRowKeys:()=>this.state.expandedRowKeys,
        expandIcon:(pops)=>{
          let {expandedRowKeys}=this.state;
          let expanded=expandedRowKeys.indexOf(pops.record.id)>-1;
          if(!pops.record.recordCount)return;
          return <Icon type={expanded?"minus-square":"plus-square"} onClick={()=>{
            let {expandedRowKeys}=this.state;
            if(expanded){
              expandedRowKeys=[];
            }else{
              expandedRowKeys=[];
              expandedRowKeys.push(pops.record.id); 
            }
            this.setState({expandedRowKeys},this.refreshExpandRow);
          }}/>
        },
        expandedRowRender:(record, index, indent, expanded)=>{
          let {recDs}=this.state;
          return <Table className="student-rec-table" columns={[
          {key:'studyTime',dataIndex:'studyTime',title:'训练时间',render:v=>util.formatDate(v)},
          {key:'coachId',dataIndex:'coachId',title:'教练',render:(v,r)=>coaches.filter(it=>it.id==v)[0].name+(v===r.mainCoachId?'（主管教练）':'')},
          {key:'consumeTimes',dataIndex:'consumeTimes',title:'课时数'},
          {key:'remainTimes',dataIndex:'remainTimes',title:'消费后余课(次)',render:(v,r,idx)=>{
            let consumeTimes=0,allTimes=0,courseMap={};
            for(let i=0;i<recDs.data.length;i++){
              let stuRec=recDs.data[i];
              if(!courseMap[stuRec.courseId])allTimes+=stuRec.times;// 对课程求总次数
              courseMap[stuRec.courseId]=true;
              if(idx<=i){
                consumeTimes+=stuRec.consumeTimes;
              }
            }
            let snapTimes=allTimes-consumeTimes;
            return `${snapTimes}/${allTimes}`;
          }},
          {key:'consumeAmount',dataIndex:'consumeAmount',title:'消费金额(元)'},
          {key:'snapAmount',dataIndex:'snapAmount',title:'已累计消费(元)',render:(v,r,idx)=>{
            let consumeAmount=0;
            for(let i=0;i<recDs.data.length;i++){
              if(idx<=i){
                consumeAmount+=recDs.data[i].consumeAmount;
              }
            }
            return `${consumeAmount}`;
          }},
          {key:'operator',dataIndex:'operator',title:'经办人'},
          {key:'opt',dataIndex:'opt',title:'操作',render:(v,r)=><span>{r.billed!=1&&<Popconfirm title="确定要删除？" onConfirm={()=>{
            util.ajaxJson(urlPrefix+'TrainOperation?opt=deleteStudentRecord',{studentId:record.id,id:r.id},res=>{
              if(res.success){
                message.success('删除流水成功');
                this.refreshExpandRow();
                emitter.emit(`queryStudentTable`, undefined, true);
              }
            });
          }}><a>删除</a></Popconfirm>}</span>}]} rowKey={r=>r.id} dataSource={recDs.data} pagination={false} size="small"/>;
        },
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.totalItem=result.data.totalItem;
          result.list.forEach(it=>{
          });
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'TrainOperation?opt=queryStudent'},
        // updateInterface:{func:urlPrefix+'TrainOperation?opt=updateStudent'},
        // deleteInterface:{func:urlPrefix+'TrainOperation?opt=deleteStudent',params:{id:'id'}}
      }
    };
    emitter.on('refresh-student-expand',this.refreshExpandRow);
    this.state={
      fileList:[],
      expandedRowKeys:[],
      recDs:{}
    };
  }

  refreshExpandRow=()=>{
    let {expandedRowKeys}=this.state;
    if(expandedRowKeys&&expandedRowKeys.length>0){
      util.ajaxJson(urlPrefix+'TrainOperation?opt=queryStudentRecord',{studentId:expandedRowKeys[0]},res=>{
        this.setState({recDs:res.data});
      });
    }
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
       <AddStudentModal />
       <ModifyStudentModal />
       <StudentConsumeModal />
       <CourseListDrawer />
       <RechargeStudentModal />
      </div>
    );
  }
}