import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox} from 'antd';
import moment from 'moment';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms,tenantConfig} from '../../../entry/index/routes/IndexPage';
import MoneyField from '../../common/fields/money';
import PhoneField from '../../common/fields/phone';
import Coach from '../../common/fields/coach';
import MyUpload from '../../common/fields/upload';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('addStudentModal', ()=>{
        let formValues={roomId:rooms.length>0?rooms[0].id+'':undefined,type:'乒乓',gmtCreate:moment(),privateTrain:tenantConfig.privateTrain?tenantConfig.privateTrain:true};
        this.setState({visible:true,formValues});
      
    });
  	this.state={
      formValues:{},
      visible:false
  	};
  }

  handleOk=()=>{
    util.validateFields.call(this,async values=>{
      let existData=await util.ajaxJsonAsync(urlPrefix+'TrainOperation?opt=getStudent',{phone:values.phone});
      if(existData.data&&existData.data[0]){// 已存在学员
        Modal.confirm({
          title:'学员是否合并提醒？',
          content:`通过手机号发现该学员已存在，余额为${existData.data[0].amount}元，是否直接做合并并继续？`,
          onOk:()=>{
            this.doReportStudent(values);
          }
        });
      }else{
        this.doReportStudent(values);
      }
      
    });
  }
  doReportStudent=(values)=>{
    this.setState({loading:true});
    util.ajaxJson(urlPrefix+'TrainOperation?opt=reportStudent', {rec:values}, res=>{
      if(res.success){
        message.success(`开户成功!`);
        this.handleCancel();
      }
    });
  }

  handleCancel=()=>{
    emitter.emit(`queryStudentTable`, undefined, true);
    this.setState({visible:false,loading:false});
  }

  render() {
    let {visible,formValues,loading} = this.state;
    let {courseTypes}=tenantConfig;
    if(!courseTypes)courseTypes=['乒乓'];
  	return (<Modal title={`学员报名开课`} className="modal-form" visible={visible} width={1200} destroyOnClose onCancel={this.handleCancel} maskClosable={false} footer = {
        [<Button key="1" type="primary" size="large" onClick={this.handleOk} loading={loading}>
            报名
         </Button>,
         <Button key="2" type="ghost" size="large" onClick={this.handleCancel}>
            取消
         </Button>]
      }>
          <Form className="add-member-modal">
            <Form.Item className="" label="手机号" required>
                <PhoneField form={this} />
            </Form.Item>
            <Form.Item className="memberName-f" label="学员名" required>
                <Input {...util.getField.call(this,'name',{rules:[{required:true,message:'必填'}]})} />
            </Form.Item>
            <Form.Item className="" label="课程类型" required>
              <Select {...util.getField.bind(this)('type',{rules:[{required:true,message:'必填'}]})}>
                {courseTypes.map(it=>{
                  return <Select.Option key={it} value={it}>{it}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            <Form.Item className="" label="私教" required>
              <Switch {...util.getField.call(this,'privateTrain',{mode:'checkbox',onChange:v=>{
                if(!v){
                  let invalidFields=['coachId','times','amount'];
                  let {formValues}=this.state;
                  for(let invalidField of invalidFields){
                    delete this['__validField_'+invalidField];
                    delete formValues[invalidField];
                  }
                  this.setState({formValues});
                }
              }})}/>
            </Form.Item>
            {formValues.privateTrain&&<><Form.Item className="amount-f" label="充值金额(元)" required>
              <MoneyField form={this} name="amount"/>
            </Form.Item>
            <Form.Item className="" label="主管教练" required>
              <Coach form={this} required showPrice/>
            </Form.Item>
            <Form.Item className="" label="课时数" required>
              <InputNumber {...util.getField.call(this,'times',{rules:[{required:true,message:'必填'}]})} min={1} precision={0}/>
            </Form.Item>
            <Form.Item className="" label="课程备注">
                <Input {...util.getField.call(this,'remark',{})} />
            </Form.Item>
            <Form.Item className="" label="开课时间" required>
              <DatePicker {...util.getField.call(this,'gmtCreate')}/>
            </Form.Item></>}
            <Form.Item className="" label="照片">
              <MyUpload message="学员" quality={0.07} onLoading={loading=>this.setState({loading})} {...util.getField.call(this,'avatar')}/>
            </Form.Item>
            <Form.Item className="" label="球馆" required>
              <Select {...util.getField.bind(this)('roomId',{rules:[{required:true,message:'必填'}]})}>
                {rooms.map(it=>{
                  return <Select.Option key={it.id} value={it.id+''}>{it.name}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            
          </Form>
      </Modal>);
  }
}

export default BizComponent;