import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon, Popconfirm} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../common/common';
import BizComp from '../../components/common';
import {urlPrefix,urlProductPrefix} from '../../interfaces/interfaces';

import QueryCond from '../../components/common/table/queryCond';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on(`menu-clicked-task`,()=>{
      emitter.emit('queryTaskTable');
    });
    this.config = {
      title:'任务待办',
      key:'Task',
      dialog:{width:1060},
      queryCond:{
        conditions:[[{key:'type'}]],
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          this.queryParam=queryParam;
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInTable:true,
          label:'id'
        },{
          key:'name',
          width:'10%',
          label:'执行人'
        },{
          key:'type',
          width:'10%',
          enums:[['跟进','跟进']],
          label:'任务类型'
        },{
          key:'startTime',
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'任务开始时间'
        },{
          key:'expireTime',
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'任务过期时间'
        },{
          key:'todoInfo',
          width:'30%',
          render:(v,r)=>{
            return <pre>{v}</pre>;
          },
          label:'任务内容'
        },{
          key:'status',
          width:'10%',
          render:(v,r)=>{
            if(r.start_time<new Date().getTime())return <span style={{color:'red'}}>已超期</span>;
            if(v===0)return '待处理';
            if(v===1)return '处理中';
            if(v===2)return '已完成';
            if(v===3)return '已放弃';
            return v;
          },
          label:'状态'
        },{
          key:'finish_time',
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'完成时间'
        }
      ],
      operationAddon:r=>{
        return (<span>{r.status===0&&<a onClick={()=>{
          if(r.type=='跟进'){
            emitter.emit('main-clickmenu',{key:'customer'});
            util.ajaxJson(urlPrefix+'?serviceId=crm_base&opt=doTasking',{id:r.id},result=>{
              emitter.emit('do-indicates-search',{company_name:r.customer_name});
            });
          }
        }}>立即处理</a>}{(r.status===0||r.status===1)&&<Popconfirm title="是否标记完结？" okText="是" cancelText="否" onConfirm={()=>{
          util.ajaxJson(urlPrefix+'?serviceId=crm_base&opt=doTasking',{id:r.id,status:2,finish_time:new Date().getTime()},result=>{
            message.success('已完结');
            emitter.emit('queryCrmTaskTable',undefined,true);
          });
        }}><a>标记完结</a></Popconfirm>}</span>);
      },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {status:'0'};
        },
        beforeModify:formData=>{
          return formData;
        },
        beforeSubmit:this.getFormData
      },
      table:{
        bordered:true,
        width:'100%',
        size:'small',
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'SysUserFramework?opt=listMyTask'},
      }
    };
    this.state={
    };
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
      </div>
    );
  }
}