import React,{Component} from 'react';
import { Menu, Icon, Switch } from 'antd';
import IIcon from '../../../components/icon';
import {role,isAdmin,menus} from './IndexPage';
const SubMenu = Menu.SubMenu;

export default class BizComponent extends Component {
	constructor(...args) {
		super(...args);
        let loginMenus = menus.filter(it=>!it.parentCode).sort((a,b)=>a.num-b.num).map(it=>{
			let menuObj={title:it.name,icon:JSON.parse(it.ext).icon,key:it.code};
			let subMenus=menus.filter(subIt=>subIt.parentCode==it.code).sort((a,b)=>a.num-b.num).map(it=>({title:it.name,icon:JSON.parse(it.ext).icon,key:it.code}));
			menuObj.children=subMenus;
			subMenus.forEach(subMenu=>{
				let subChildren=menus.filter(it=>it.parentCode==subMenu.key).sort((a,b)=>a.num-b.num).map(it=>({title:it.name,icon:JSON.parse(it.ext).icon,key:it.code}));
				if(subChildren.length>0)subMenu.children=subChildren;
			});
			return menuObj;
		});
		let current='train/student';
		if(!window.isProd()){
			let lastActiveMenuKey=localStorage.getItem('_curActiveMenuKey');
			if(lastActiveMenuKey){
				current=lastActiveMenuKey;
			}
		}
		let {hash}=window.location;
		if(hash&&hash.length>1){// 如果是非菜单，则默认不选任何菜单
            let hashKey=hash.substring(1);
            let qIdx=hashKey.indexOf('?');
            if(qIdx>-1){
                hashKey=hashKey.substring(0,qIdx);
            }
            if(hashKey!=current){
				current=undefined;
			}
		}
    	this.state={
    		loginMenus,
      		openKeys:menus.map(it=>it.code),
      		current
    	};
	}
	componentDidMount(){
        let {current} = this.state;
		if(current){
			this.props.onClick(current,undefined, this.getTitleByKey(current));
		}
	}
    getTitleByKey=key=>{
        let {loginMenus} = this.state;
        let title,icon;
		for(let lm of loginMenus){
			if(lm.key==key){
				title = lm.title;
				icon = lm.icon;
				break;
			}
			for(let lc of lm.children){
				if(lc.key == key){
					title = lc.title;
					icon = lc.icon;
					break;
                }
				if(lc.children){
					for(let lcc of lc.children){
						if(lcc.key == key){
							title = lcc.title;
							icon = lcc.icon;
							break;
						}
					}
				}
				
			}
		}
		if(title)
			return <span className="tab-title"><IIcon type={icon}/>{title}</span>;
    }
    changeCurrentMenu=(current)=>{
        this.setState({
          current
        });
    }
	handleClick=(e)=>{
		window._menuClickProtect=true;//打保护防止重复render
        window.location.hash=e.key;
		setTimeout(()=>delete window._menuClickProtect,10);
	    this.changeCurrentMenu(e.key);
		this.props.onClick(e.key,undefined,this.getTitleByKey(e.key));
		if(!window.isProd()){
			localStorage.setItem('_curActiveMenuKey', e.key);
		}
	}
	render() {
	  	const loop = data => data.map(item => {
	      if (item.children && item.children.length) {
	        return (<SubMenu key={item.key} title={(()=>{
	          return <div><IIcon type={item.icon}/>{item.title}</div>;
	        })()}>{loop(item.children)}</SubMenu>);
	      }
	      return <Menu.Item key={item.key}><IIcon type={item.icon} /><span>{item.title}</span></Menu.Item>;
		});
		return <div className={"fc-menu-container"}>
			<div className="logo-c"><IIcon type="icon-pingpong"/><span className="logo-text">球掌柜</span></div>
			<Menu id="fc-menu-container" onClick={this.handleClick}
				openKeys={this.state.openKeys}
				onOpenChange={openKeys=>{
				this.setState({openKeys});
				}}
				selectedKeys={[this.state.current]}
				mode="inline"
		
				inlineIndent={10}
			>
				{loop(this.state.loginMenus)}
			</Menu>
		  </div>
	    ;
	}
}
