import React, { Component } from 'react';
import {message,Input,Modal,Upload,Icon,Select,Collapse,Popconfirm,Tooltip,Popover,DatePicker,InputNumber,Tag } from 'antd';
import Compressor from 'compressorjs';
import { emitter, util } from '../../../common/common';
import { urlPrefix } from '../../../interfaces/interfaces';
import {tenantId} from '../../../entry/index/routes/IndexPage';
export let imagePrefix=()=>'https://cdn.jsdelivr.net/gh/kkccww21/static@master/pic/'+tenantId+'/';
let uid=0;
export default class BizComponent extends Component {
    static defaultProps = {
        fileSize:1,
        quality:0.5,
        onLoading:loading=>{}
    };
    constructor(...args) {
        super(...args);
        let fileList=[];
        if(this.props.value){
            fileList=[{name:this.props.value,status:'done',url:imagePrefix()+this.props.value,uid:uid++}];
        }
        this.state = {
            previewVisible: false,
            previewImage: '',
            fileList
        };
    }
    

    componentDidUpdate(preProps){
      if(this.props.value!=preProps.value){
        let {fileList}=this.state;
              if(this.props.value){
                  if(!fileList||fileList.length==0){
                      fileList=[{name:this.props.value,status:'done',url:imagePrefix()+this.props.value,uid:uid++}];
                  }
              }else{
                  fileList=[];
              }
              this.setState({fileList});
      }
    }

    handleCancel = () => this.setState({ previewVisible: false });

    handlePreview = async file => {
        if (!file.url && !file.preview) {
          file.preview = await util.getFileBase64(file.originFileObj);
        }
    
        this.setState({
          previewImage: file.url || file.preview,
          previewVisible: true,
        });
    }

    handleChange = ({ fileList }) => {
        this.setState({ fileList });
    };

    render() {
        let {fileSize}=this.props;
        const { previewVisible, previewImage, fileList } = this.state;
        const uploadButton = (
          <div>
            <Icon type="plus" />
            <div className="ant-upload-text">上传</div>
          </div>
        );
        return (
          <div className="clearfix">
            <Upload
              listType="picture-card"
              fileList={fileList} withCredentials
              onPreview={this.handlePreview}
              accept="image/png, image/jpeg"
              onChange={this.handleChange}
              customRequest={({onProgress,onError,onSuccess,filename,file})=>{
                let hide=message.loading('文件上传中...', 20);
                this.props.onLoading(true);
                new Compressor(file, {
                    quality:this.props.quality,
                    success:async (result)=> {
                        let base64Str=await util.getFileBase64(result);
                        let key=util.guid()+'.jpg';
                        let res=await util.ajaxJsonAsync(urlPrefix+'SysUserFramework?opt=uploadFile',{key,type:result.type,message:this.props.message,content:base64Str.substring(base64Str.indexOf(";base64,") + 8)});
                        this.props.onLoading(false);
                        if(res.success){
                            message.success('上传成功！');
                            onSuccess(res);
                            if(fileSize==1){
                                this.props.onChange(key);
                            }else{}

                            let img=new Image();
                            img.src=imagePrefix()+key;// cdn预热一下
                            hide();
                        }else{
                            message.error('上传失败');
                            onError();
                            hide();
                        }
                    },
                    error(err) {
                      message.error('压缩失败:' + err);
                      hide();
                    },
                });
              }}
            >
              {fileList.length >= fileSize ? null : uploadButton}
            </Upload>
            <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
              <img alt="example" style={{ width: '100%' }} src={previewImage} />
            </Modal>
          </div>
        );
    }
}