import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';

import QueryCond from '../../../components/common/table/queryCond';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on(`menu-clicked-sys/optLog`,()=>{
      emitter.emit('queryOptLogTable');
    });
    this.config = {
      title:'操作日志',
      key:'OptLog',
      dialog:{width:1060},
      queryCond:{
        conditions:[[{key:'type'},{key:'accountName'}]],
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          this.queryParam=queryParam;
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInTable:true,
          label:'id'
        },{
          key:'accountName',
          width:'10%',
          label:'操作人'
        },{
          key:'type',
          width:'10%',
          label:'操作类型'
        },{
          key:'content',
          width:'40%',
          render:(v,r)=>{
            let reg = new RegExp(`"customer_name":"(.+?)"`,"mg");
            let rs;
            let resArr=[];
            while((rs=reg.exec(v)) != null){
              if(rs.length>0){
                resArr.push(rs[1]);
              }
            }
            if(resArr.length>0){
              let resStr=resArr.join(', ');
              if(r.type=='doTransfer'){
                let obj=JSON.parse(v);
                return `【转交数：${obj.customers.length}，接收人：${obj.owner_name}】 客户明细：${resStr}`;
              }
              return resStr;
            }
            return v;
          },
          label:'操作对象'
        },{
          key:'gmtCreate',
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'操作时间'
        },{
          key:'ip',
          width:'10%',
          label:'IP'
        },{
          key:'ua',
          width:'10%',
          label:'客户端'
        }
      ],
      // operationAddon:record=>{
      //   return (<span><a onClick={()=>{
          
      //   }}>设计</a></span>);
      // },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {status:'0'};
        },
        beforeModify:formData=>{
          return formData;
        },
        beforeSubmit:this.getFormData
      },
      table:{
        bordered:true,
        lazyInit:true,
        size:'small',
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'SysUserFramework?opt=queryOptLog'},
      }
    };
    this.state={
    };
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
      </div>
    );
  }
}