import React,{Component} from 'react';
import {
  Form,
  Input,
  Button,
  Checkbox,
  Radio,
  Tooltip,
  Icon,
  Select,
  Modal,
  message,
  Cascader,
  DatePicker,
  InputNumber,
  Col,
  Row
} from 'antd';
import CommonSelect from '../../select';
import {emitter,util} from '../../../common/common';
const RadioButton = Radio.Button;
const FormItem = Form.Item;
const Option = Select.Option;
export default class Field extends Component {
  constructor(...args) {
    super(...args);
  }

  render(){
    let {field,context} = this.props;
    let disabled = this.props.optType == 'modify' && field.editableInModify === false;
    let props = Object.assign({},this.props);
    delete props.field;
    delete props.optType;
    if(field.enums){
      let {enums}=field;
      if(typeof enums=='function')enums=enums();
      let mode=field.multi?'multiple':undefined;
      return (<Select multiple={field.multi} disabled={disabled} mode={mode} {...props}>{enums.map(e=>{
        let key,value;
        key=Array.isArray(e)?e[0]:(typeof e == 'string' ? e : e.key);
        value=Array.isArray(e)?e[1]:(typeof e == 'string' ? e : e.text);
        return <Option key={key} value={key+''}>{value}</Option>;
      })}</Select>);
    }else if(field.dictCode){
      return (<CommonSelect {...field} {...props}/>);
    }else if(field.type=='textarea'){
      let TextArea = Input.TextArea;
      if(!TextArea)TextArea=Input;
      return (<TextArea type="textarea" rows={field.rows?field.rows:4} disabled={disabled} {...props}/>);
    }else if(field.type=='date'){
      return (<DatePicker format="YYYY-MM-DD" disabled={disabled} {...props}/>);
    }else if(field.type=='datetime'){
      return (<DatePicker showTime format="YYYY-MM-DD HH:mm:ss" disabled={disabled} {...props}/>);
    }else if(field.type=='custom'){
      return field.getComponent(props,context);
    }else if(field.type=='number'){
      return (<InputNumber disabled={disabled} onKeyDown={this.props.onKeyDown} {...props}/>);
    }else{
      return (<Input disabled={disabled} onKeyDown={this.props.onKeyDown} {...props}/>);
    }
  }
}