import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,menus,urls,isAdmin} from '../../../entry/index/routes/IndexPage';
import SelectTable from '../../../components/select/selectTable';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    this.config = {
      title:'角色管理',
      key:'sysRole',
      dialog:{width:1060},
      queryCond:{
      conditions:[[{key:'name'}]],
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          this.queryParam=queryParam;
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInForm:true,
          notInTable:true,
          label:'id'
        },{
          key:'name',
          width:'6%',
          rules:[{required: true, message: '必填'}],
          label:'名称'
        },{
          key:'type',
          width:'4%',
          notInForm:true,
          enums:[{key:'系统',text:'系统'},{key:'自定义',text:'自定义'}],
          label:'类型'
        },{
          key:'menus',
          width:'20%',
          enums:()=>menus.map(it=>({key:it.code,text:it.name})),
          rules:[{required: true, message: '必填'}],
          multi:true,
          label:'角色包含菜单'
        },{
          key:'urls',
          width:'20%',
          enums:()=>urls.map(it=>({key:it.code,text:it.name})),
          multi:true,
          label:'角色包含功能'
        },{
          key:'gmtModified',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'修改时间'
        },{
          key:'gmtCreate',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'创建时间'
        }
      ],
      // operationAddon:record=>{
      //   return (<span><a onClick={()=>{
          
      //   }}>设计</a></span>);
      // },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {type:'自定义'};
        },
        beforeModify:formData=>{
          return formData;
        },
        beforeSubmit:formData=>{
          formData.content=JSON.stringify({menus:formData.menus,urls:formData.urls});
          return formData;
        },
        afterSubmit:res=>{
          emitter.emit('init-user-info');
        }
      },
      table:{
        bordered:true,
        size:'small',
        canModify:r=>r.type=='自定义',
        canDelete:r=>r.type!='系统',
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.list.forEach(it=>{
            if(it.content){
              let contentObj=JSON.parse(it.content);
              if(contentObj){
                it.menus=contentObj.menus;
                it.urls=contentObj.urls;
              }
            }
          });
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'SysUserFramework?opt=querySysRole'},
        updateInterface:isAdmin?{func:urlPrefix+'SysUserFramework?opt=updateSysRole'}:undefined,
        insertInterface:isAdmin?{func:urlPrefix+'SysUserFramework?opt=insertSysRole'}:undefined,
        deleteInterface:isAdmin?{func:urlPrefix+'SysUserFramework?opt=deleteSysRole',params:{id:'id'}}:undefined
      }
    };
    this.state={
    };
  }

  render() {
    return (
      <div>
        {/* <SelectTable columns={[{key:'id',title:'ID',width:'30%'},{key:'name',title:'名称',width:'70%'}]} urlSuffix="MemberOperation?opt=queryMember" searchKey="name"/> */}
       <BizComp config={this.config}/>
      </div>
    );
  }
}