import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Tabs, Form, Select,Tree, Popconfirm, TreeSelect} from 'antd';
import '../index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../../common/common';
import {urlPrefix,urlProductPrefix} from '../../../../interfaces/interfaces';
import {convertWithChildren,cateTypes} from './index';
export default class BizComponent extends Component {
  static defaultProps = {
    withEmpty:false
  }
  constructor(...args) {
    super(...args);
    this.initTreeData();
    this.state={
        treeData:[]
    };
  }

  initTreeData=()=>{
    let {type,withEmpty}=this.props;
    util.ajaxJson(urlPrefix+'SysConfigManager?opt=categoryList',{type:type?type:cateTypes[0]},res=>{
      let arryData=res.data;
      if(withEmpty){
        arryData.push({id:-1,cateName:'无'});
      }
      let treeData=convertWithChildren(arryData);
      this.setState({treeData});
    });
  }

  loop = data => {
    if(!data)return data;
    return data.map(item => {
      if (item.children && item.children.length > 0) {
        return <Tree.TreeNode title={item.cateName} key={item.id} value={item.id}>{this.loop(item.children)}</Tree.TreeNode>;
      }
      return <Tree.TreeNode title={item.cateName} key={item.id} value={item.id} isLeaf={item.isLeaf}/>;
    });
  }

  render() {
    let {fieldKey}=this.props;
    let {treeData}=this.state;
    let nodes = this.loop(treeData);
    return (
      <TreeSelect
        showSearch
        style={{ width: '100%' }}
        
        dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
        placeholder="请选择"
        allowClear
        treeDefaultExpandAll
        {...this.props}
      >
        {nodes}
      </TreeSelect>
    );
  }
}