import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Select,Icon, Tabs} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../../common/common';
import BizComp from '../../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../../interfaces/interfaces';
import {isAdmin,allStaffs} from '../../../../entry/index/routes/IndexPage';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    // emitter.on(`menu-clicked-sys/config/baseItem`,()=>{
    //   emitter.emit('queryOptLogTable');
    // });
    let configs=[{
      key:'StockMgr',
      title:'仓库',
      conditions:[[{key:'name'}]],
      fields:[{
        key:'owner',
        label:'联系人',
        render:(v,r)=>r.content.owner,
        enums:allStaffs.map(it=>it.name)
      },{
        key:'address',
        render:(v,r)=>r.content.address,
        label:'地址'
      }],
      beforeSubmit:formData=>{
        let {owner,address,content}=formData;
        if(!content)content={};
        if(owner){
            content.owner=owner;
            delete formData.owner;
        }
        if(address){
          content.address=address;
          delete formData.address;
        }
        formData.content=JSON.stringify(content);
        return formData;
      }
    },{
        key:'UnitMgr',
        title:'单位',
        conditions:[[{key:'name'}]],
        fields:[]
    },{
        key:'ItemProp',
        title:'商品属性',
        conditions:[[{key:'name'}]],
        fields:[{
            key:'values',
            multi:true,
            type:'custom',
            label:'属性值',
            rules:[{required:true,message:'必填'}],
            render:v=>{
                if(!v)return v;
                return v.join(',');
            },
            getComponent:(props,context)=>{
                let values=[];
                if(context.state.formValues&&context.state.formValues.values){
                    values = context.state.formValues.values;
                }
                return <Select mode="tags" placeholder="请输入需要新增的属性" {...props}>
                {values.map(it=><Select.Option key={it} value={it}>{it}</Select.Option>)}
            </Select>;
            }
        }],
        beforeSubmit:formData=>{
            let {values,content}=formData;
            if(!content)content={};
            if(values){
                content.values=values;
                delete formData.values;
            }
            formData.content=JSON.stringify(content);
            return formData;
        }
    }];
    this.configs = configs.map(cfg=>({
        title:cfg.title,
        key:cfg.key,
        dialog:{width:1060},
        queryCond:{
          conditions:cfg.conditions,
          beforeQuery:queryParam=>{
            queryParam = queryParam || {};
            queryParam.type=cfg.title;
            this.queryParam=queryParam;
            return queryParam;
          },
          size:'small'
        },
        fields:[
          {
            key:'id',
            rowKey:true,
            notInTable:true,
            notInForm:true,
            label:'id'
          },{
            key:'name',
            width:'20%',
            label:'名称',
            rules:[{required:true,message:'必填'}]
          },...cfg.fields,{
            key:'num',
            width:'5%',
            notInForm:true,
            label:'序值'
          },{
            key:'gmtModified',
            width:'15%',
            render:v=>util.formatDate(v),
            notInForm:true,
            label:'修改时间'
          },{
            key:'lastModifier',
            width:'10%',
            notInForm:true,
            label:'最近修改人'
          },{
            key:'operator',
            width:'10%',
            notInForm:true,
            label:'创建人'
          }
        ],
        operationAddon:(record,idx)=>{
          if(cfg.operationAddon)return cfg.operationAddon(record);
          if(this.resultList){
            let data=this.resultList;
            let upEl=idx>0&&<a onClick={()=>{
                let otherItem=data[idx-1];
                util.ajaxJson(urlPrefix+'SysConfigManager?opt=moveBaseItem',{id1:record.id,num1:otherItem.num,id2:otherItem.id,num2:record.num},res=>{
                  if(res.success)message.success('上移成功');
                  emitter.emit(`query${cfg.key}Table`);
                });
              }}>上移</a>;
              let downEl=idx<data.length-1&&<a onClick={()=>{
                let otherItem=data[idx+1];
                util.ajaxJson(urlPrefix+'SysConfigManager?opt=moveBaseItem',{id1:record.id,num1:otherItem.num,id2:otherItem.id,num2:record.num},res=>{
                  if(res.success)message.success('下移成功');
                  emitter.emit(`query${cfg.key}Table`);
                });
              }}>下移</a>;
            return (<span>{upEl} {downEl}</span>);
          }
        },
        form:{
          col:1,
          beforeAdd:formData=>{
            return {status:'1'};
          },
          beforeModify:formData=>{
            return {...formData,...formData.content};
          },
          beforeSubmit:formData=>{
            formData.type=cfg.title;
            formData.code=formData.name;
            if(cfg.beforeSubmit){
                return cfg.beforeSubmit(formData);
            }
            return formData;
          }
        },
        table:{
          bordered:true,
          size:'small',
          resultHandler:(result,queryParam)=>{
            if(cfg.resultHandler){
                return cfg.resultHandler(result,queryParam);
            }
            this.resultList=result.data;
            if(result.data){
                result.data.forEach(it=>{
                    if(it.content){
                        it.content=JSON.parse(it.content);
                        it.values=it.content.values;
                    }
                });
            }
          }
        },
        api:{
          queryInterface:{func:urlPrefix+'SysConfigManager?opt=baseItemList'},
          updateInterface:isAdmin?{func:urlPrefix+'SysConfigManager?opt=updateBaseItem'}:undefined,
          insertInterface:isAdmin?{func:urlPrefix+'SysConfigManager?opt=insertBaseItem'}:undefined,
          deleteInterface:isAdmin?{func:urlPrefix+'SysConfigManager?opt=deleteBaseItem',params:{id:'id'}}:undefined
        }
      }));
    this.state={
    };
  }

  render() {
    return (
      <div>
        <Tabs defaultActiveKey="0">
            {this.configs.map((it,idx)=><Tabs.TabPane tab={it.title+'管理'} key={idx+''}>
            <BizComp config={it}/>
            </Tabs.TabPane>)}
        </Tabs>
       
      </div>
    );
  }
}