import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Popconfirm, Switch} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs} from '../../../entry/index/routes/IndexPage';
import DatePick from '../../../components/select/datePick';
import SettlementDrawer from './settlementDrawer';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on(`menu-clicked-train/coach`,()=>{
      emitter.emit('queryCoachTable');
    });
    this.config = {
      title:'教练管理',
      key:'Coach',
      dialog:{width:1060},
      queryCond:{
      conditions:[[{key:'name'},{key:'onlyTrain',label:'仅有训练记录',type:'custom',options:{mode:'checkbox'},getComponent:props=><Switch {...props} onChange={(v)=>{
        if(props.onChange)props.onChange(v);
        emitter.emit(`on${this.config.key}QueryBtnClick`,true);
        this.refreshExpandRow(false);
      }}></Switch>},
        {key:'time',label:'时间范围',type:'custom',shouldDisplay:(context)=>context.state.formValues.onlyTrain,getComponent:(props,context)=>{
        return <DatePick onChange={rangeValue=>{
          props.onChange(rangeValue);
          emitter.emit(`on${this.config.key}QueryBtnClick`,true);
          this.refreshExpandRow(false);
        }}/>;
      }}]],
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInForm:true,
          notInTable:true,
          label:'id'
        },{
          key:'name',
          width:'6%',
          rules:[{required: true, message: '必填'}],
          label:'名称'
        },{
          key:'phone',
          width:'10%',
          props:{maxLength:11},
          rules:[{required: true, message: '必填'},{validator:v=>{
            return /^1[0-9]{10}$/.test(v);
          },message:'必须为1开头的11位手机号'}],
          label:'手机号'
        },{
          key:'introduction',
          width:'20%',
          rules:[{required: true, message: '必填'}],
          label:'简介'
        },{
          key:'score',
          width:'6%',
          type:'number',
          props:{min:0,precision:0},
          label:'积分'
        },{
          key:'unitPrice',
          width:'6%',
          type:'number',
          props:{min:0,precision:0},
          rules:[{required: true, message: '必填'}],
          label:'课时单价'
        },{
          key:'type',
          width:'6%',
          enums:[{key:'乒乓',text:'乒乓'}],
          label:'类型'
        },{
          key:'status',
          width:'6%',
          enums:[{key:'1',text:'在职'},{key:'0',text:'离职'}],
          label:'类型'
        },{
          key:'gmtModified',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'修改时间'
        },{
          key:'gmtCreate',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'创建时间'
        }
      ],
      operationPrefix:record=>{
        return (<span><a onClick={()=>{
          emitter.emit('show-coach-settlement-drawer',record);
        }}>结算</a></span>);
      },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {status:'1',type:'乒乓'};
        },
        beforeModify:formData=>{
          formData.status=formData.status+'';
          return formData;
        },
        beforeSubmit:formData=>{
          return formData;
        },
        afterSubmit:()=>{
          emitter.emit('init-user-info');
        }
      },
      table:{
        bordered:true,
        size:'small',
        expandedRowKeys:()=>this.state.expandedRowKeys,
        expandIcon:(pops)=>{
          let {expandedRowKeys}=this.state;
          let expanded=expandedRowKeys.indexOf(pops.record.id)>-1;
          if(!pops.record.recordCount)return;
          return <Icon type={expanded?"minus-square":"plus-square"} onClick={()=>{
            let {expandedRowKeys}=this.state;
            if(expanded){
              expandedRowKeys=[];
            }else{
              expandedRowKeys=[];
              expandedRowKeys.push(pops.record.id); 
            }
            this.setState({expandedRowKeys},this.refreshExpandRow);
          }}/>
        },
        expandedRowRender:(record, index, indent, expanded)=>{
          let {recDs}=this.state;
          return <Table className="student-rec-table" columns={[
          {key:'studyTime',dataIndex:'studyTime',title:'训练时间',render:v=>util.formatDate(v)},
          {key:'studentName',dataIndex:'studentName',title:'学员'},
          {key:'phone',dataIndex:'phone',title:'学员手机'},
          {key:'consumeTimes',dataIndex:'consumeTimes',title:'课时数'},
          {key:'consumeAmount',dataIndex:'consumeAmount',title:'消费金额(元)'},
          {key:'billed',dataIndex:'billed',title:'是否结算',render:v=>v==1?'已结算':'未结算'},
          {key:'billedTime',dataIndex:'billedTime',title:'结算时间',render:v=>util.formatDate(v)},
          {key:'billedOperator',dataIndex:'billedOperator',title:'结算人'},
          {key:'operator',dataIndex:'operator',title:'经办人'},
          {key:'opt',dataIndex:'opt',title:'操作',render:(v,r)=><span>{r.billed!=1&&<Popconfirm title="确定要删除？" onConfirm={()=>{
            util.ajaxJson(urlPrefix+'TrainOperation?opt=deleteStudentRecord',{studentId:record.id,id:r.id},res=>{
              if(res.success){
                message.success('删除流水成功');
                this.refreshExpandRow(true);
              }
            });
          }}><a>删除</a></Popconfirm>}</span>}]} rowKey={r=>r.id} dataSource={recDs.data} onChange={(recPagination)=>{
            this.setState({recPagination},()=>this.refreshExpandRow(true));
          }} pagination={recDs.totalItem<=10?false:{...this.state.recPagination,total:recDs.totalItem}} size="small"/>;
        },
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.list.forEach(it=>{
            if(it.ext){
              it.staffs=JSON.parse(it.ext).staffs;
            }
          });
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'TrainOperation?opt=queryCoach'},
        updateInterface:isAdmin?{func:urlPrefix+'TrainOperation?opt=updateCoach'}:undefined,
        insertInterface:isAdmin?{func:urlPrefix+'TrainOperation?opt=insertCoach'}:undefined,
        deleteInterface:isAdmin?{func:urlPrefix+'TrainOperation?opt=deleteCoach',params:{id:'id'}}:undefined
      }
    };
    this.state={
      expandedRowKeys:[],
      recDs:{totalItem:0}
    };
  }

  refreshExpandRow=(retainPage)=>{
    let {expandedRowKeys,recPagination}=this.state;
    if(!recPagination)recPagination={current:1,pageSize:10};
    if(!retainPage)recPagination.current=1;
    if(expandedRowKeys&&expandedRowKeys.length>0){
      emitter.emit(`get${this.config.key}QueryCondForm`,(queryParam={})=>{
        let {time,onlyTrain}=queryParam;
        util.ajaxJson(urlPrefix+'TrainOperation?opt=queryStudentRecord',{coachId:expandedRowKeys[0],onlyTrain,coach:true,time,currentPageNum:recPagination.current,pageSize:recPagination.pageSize},res=>{
          this.setState({recDs:res.data});
        });
      });
    }
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
       <SettlementDrawer/>
      </div>
    );
  }
}