import React,{Component,Fragment} from 'react';
import ReactDOM from 'react-dom';
import Table from './table/table';
import Form from './form/form';
import QueryCond from './table/queryCond';
import {emitter} from '../../common/common';
import {
  Button,
  Row,
  Col,Icon
} from 'antd';
import './index.less';

export default class BizIndex extends Component {
  constructor(...args) {
    super(...args);
    this.components={};
    this.state = {
         keyword:''
    };
  }

  getForm=()=>{
    return this.components.form;
  }
  
  render() {
    let {onlyUseForm} = this.props;// 只是想用下表单，场景：其他页面想新增/修改时
    return (
      <div className={this.props.config.key+"-container"}>
        {!onlyUseForm&&<Fragment>
          <Row type="flex" justify="space-between" className="table-header">
            <Col span={6}><h2>{this.props.config.title}</h2></Col>
            <Col span={2}>
            </Col>
            <Col span={14}></Col>
            <Col span={2}>{this.props.config.api.insertInterface&&<Button type="primary" size="small" className="button" onClick={()=>{
              emitter.emit(`show${this.props.config.key}Dialog`, undefined, 'add');
            }}><Icon type="plus" />新增</Button>}</Col>
          </Row>
          <QueryCond wrappedComponentRef={e=>{
            this.components.queryCond=e;
          }} {...this.props}/>
          <Table ref={e=>{
            this.components.table=e;
          }} {...this.props}/>
        </Fragment>}
        <Form ref={e=>{
            this.components.form=e;
          }} {...this.props} />
      </div>
    );
  }
}

