import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs} from '../../../entry/index/routes/IndexPage';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    this.config = {
      title:'球馆管理',
      key:'ballRoom',
      dialog:{width:1060},
      queryCond:{
      conditions:[[{key:'name'}]],
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          this.queryParam=queryParam;
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInForm:true,
          notInTable:true,
          label:'id'
        },{
          key:'name',
          width:'15%',
          rules:[{required: true, message: '必填'}],
          label:'名称'
        },{
          key:'address',
          width:'20%',
          rules:[{required: true, message: '必填'}],
          label:'地址'
        },{
          key:'boss',
          width:'10%',
          rules:[{required: true, message: '必填'}],
          label:'法人'
        },{
          key:'staffs',
          width:'30%',
          enums:allStaffs.map(it=>({key:it.id+'',text:it.name})),
          rules:[{required: true, message: '必填'}],
          multi:true,
          label:'球馆员工'
        },{
          key:'gmtModified',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'修改时间'
        },{
          key:'gmtCreate',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:'创建时间'
        }
      ],
      // operationAddon:rec=>{
      //   return (<span><a onClick={()=>{
          
      //   }}>设计</a></span>);
      // },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {};
        },
        beforeModify:formData=>{
          return formData;
        },
        beforeSubmit:formData=>{
          formData.ext=JSON.stringify({staffs:formData.staffs});
          delete formData.staffs;
          return formData;
        },
        afterSubmit:()=>{
          emitter.emit('init-user-info');
        }
      },
      table:{
        bordered:true,
        size:'small',
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.list.forEach(it=>{
            if(it.ext){
              it.staffs=JSON.parse(it.ext).staffs;
            }
          });
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'BallRoomOperation?opt=queryBallRoom'},
        updateInterface:isAdmin?{func:urlPrefix+'BallRoomOperation?opt=updateBallRoom'}:undefined,
        insertInterface:isAdmin?{func:urlPrefix+'BallRoomOperation?opt=insertBallRoom'}:undefined,
        deleteInterface:isAdmin?{func:urlPrefix+'BallRoomOperation?opt=deleteBallRoom',params:{id:'id'}}:undefined
      }
    };
    this.state={
    };
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
      </div>
    );
  }
}