import React, {Component} from 'react';
import {
  Form,
  Input,
  Button,
  Checkbox,
  Radio,
  Tooltip,
  Icon,
  Select,
  Modal,
  message,
  Cascader,
  InputNumber,
  Col,
  Row
} from 'antd';
import {emitter, util} from '../../../common/common';
import Field from './field';
const RadioButton = Radio.Button;
const FormItem = Form.Item;
const Option = Select.Option;

export default class BizForm extends Component {
  constructor(...args) {
    super(...args);
    this.hiddenFields = this
      .props
      .config
      .fields
      .filter(f => f.isHiddenInForm && !f.notInForm);
    this.formFields = this
      .props
      .config
      .fields
      .filter(f => !f.notInForm && !f.isHiddenInForm);
    emitter.on(`show${this.props.config.key}Dialog`, (formData, optType) => {
      if (this.props.config.form && this.props.config.form.beforeShowDialog) {
        let shouldShow = this
          .props
          .config
          .form
          .beforeShowDialog(formData, optType);
        if (!shouldShow) 
          return;
        }
      this.setState({
        visible: true,
        optType
      }, () => {
        if (optType == 'modify') {
          let fieldsValue = formData;
          if (this.props.config.form && this.props.config.form.beforeModify) {
            fieldsValue = this
              .props
              .config
              .form
              .beforeModify(formData);
          }
          this.setState({formValues: fieldsValue});
        } else {
          let formValues = {};
          if (this.props.config.form && this.props.config.form.beforeAdd) {
            let fieldsValue = this
              .props
              .config
              .form
              .beforeAdd(formData);
            fieldsValue && (formValues = fieldsValue);
          }
          this.setState({formValues});
        }
      });
    });
    this.state = {
      visible: false,
      optType: 'add',
      formValues: {}
    };
  }
  handleSubmit = () => {
    util
      .validateFields
      .call(this, values => {
        let fieldsValue = values;
        if (this.props.config.form && this.props.config.form.beforeSubmit) {
          fieldsValue = this
            .props
            .config
            .form
            .beforeSubmit(values, this.state.optType);
        }
        let hideLoading = message.loading('executing', 10);
        util.ajaxJson(this.state.optType == 'modify'
          ? this.props.config.api.updateInterface.func
          : this.props.config.api.insertInterface.func, {
          rec: fieldsValue
        }, (data) => {
          if (data.success) {
            if (data.result && data.result.resultType == 'FAIL') {
              message.error('保存失败, 原因：' + data.result.failMessage);
            } else {
              message.success('保存成功');
            }
            this.setState({
              visible: false
            }, () => {
              this.setState({});
            });
            setTimeout(() => {
              emitter.emit(`query${this.props.config.key}Table`, undefined, true);
            }, this.props.config.delayQueryTable
              ? this.props.config.delayQueryTable
              : 0);
          }
          if (this.props.config.form && this.props.config.form.afterSubmit) {
            this
              .props
              .config
              .form
              .afterSubmit(data, fieldsValue);
          }
          hideLoading();
        }, () => {
          hideLoading();
        });

      });
  }
  handleCancel = () => {
    if (this.props.config.form && this.props.config.form.beforeCancel) {
      this
        .props
        .config
        .form
        .beforeCancel();
    }
    this.setState({visible: false, formValues: {}});
  }

  shouldFieldDisplay = (field) => {
    let shouldDisplay = true;
    if (this.state.optType == 'add' && field.notInAdd === true) {
      shouldDisplay = false;
    }
    return shouldDisplay;
  }

  isRequired = (field) => {
    if (field && field.rules && field.rules.length > 0) {
      for (let k in field.rules) {
        if (field.rules[k].required) {
          return true;
        }
      }
    }
    return false;
  }

  render() {
    const formItemLayout = {
      labelCol: {
        span: 6
      },
      wrapperCol: {
        span: 14
      }
    };
    let col = 1,
      className;
    if (this.props.config.form) {
      if (this.props.config.form.col) {
        col = this.props.config.form.col;
      }
      if (this.props.config.form.className) {
        className = this.props.config.form.className;
        this['__formClassName_'] = className;
      }
    }
    let modalTitle = typeof this.props.config.title == 'string'
      ? this.props.config.title
      : '';
    return (
      <Modal
        title={this.state.optType == 'modify'
        ? `修改 ${modalTitle}`
        : `新增 ${modalTitle}`}
        maskClosable={false}
        visible={this.state.visible}
        width={this.props.config.dialog.width}
        onCancel={this.handleCancel}
        footer=
        { [<Button key="1" type="primary" size="large" onClick={this.handleSubmit}> 保存 </Button>, <Button key="2" type="ghost" size="large" onClick={this.handleCancel}> 取消 </Button>] }>
        <Form className={className}>
          {this
            .hiddenFields
            .map(hiddenField => {
              return <Input
                key={hiddenField.key}
                type="hidden"
                {...util.getField.bind(this)(hiddenField.key,{...hiddenField.options})}/>;
            })}
          <Row>
            {this
              .formFields
              .filter(field => {
                if (field.shouldDisplay && field.shouldDisplay(this) === false) {
                  return false;
                }
                return true;
              })
              .map(field => {
                let label = <span>{field.label}{field.tooltip}</span>;
                return (
                  <Col key={field.key} span={24 / col}>
                    <FormItem
                      required={this.isRequired(field)}
                      style={{
                      display: this.shouldFieldDisplay(field)
                        ? 'block'
                        : 'none'
                    }}
                      label={label}
                      {...formItemLayout}>
                      <Field
                        field={field}
                        optType={this.state.optType}
                        {...field.props}
                        {...util.getField.bind(this)(field.key,{rules:field.rules,...field.options})}
                        context={this}/>
                    </FormItem>
                  </Col>
                );
              })}
          </Row>
        </Form>
      </Modal>
    );
  }
}