import React,{Component} from 'react';
import PropTypes from 'prop-types';
import {message,Input,Modal,Form,Button} from 'antd';
import './index.less';
import {util,emitter} from '../../common/common';
import {prefix} from '../../interfaces/interfaces';
import Contacts from './contact';
const BizContext = React.createContext();
export const {Provider, Consumer} = BizContext;
export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    this.state={
      formValues:{
        contacts:this.props.data||[]
      }
    };
  }
  componentDidUpdate(preProps){
		if(this.props.data!=preProps.data){
			let {formValues}=this.state;
			formValues.contacts=this.props.data;
			this.setState({formValues});
		}
  }

  onChange=formValues=>{
    let contacts;
    if(formValues){
      contacts=formValues.contacts;
    }
    this.props.onChange(contacts);
  }
  
  render() {
    return (
      <Provider value={this}>
        <div className="contact-form">
            <label>联系人信息：</label>
            <Contacts onChange={this.onChange} onCall={this.props.onCall}/>
        </div>
      </Provider>
    );
  }
}