import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Tabs, Form, Select,Tree, Popconfirm, InputNumber} from 'antd';
import '../index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../../common/common';
import {urlPrefix,urlProductPrefix} from '../../../../interfaces/interfaces';
import CateSelect from './cateSelect';
export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on('show-category-crud-modal',({opt,type,rec})=>{
        let {formValues}=this.state;
        if(opt=='insert'){
            formValues={};
            if(rec&&rec.pid)formValues.pid=rec.pid;
        }else{
            formValues=rec;
        }
        this.setState({opt,type,visible:true,formValues},()=>{
            if(this.cateSelectEl)this.cateSelectEl.initTreeData();
        });
    });
    this.state={
        formValues:{

        }
    };
  }

  handleOk=()=>{
    util.validateFields.call(this,values=>{
      util.ajaxJson(urlPrefix+`SysConfigManager?opt=${this.state.opt}Category`, {rec:{...values,type:this.state.type}}, res=>{
        if(res.success){
          message.success(`${this.state.opt=='insert'?'新增':'修改'}类目成功!`);
          emitter.emit('refresh-cate-data-mgr');
          this.cateSelectEl.initTreeData();
          this.handleCancel();
        }
      });
    });
  }

  handleCancel=()=>{
    this.setState({visible:false});
  }

  render() {
    let {formValues,opt,type}=this.state;
    return (
        <Modal title={`${opt=='insert'?'新增':'修改'}类目`} visible={this.state.visible} width={400} onCancel={this.handleCancel} maskClosable={false}
        footer={[
          <Button key="login" type="primary" onClick={this.handleOk}>保存</Button>,
          <Button key="close" onClick={this.handleCancel}>关闭</Button>
        ]}>
            <Form.Item className="" label="类名名称" required>
                <Input {...util.getField.call(this,'cateName',{rules:[{required:true,message:'必填'}]})}/>
            </Form.Item>
            <Form.Item className="" label="上级分类" required>
                <CateSelect withEmpty {...util.getField.call(this,'pid',{rules:[{required:true,message:'必填'}]})} type={type} ref={el=>this.cateSelectEl=el}/>
            </Form.Item>
        </Modal>
    );
  }
}