import React,{Component} from 'react';
import {message,Input,Modal,Button} from 'antd';
import {util,emitter} from '../../../../common/common';
import {domain} from '../../../../interfaces/interfaces';
import './index.less';
export default class BizComp extends Component {
    constructor(...args) {
        super(...args);
        emitter.on('call-out-ccc',(callee,that)=>{
            callee=callee+'';
            if(!(/^1[3456789]\d{9}$/.test(callee))){
                return message.warning('手机号格式有误，必须为1开头的11位数字');
            }
            if(!window.workbench){
                return message.warning('电话坐席未初始化！');
            }
            let status=window.workbench.getStatus();
            if(status!='ready')return message.warning('电话坐席未初始化成功！');
            window.workbench.call(callee);
            if(that&&that.props&&that.props.onCall)that.props.onCall(callee);
        });
        this.state={
        };
    }
    componentDidMount(){
        setTimeout(this.initHotline,200);
        
    }

    initHotline=()=>{
        window.workbench = new window.WorkbenchSdk({
            dom: 'workbench',
            instanceId: '62e83165-8999-4462-81b9-7adb13521ab6',
            ajaxOrigin:'//'+domain,
            ajaxPath: '/iflow-proxy/api/v1/crm',
            ajaxMethod: 'post',
            ajaxOtherParams:{isFromCCC:1},
            withCredentials:true,
            exportErrorOfApi:true,
            
            moreActionList:['onlyCall'],
            header: true,
            onInit: function () {
                // window.workbench.register();
            },
            onRegister:function(config){
                // document.querySelector('.next-icon-suoxiao1').click();
            },
            onErrorNotify: function (error) {
              console.warn(error)
            },
            onCallComing: function (aa, bb, cc, dd) {
              console.log(aa, bb, cc, dd);
            },
            onCallDialing: function (aa, bb, cc, dd) {
              let iconEl=document.querySelector('#workbench .next-icon-guaduan');
              if(iconEl&&iconEl.previousElementSibling){
                iconEl.previousElementSibling.style.visibility='hidden';
              }
              let tabContentEl=document.querySelector('.next-tabs-content h1');
              if(tabContentEl){
                tabContentEl.style.visibility='hidden';
              }
            },
            onStatusChange: function (code, lastCode, context){
              console.log(code, lastCode, context);
            },
            onCallRelease: function (connid, caller, privateData) {
                console.log("这里是通话结束时触发的回调函数", connid, caller, privateData)
            }
        });
        document.querySelector('.next-icon-suoxiao1').click();
    }

    render() {
  	    return (<div className="hotline-widget">
            <div id="workbench"></div>
          </div>);
    }
}