import React,{Component} from 'react';
import {message,Input,Modal,Button,Form,Select,Collapse,Popconfirm,Tooltip,Popover } from 'antd';
import {util,emitter} from '../../common/common';
import './index.less';
export default class CommonComp extends Component {
	constructor(...args) {
		super(...args);
	    this.state={formValues:{}};
	}
	render(){
		let {storageItemKey,value,onLoad,onSave,top,left,right,bottom,text,zIndex,position} = this.props;
		storageItemKey = storageItemKey.replace('.','-');
		let paramArr = [];
	    let loadParams = localStorage.getItem(storageItemKey);
	    if(loadParams){
	    	paramArr = JSON.parse(loadParams);
	    }
	    let paramArrRevers = [];
	    for (let i = 0; i < paramArr.length;i++) {
	    	paramArrRevers[i] = paramArr[paramArr.length - i - 1];
	    }
	    let content = <ul className={`save-btn-pop save-btn-pop-${storageItemKey}`}>
	  		<li>名称:<Input {...util.getField.bind(this)('saveParamName')}/><Button type="primary" size="small" onClick={()=>{
	    			paramArr.push({name:this.state.formValues.saveParamName,value,t:new Date().getTime()});
	    			if(paramArr.length > 10){
	    				paramArr.splice(0,1);
					}
					let paramsStr=JSON.stringify(paramArr);
	    			localStorage.setItem(storageItemKey,paramsStr);
					message.success(`保存${text?text:'参数'}成功!`);
					if(onSave)onSave(paramsStr);
	    			this.setState({...this.state});
	            }}>保存</Button></li>
	  		<div>
	  		{paramArrRevers.map((p,idx)=>{
	  			return <div className="save-item" key={idx}><a className="save-name-label" title={p.name} onClick={()=>{
	  				onLoad(p.value);
	  			}}>{p.name}</a> <span title={p.value}>{util.formatDate(p.t,'MM-dd hh:mm:ss')}</span><Popconfirm title={<div>确定要删除[{p.name}]吗?</div>} onConfirm={()=>{
					    paramArr.splice(paramArr.length-1-idx,1);
					    let paramsStr=JSON.stringify(paramArr);
						localStorage.setItem(storageItemKey,paramsStr);
						if(onSave)onSave(paramsStr);
	  				    this.setState({...this.state});
	  			}} okText="确定" cancelText="取消"><a>删除</a></Popconfirm></div>;
	  		})}
	  		</div>
	  	</ul>;
	    return <Popover title={`保存&加载${text?text:'参数'}`} content={content} trigger="click"><a style={{top,left,right,bottom,position:position?position:'absolute',zIndex}}>保存{text!==undefined?text:'配置参数'}</a></Popover>;
	}
}