import React, { Component } from 'react';
import {Button, Drawer, Modal, Popconfirm,Form,Input} from 'antd';
import { emitter, util } from '../../../../common/common';
import './index.less';
export default class BizComponent extends Component {
    constructor(...args) {
        super(...args);
        emitter.on('open-rich-editor-drawer',({key,title,width=1000,content='',rec,fields})=>{
            let {formValues}=this.state;
            if(fields){
                fields.forEach(it=>{
                    if(it.defaultValue){
                        formValues[it.key]=it.defaultValue;
                    }
                });
            }
            this.setState({visible:true,key,width,title,rec,fields,formValues,isModified:false},()=>{
                if(!window.ue){
                    setTimeout(()=>{
                        window.ue=window.UE.getEditor('uedit-container');
                        window.ue.ready(()=>{
                            window.ue.setContent(content);
                            this.setState({isModified:false});
                            window.ue.addListener("contentChange",()=>{
                                this.setState({isModified:true});
                            });
                            window.ue.addListener("keydown", (cmd, evt)=>{
                                if(evt.ctrlKey&&evt.keyCode==83){
                                    evt.preventDefault ? evt.preventDefault() : (evt.returnValue = false);
                                    this.onSave(true);
                                    this.setState({isModified:false});
                                }
                            });
                        });
                    },200);
                    
                }else{
                    window.ue.setContent(content);
                    this.setState({isModified:false});
                }
            });
            
        });
        this.state={
            visible:false,
            isModified:false,
            formValues:{}
        };
    }
    onSave=(dontClose)=>{
        emitter.emit('rich-editor-save-'+this.state.key,{content:window.ue.getContent(),rec:this.state.rec,fieldValues:this.state.formValues});
        if(!dontClose)this.onClose();
        this.setState({isModified:false});
    }
    onClose=()=>{
        this.setState({visible:false});
    }
    onCloseConfirm=()=>{
        if(this.state.isModified){
            Modal.confirm({
                title:"关闭确认",
                content:"确定要未保存就退出吗？",
                onOk:this.onClose
            });
        }else{
            this.onClose();
        } 
    }
    render() {
        let {title,width,visible,fields,isModified}=this.state;
        return <>
        <Drawer title={title} visible={visible} maskClosable={false} width={width} className="ueditor-drawer" placement="right" onClose={this.onCloseConfirm}>
            {fields&&fields.map(field=><Form.Item key={field.label} label={field.label}><Input {...util.getField.call(this,field.key,field.options)}/></Form.Item>)}
            <script id="uedit-container" name="content" type="text/plain" style={{height:'calc(100vh - 300px)'}}></script>
            <Popconfirm disabled={!isModified} title="确定提交？" onConfirm={this.onSave}><Button disabled={!isModified} type="primary">保存</Button></Popconfirm>
            <Button onClick={this.onCloseConfirm}>取消</Button>
        </Drawer>
        </>;
    }
}