import React, {
  Component
} from 'react';
import {
  Table,
  message
} from 'antd';
import {emitter,util} from '../../../common/common';
export default class Grid extends Component {
  static defaultProps = {
      pagination : true,
      currentPageName : 'currentPageNum',
      pageSizeName : 'pageSize',
      totalName : 'totalItem',
      defaultPageSize:10,
      queryCondHandler : queryParam=>queryParam
  };

  constructor(...args) {
    super(...args);
    emitter.on(this.props.queryEmitter, (queryCond,retainPagintion)=>{
      if (!queryCond) {
        queryCond = this.state.queryCond;
      }
      !this.unmount&&this.setState({
        queryCond,
        pagination:this.props.pagination?{pageSize:this.props.defaultPageSize,current:retainPagintion?this.state.pagination.current:1}:undefined
      },()=>{
        this.fetchDataAjax(queryCond);
      });
    });
    this.columns = this.props.columns.map(c => {return {
      title:c.title,
      dataIndex:c.key,
      key:c.key,
      width:c.width,
      fixed:c.fixed,
      render:c.render,
      sorter:c.sorter
    }});
    
    this.state = {
      queryCond:{},
      tableData:[],
      pagination:this.props.pagination?{pageSize:this.props.defaultPageSize,current:1} : undefined,
      loading:false
    };
  }

  componentDidMount(){
    
    if(!this.props.lazyInit){// 支持懒加载
      this.fetchDataAjax(this.props.initQueryCond);
    }
  }
  handleTableChange=(pagination, filters, sorter)=>{
    if(this.props.onChange){
      this.props.onChange(pagination, filters, sorter);
    }
    this.setState({
      pagination
    },()=>{
      this.fetchDataAjax(this.state.queryCond,pagination,filters,sorter);
    });
  }
  onRowClick=(record, index)=>{
    if(this.props.onRowClick){
      this.props.onRowClick(record, index);
    }
  }
  fetchDataAjax=(queryCond,pagination,filters, sorter)=>{
    if(this.props.beforeQuery){
      try{
        queryCond = this.props.beforeQuery(queryCond);
      }catch(e){
        this.setState({loading:false});
        return;
      }
    }
    this.setState({ queryCond, loading: true });
    let ajaxMethod = util.ajaxJson;
    
    let queryCondParam = Object.assign({},queryCond,this.props.pagination?{[this.props.currentPageName]:this.state.pagination.current,[this.props.pageSizeName]:this.state.pagination.pageSize}:undefined);
    if(typeof this.props.ajax == 'string'){
      let queryParam=this.props.queryCondHandler(queryCondParam,pagination,filters,sorter);
      ajaxMethod(this.props.ajax, queryParam, (result)=>{
        if(this.props.resultHandler){
          this.props.resultHandler(result,queryParam);
        }
        let tableData = [];
        if(Array.isArray(result)){
          tableData = result;
        }else{
          for(let k in result){
            if(Array.isArray(result[k]) && k != 'logs'){
              tableData = result[k];
            }
          }
        }
        if(this.props.pagination){
          this.state.pagination.total=result[this.props.totalName];
        }
        !this.unmount&&this.setState({
          tableData,
          loading: false,
          pagination: this.props.pagination ? this.state.pagination : undefined
        });
      },err=>{
        !this.unmount&&this.setState({
          loading: false
        });
      });
    }else if(typeof this.props.ajax == 'function'){// 解决请求前需要异步请求前提数据场景
      this.props.ajax(this.props.queryCondHandler(queryCondParam,pagination,filters,sorter), (tableData,total)=>{
        if(this.props.pagination){
          this.state.pagination.total=total;
        }
        !this.unmount&&this.setState({
          tableData,
          loading: false,
          pagination: this.props.pagination ? this.state.pagination : undefined
        });
      });
    }
    
  }

  componentWillUnmount(){
    this.unmount=true;
  }
  render() {
    return (<><Table rowKey={this.props.rowKey?this.props.rowKey:record=>record.id} rowSelection={this.props.rowSelection} columns={this.columns} dataSource={this.state.tableData} pagination={this.props.pagination ? this.state.pagination : false} onChange={this.handleTableChange} onRow={this.onRowClick} loading={this.state.loading} size={this.props.size} scroll={this.props.scroll} bordered={this.props.bordered} title={this.props.title} rowClassName={this.props.rowClassName} expandedRowRender={this.props.expandedRowRender} expandIcon={this.props.expandIcon} expandRowByClick={this.props.expandRowByClick} expandedRowKeys={this.props.expandedRowKeys?this.props.expandedRowKeys():[]} />
    {this.props.showTotal&&this.state.pagination&&<span className="table-total-tips">共{this.state.pagination.total}条</span>}
    </>);
  }
}