import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox,Drawer,Collapse, Row, Popconfirm} from 'antd';
import moment from 'moment';
import './index.less';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms,coaches} from '../../../entry/index/routes/IndexPage';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('show-course-list-drawer', (rec,courseId)=>{
        util.ajaxJson(urlPrefix+'TrainOperation?opt=getCourseInfo',{courseId},res=>{
            if(res.success){
                let courseInfo=res.data;
                this.setState({visible:true,rec,courseInfo,courseId,modifyCourseTime:undefined});
            }
        });
        
    });
  	this.state={
      visible:false,
      rec:{},
      courseInfo:{
          course:{},
          records:[]
      }
  	};
  }

  deleteStudentRec=r=>{
    util.ajaxJson(urlPrefix+'TrainOperation?opt=deleteStudentRecord',{studentId:r.studentId,id:r.id},res=>{
      if(res.success){
        message.success('删除流水成功');
        this.refreshCourseInfo();
      }
    });
  }
  deleteStudentRechargeBillRec=r=>{
    Modal.confirm({
      title:'删除充值记录确认',
      content:'删除充值记录后，课程余额余次将同步扣减，且账单做同步删除，是否继续？',
      onOk:()=>{
        util.ajaxJson(urlPrefix+'TrainOperation?opt=deleteStudentRechargeBillRec',{id:r.id},res=>{
          if(res.success){
            message.success('删除充值记录成功');
            this.refreshCourseInfo();
          }
        });
      }
    });
  }
  refreshCourseInfo=()=>{
    let {courseId}=this.state;
    util.ajaxJson(urlPrefix+'TrainOperation?opt=getCourseInfo',{courseId},res=>{
        if(res.success){
            let courseInfo=res.data;
            this.setState({courseInfo});
        }
    });
    emitter.emit(`queryStudentTable`, undefined, true);
  }

  deleteCourse=async ()=>{
    let {courseId}=this.state;
    let billRes=await util.ajaxJsonAsync(urlPrefix+'BillOperation?opt=queryBill',{domain:'学员',billIdPrefix:'stuRecharge'+courseId+'-'});
    if(!billRes.success)return;
    if(billRes.data.totalItem>0){
      Modal.confirm({
        title:'同步删除账单确认？',
        content:`系统监测到您已有${billRes.data.totalItem}笔学员课程充值账单录入，是否同步删除？`,
        onOk:()=>this.doDeleteCourse(billRes.data.data.map(it=>it.id))
      });
    }else{
      this.doDeleteCourse(billRes.data.data.map(it=>it.id));
    }
  }

  doDeleteCourse=(ids)=>{
    let {courseId}=this.state;
    util.ajaxJson(urlPrefix+'TrainOperation?opt=deleteStudentCourse',{courseId,ids},res=>{
      if(res.success){
        message.success('删除课程成功');
        this.setState({visible:false});
        emitter.emit(`queryStudentTable`, undefined, true);
      }
    });
  }

  onClose=()=>{
      this.setState({visible:false});
  }

  modifyCourseTimes=()=>{
    let {courseInfo,modifyCourseTime} = this.state;
    let {course}=courseInfo;
    if(!modifyCourseTime||modifyCourseTime==course.times)return;
    if(modifyCourseTime<(course.times-course.remainTimes))return message.warn('课时数不足，无法修改！');
    let remainTimes=course.remainTimes+(modifyCourseTime-course.times);
    Modal.confirm({
      title:"修改总课时数确认",
      content:`确定要将该课程的总课时数从${course.times}调整到${modifyCourseTime}吗？调整后，剩余课时数将从${course.remainTimes}变成${remainTimes}`,
      onOk:()=>{
        util.ajaxJson(urlPrefix+'TrainOperation?opt=updateCourse',{courseId:course.id,times:modifyCourseTime,remainTimes},res=>{
          if(res.success)message.success('修改课时数成功');
          this.refreshCourseInfo();
        });
      }
    });
  }
  modifyCourseCoach=()=>{
    let {courseInfo,mainCoachId} = this.state;
    if(!mainCoachId)return message.warn('请选择教练！');
    let {course}=courseInfo;
    let coach=coaches.filter(it=>it.id==course.coachId)[0];
    if(!coach || coach.id==mainCoachId)return;
    Modal.confirm({
      title:"修改课程主教练确认",
      content:`确定要将该课程的主教练从${coach.name}调整到${coaches.filter(it=>it.id==mainCoachId)[0].name}吗？`,
      onOk:()=>{
        util.ajaxJson(urlPrefix+'TrainOperation?opt=updateCourse',{courseId:course.id,coachId:mainCoachId},res=>{
          if(res.success)message.success('修改课时数成功');
          this.refreshCourseInfo();
        });
      }
    });
  }

  render() {
    let {visible,rec,courseInfo,modifyCourseTime} = this.state;
    let {course,records,bills}=courseInfo;
    let coach=coaches.filter(it=>it.id==course.coachId)[0];
  	return (<Drawer title={`学员【${rec.name}】- 教练【${coach&&coach.name}】课程信息`} getContainer={()=>document.querySelector('.Student-container')} className="student-course-drawer" visible={visible} width={800} placement="right" onClose={this.onClose} destroyOnClose >
          <Collapse defaultActiveKey={['1','2','3']}>
            <Collapse.Panel header="课程详情" key="1">
                <Row>
                    <Form.Item label="学员名">{rec.name}</Form.Item>
                    <Form.Item label="主管教练">{coach&&coach.name}&nbsp;<Popconfirm getPopupContainer={()=>document.querySelector('.student-course-drawer .ant-drawer-body')} title={<><h3>修改主管教练</h3><Form.Item label="主管教练"><Select value={this.state.mainCoachId} onChange={v=>this.setState({mainCoachId:v})}>{coaches.map(it=><Select.Option key={it.id+'c'} value={it.id+''}>{it.name}</Select.Option>)}</Select></Form.Item></>} onConfirm={this.modifyCourseCoach}><a>修改</a></Popconfirm></Form.Item>
                </Row>
                <Row>
                    <Form.Item label="缴费金额(元)">{course.remainAmount}/{course.amount}</Form.Item>
                    <Form.Item label="课程次数">{course.remainTimes}/{course.times}&nbsp;<Popconfirm getPopupContainer={()=>document.querySelector('.student-course-drawer .ant-drawer-body')} title={<><h3>修改课程总次数</h3><Form.Item label="课程次数"><InputNumber value={modifyCourseTime?modifyCourseTime:course.times} onChange={e=>{
                      this.setState({modifyCourseTime:e});
                    }} min={0} max={1000} precision={0}/></Form.Item></>} onConfirm={this.modifyCourseTimes}><a>修改</a></Popconfirm></Form.Item>
                </Row>
                <Row>
                    <Form.Item label="课程备注">{course.remark}</Form.Item>
                    <Form.Item label="课程类型">{course.type}</Form.Item>
                </Row>
                <Row>
                    <Form.Item label="开课时间">{util.formatDate(course.gmtCreate)}</Form.Item>
                    <Form.Item label="经办人">{course.operator}</Form.Item>
                </Row>
            </Collapse.Panel>

            <Collapse.Panel header="课程培训记录" key="2">
                <Table pagination={false} rowKey={r=>r.id} dataSource={records} size="small" columns={[{key:'studyTime',dataIndex:'studyTime',title:'训练时间',render:v=>util.formatDate(v)},
          {key:'coachId',dataIndex:'coachId',title:'教练',render:(v,r)=>coaches.filter(it=>it.id==v)[0].name+(v===r.mainCoachId?'（主管教练）':'')},
          {key:'consumeTimes',dataIndex:'consumeTimes',title:'消费课时数'},
          {key:'remainTimes',dataIndex:'remainTimes',title:'消费后余课(次)',render:(v,r,idx)=>{
            let consumeTimes=0;
            for(let i=0;i<records.length;i++){
              if(idx<=i){
                consumeTimes+=records[i].consumeTimes;
              }
            }
            let snapTimes=course.times-consumeTimes;
            return `${snapTimes}/${course.times}`;
          }},
          {key:'consumeAmount',dataIndex:'consumeAmount',title:'消费金额(元)'},
          {key:'snapAmount',dataIndex:'snapAmount',title:'消费后余额(元)',render:(v,r,idx)=>{
            let consumeAmount=0;
            for(let i=0;i<records.length;i++){
              if(idx<=i){
                consumeAmount+=records[i].consumeAmount;
              }
            }
            return `${course.amount-consumeAmount}/${course.amount}`;
          }},
          {key:'operator',dataIndex:'operator',title:'经办人'},
          {key:'opt',dataIndex:'opt',title:'操作',render:(v,r)=><span><Popconfirm getPopupContainer={()=>document.querySelector('.student-course-drawer')} title="确定删除？" onConfirm={()=>this.deleteStudentRec(r)}><a>删除</a></Popconfirm></span>}]}/>
            </Collapse.Panel>

            <Collapse.Panel header="课程充值记录" key="3">
                <Table pagination={false} rowKey={r=>r.id} dataSource={bills} size="small" columns={[{key:'chargeTime',dataIndex:'chargeTime',title:'充值时间',render:v=>util.formatDate(v)},
          {key:'times',dataIndex:'times',title:'充值课时数'},
          {key:'amount',dataIndex:'amount',title:'充值金额(元)'},
          {key:'operator',dataIndex:'operator',title:'经办人'},
          {key:'opt',dataIndex:'opt',title:'操作',render:(v,r)=><span><Popconfirm getPopupContainer={()=>document.querySelector('.student-course-drawer')} title="确定删除？" onConfirm={()=>this.deleteStudentRechargeBillRec(r)}><a>删除</a></Popconfirm></span>}]}/>
            </Collapse.Panel>
          </Collapse>

          <Button type="primary" onClick={()=>emitter.emit('rechargeStudentModal',rec,courseInfo.course)}>课程充值</Button>
          {records&&records.length>0&&<Tooltip getPopupContainer={()=>document.querySelector('.student-course-drawer')} title="删除课程前需要先清空课程记录" ><Button disabled type="danger">删除课程</Button></Tooltip>}
          {!records||records.length<=0&&<Popconfirm getPopupContainer={()=>document.querySelector('.student-course-drawer')} title="确定删除课程？" onConfirm={this.deleteCourse}><Button type="danger">删除课程</Button></Popconfirm>}
          
      </Drawer>);
  }
}

export default BizComponent;