import React, {Component, PropTypes} from 'react';
import {Select,message,Table } from 'antd';
import {util,emitter} from '../../common/common';
import {prefix,urlPrefix,domain,productDomain,productPrefix} from '../../interfaces/interfaces';
import './index.less';

export default class BizComp extends Component {
    static defaultProps = {
        searchKey : 'searchText',
        columns:[{key:'id'},{key:'name'}],
        optionLabelProp:'name'
    };
  constructor(...args) {
    super(...args);
    // this.doSearch('');
    this.state={
        dataSource:[]
    };
  }

  doSearch=(v)=>{
    let {urlSuffix,searchKey,excludeValues,data}=this.props;
    if(data){
        let dataSource=data.filter(it=>{
            for(let k in it){
                if(it[k]&&(typeof it[k]=='string'||typeof it[k]=='number')&&(it[k]+'').indexOf(v)>-1){
                    return true;
                }
            }
        });
        setTimeout(()=>{
            this.setState({dataSource,searchInput:v});
        },10);
        
    }else{
        util.ajaxJson(urlPrefix+urlSuffix,{[searchKey]:v,excludeValues:v?undefined:excludeValues,noPaging:true},res=>{
            if(res.success){
                let dataSource;
                if(Array.isArray(res.data)){
                    dataSource=res.data;
                }else{
                    dataSource=res.data.data;
                }
                this.setState({dataSource,searchInput:v});
            }
        });
    }
    
  }
  hightLight=v=>{
    if(v){
        if(v.type&&v.props)return v;
        let value=this.state.searchInput+'',colValue=(v+'');
        let idx=colValue.indexOf(value);
        if(idx>-1){
            return <span>{colValue.substring(0,idx)}<span className="hight-light">{value}</span>{colValue.substring(idx+value.length)}</span>;
        }
    }
    return v;
}

  render(){
    let {columns,width,onChange,value,valueKey,nameKey,onSelected,excludeValues,allowClear,optionLabelProp,initRecords}=this.props;
    let {open,dataSource,searchInput}=this.state;
    if(!searchInput&&excludeValues&&excludeValues.length>0){// 过滤已选择的
        dataSource=dataSource.filter(it=>excludeValues.indexOf(it[valueKey]+'')==-1);
    }
    if(initRecords&&!searchInput&&dataSource.length==0&&initRecords.length>0){// 兜底展示防空
        dataSource=initRecords;
    }
    return (<Select ref={el=>this.el=el} optionLabelProp={optionLabelProp} filterOption={false} className="selecttable-select" dropdownClassName="selecttable-dropdown" firstActiveValue={dataSource.length>0?dataSource[0][valueKey]+'':undefined} {...this.props} value={value} showSearch style={{width:width?width:'100%'}} open={open} dropdownMatchSelectWidth={false} onDropdownVisibleChange={open=>{
            if(open&&!value){
                this.doSearch('');
            }
            this.setState({open});
        }} onChange={e=>{
            let goOn=true;
            if(onSelected){
                goOn=onSelected(e,dataSource.filter(it=>it[valueKey]==e)[0]);
            }
            if(goOn)onChange(e);
        }} onSearch={v=>{
            this.doSearch(v);
        }} allowClear={allowClear}>
          <Select.Option disabled key="-1"><table className="selectTable" style={{width:'100%'}}><colgroup>{columns.filter(c=>!c.hidden).map((c,idx)=><col key={'col'+idx} style={{width:c.width,minWidth:c.width}}></col>)}</colgroup><thead className="ant-table-thead"><tr>{columns.filter(c=>!c.hidden).map(c=><th className="ant-table-row-cell-break-word" key={c.key}><div>{c.title}</div></th>)}</tr></thead></table></Select.Option>
          {dataSource.map((it,idx)=>{
            let hasSelected=false;
            if(excludeValues&&excludeValues.indexOf(it[valueKey]+'')>-1)hasSelected=true;// 已选择过的重复的，样式变一下
            return <Select.Option key={'so'+idx} value={it[valueKey]+''} name={it[nameKey]} {...{[optionLabelProp]:it[optionLabelProp]}}>
                <table className={"selectTable" + (hasSelected?' selectedTable':'')} style={{width:'100%'}}><colgroup>{columns.filter(c=>!c.hidden).map((c,idx)=><col key={'col'+idx} style={{width:c.width,minWidth:c.width}}></col>)}</colgroup><tbody className="ant-table-tbody"><tr>{columns.filter(c=>!c.hidden).map((c,idx)=><td className="ant-table-row-cell-break-word" key={c.key}>{this.hightLight(c.render?c.render(it[c.key],it,idx):it[c.key])}</td>)}</tr></tbody></table>
            </Select.Option>;
          })}
        </Select>);
  }
}