import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { Form, Input, Row, Col, Button, DatePicker,Select,Icon } from 'antd';
import Field from '../form/field';
import {emitter,util} from '../../../common/common.js';
const FormItem = Form.Item;
class QueryCond extends Component {
  constructor(...args) {
    super(...args);
    emitter.on(`set${this.props.config.key}QueryCondForm`,queryCond=>{
      this.setState({formValues:queryCond});
    });
    emitter.on(`get${this.props.config.key}QueryCondForm`,(cb)=>{
      cb(this.getQueryParam());
    });
    emitter.on(`on${this.props.config.key}QueryBtnClick`,(retainPage)=>{
      this.onQueryCondQuery(undefined,retainPage);
    });
    this.state={
      formValues:{}
    };
  }
  getQueryParam=()=>{
    return this.beforeQuery(this.state.formValues);
  }
  onQueryCondQuery=(e,retainPage)=>{
    if(!e || e.key == 'Enter' || e.type=='click'){
      emitter.emit(`query${this.props.config.key}Table`, this.beforeQuery(this.state.formValues), retainPage);
    }
  }
  beforeQuery=(queryParam)=>{
    if(queryParam){
      for(let k in queryParam){
        if(queryParam[k] && typeof queryParam[k] == 'string'){
          queryParam[k] = queryParam[k].trim();
        }
        if(queryParam[k] === ''){
          delete queryParam[k];
        }
      }
    }
    queryParam=util._convertFormValues(queryParam);
    return queryParam;
  }
  getExtButtons=()=>{
    let {extButtons}=this.props.config.queryCond;
    if(extButtons){
      if(typeof extButtons == 'function'){
        return extButtons();
      }else if(Array.isArray(extButtons)){
        return extButtons;
      }
    }
  }
  render(){
    if(!this.props.config.queryCond || !this.props.config.queryCond.conditions || this.props.config.queryCond.conditions.length <= 0){
      return <div></div>;
    }
    return (
    <Form className="ant-advanced-search-form queryCond">
      {this.props.config.queryCond.conditions.map((conditions,idx1)=>{
        return (<Row key={idx1}>
          {conditions.map((condition,idx2)=>{
            if(!condition){
              return;
            }
            let curConditon;
            this.props.config.fields.forEach(field=>{
              if(field.key == condition.key){
                curConditon = Object.assign({},field);
              }
            });
            if(!curConditon){// fields中未找到匹配，则用自己
              curConditon = condition;
            }else if(condition.type == 'custom'){
              curConditon.label&&(condition.label=curConditon.label);
              curConditon = condition;
            }
            condition.label&&(curConditon.label=condition.label);
            // 是否展示
            if(curConditon.shouldDisplay){
              if(!curConditon.shouldDisplay(this)){
                return <Col key={idx2} sm={6}></Col>;
              }
            }
            return (<Col key={idx2} sm={6}><span className="label">{curConditon.label}：</span>
            <Field {...util.getField.call(this,curConditon.key,{...curConditon.options, onChange:v=>{
              if(curConditon.onChange){
                curConditon.onChange(v,this.state.formValues);// 把当前queryForm值给到conditions
              }
            }})} field={curConditon} context={this} onKeyDown={this.onQueryCondQuery} placeholder={curConditon.placeholder} size={this.props.config.queryCond.size}/>
          </Col>);
          })}
        </Row>);
      })}
      {!this.props.config.queryCond.hideButtons&&<Row>
        <Col sm={12}>
            <Button type="primary" className="search-button" onClick={this.onQueryCondQuery} size={this.props.config.queryCond.size}><Icon type="search" />搜索</Button>
            <Button type="ghost" className="search-button" onClick={()=>{
              this.setState({formValues:{}},()=>{
                this.onQueryCondQuery(undefined,true);
              });
              
            }} size={this.props.config.queryCond.size}><Icon type="close-square" />清除</Button>
            {this.getExtButtons()}
        </Col>
      </Row>}
    </Form>);
  }
}
QueryCond = Form.create({
})(QueryCond);
export default QueryCond;
