import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox,Drawer, Row, Popconfirm} from 'antd';
import moment from 'moment';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';
import MoneyField from '../../common/fields/money';
import SelectTable from '../../../components/select/selectTable';
import {projects} from './index';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('gameModal', (rec,opt)=>{
        let formValues={project:'乒乓',ballMates:[{},{},{}]};
        if(opt=='modify'){
          formValues=rec;
          formValues.time=[moment(rec.startTime),moment(rec.endTime)];
          rec.ballMates.forEach(it=>it.id=it.id+'');
        }
        this.setState({visible:true,formValues,opt});
      
    });
  	this.state={
      formValues:{
      },
      visible:false
  	};
  }

  handleOk=(e,finish)=>{
    let {opt}=this.state;
    util.validateFields.call(this,async values=>{
      let {ballMates}=values;
      let rec={...values};
      rec.startTime=util.formatDate(rec.time[0]);
      rec.endTime=util.formatDate(rec.time[1]);
      delete rec.ballMates;
      delete rec.time;
      ballMates=ballMates.filter(it=>it.id);
      if(ballMates.length<1){
        return message.warning('至少有一个球友');
      }
      let res=await util.ajaxJsonAsync(urlPrefix+'GameOperation?opt='+(opt=='add'?'createGame':'updateGame'),{rec,ballMates,finish});
      if(res.success){
        message.success((finish?'完结':'保存')+'成功');
        this.handleCancel();
      }
    });
  }
  handleCancel=()=>{
    emitter.emit(`queryGameTable`, undefined, true);
    this.setState({visible:false});
  }

  onMateRowClick=(add,i)=>{
    let {formValues}=this.state;
    let {ballMates}=formValues;
    if(add){
      ballMates.splice(i+1,0,{});
    }else{
      if(ballMates.length==1)return message.warn('至少保留一行');
      ballMates.splice(i,1);
    }
    this.setState({formValues});
  }
  onBallMateChange=(v,r,idx)=>{
    let {formValues}=this.state;
    let {ballMates}=formValues;
    ballMates[idx].phone=r?r.phone:undefined;
    this.setState({formValues});
    return true;
  }
  getSelectedMates=(idx)=>{
    let {formValues}=this.state;
    let {ballMates}=formValues;
    let selectedMates=[];
    for(let i=0;i<ballMates.length;i++){
      if(i!=idx&&ballMates[i].id){
        selectedMates.push(ballMates[i].id+'');
      }
    }
    return selectedMates;
  }

  render() {
    let {visible,formValues,opt} = this.state;
  	return (<Drawer title={`${opt=='add'?'新建':'修改'}比赛`} className="modal-form" visible={visible} width={1200} destroyOnClose onClose={this.handleCancel} placement="right" maskClosable={false} getContainer={false}>
          <Form className="add-member-modal">
            <Row>
              <Form.Item className="memberName-f" label="比赛名称" required>
                  <Input {...util.getField.call(this,'name',{rules:[{required:true,message:'必填'}]})} />
              </Form.Item>
              <Form.Item className="" label="比赛时间" required>
                  <DatePicker.RangePicker showTime={{defaultValue:[moment().hour(10).startOf('hour'),moment().hour(16).startOf('hour')]}} disabledDate={current=>current && current < moment().endOf('day')} {...util.getField.call(this,'time',{rules:[{required:true,message:'必填'}]})} />
              </Form.Item>
            </Row>
            <Row>
              <Form.Item className="" label="比赛项目">
                  <Select {...util.getField.call(this,'project',{})}>
                    {projects.map(it=><Select.Option key={it}>{it}</Select.Option>)}
                  </Select>
              </Form.Item>
              <Form.Item className="" label="比赛规则">
                  <Input {...util.getField.call(this,'rule',{rules:[]})} />
              </Form.Item>
            </Row>
            <Form.Item className="" label="比赛描述" required>
                <Input {...util.getField.call(this,'gameDesc',{rules:[{required:true,message:'必填'}]})} />
            </Form.Item>
            <Row>
              <Form.Item className="amount-f" label="投入成本" required>
                <MoneyField form={this} name="cost" hideLabel/>
              </Form.Item>
              <Form.Item className="" label="备注">
                  <Input {...util.getField.call(this,'remark',{})} />
              </Form.Item>
            </Row>
            <Form.Item className="" label="参赛选手">
                <Table className="form-table" columns={[
                    {key:'opt',title:<Icon type="usergroup-delete" />,width:'64px',render:(v,r,i)=>
                      <div className="table-opt-col"><span className="idx">{i+1}</span><span className="btns show-off-i"><Button icon="plus" size="small" shape="round" onClick={()=>this.onMateRowClick(true,i)}></Button><Button icon="minus" size="small" shape="round" onClick={()=>this.onMateRowClick(false,i)}></Button></span></div>},
                    {key:'id',dataIndex:'id',title:'姓名',width:'30%',render:(v,r,idx)=>
                        <SelectTable {...util.getSubField.call(this,`ballMates[${idx}]`,'id',formValues.ballMates[idx])} onSelected={(v,r)=>this.onBallMateChange(v,r,idx)} className={v?'':"show-off-i"} placeholder="请输入球友名或手机号查找" valueKey="id" nameKey="name" columns={[
                          {key:'name',title:'名称',width:'120px'},
                          {key:'phone',title:'手机',width:'120px'},
                          {key:'score',title:'当前积分',width:'80px'}]} excludeValues={this.getSelectedMates(idx)} urlSuffix="GameOperation?opt=queryBallMate" searchKey="namePhone" allowClear={false}/>},
                    {key:'phone',dataIndex:'phone',title:'手机',width:'30%'},
                    {key:'getScore',dataIndex:'getScore',title:'获得积分',render:(v,r,idx)=><span className={v?'':"show-off-i"}><InputNumber placeholder="本次比赛的积分" precision={0} {...util.getSubField.call(this,`ballMates[${idx}]`,'getScore',r)}/></span>}]}
                rowKey={(r,id)=>id} size="small" dataSource={formValues.ballMates}/>
            </Form.Item>
          </Form>
          <Button key="1" type="primary" onClick={this.handleOk}>
            保存
          </Button>
          {opt=='modify'&&<Popconfirm title="完结后积分将更新到球友身上，且无法再做修改，确认无误？" onConfirm={(e)=>this.handleOk(e,true)}><Button type="primary">
            完结比赛
          </Button></Popconfirm>}
          <Button key="2" type="ghost" onClick={this.handleCancel}>
            取消
          </Button>
      </Drawer>);
  }
}

export default BizComponent;