import React, { Component } from 'react';
import {message,Input,Modal,Button,Form,Select,Collapse,Popconfirm,Tooltip,Popover,DatePicker,InputNumber,Tag } from 'antd';
import { emitter, util } from '../../../common/common';
export default class BizComponent extends Component {
    constructor(...args) {
        super(...args);
    }
    render() {
        let {name,form,required}=this.props;
        return <>
            <Input maxLength={11} {...util.getField.call(form,name?name:'phone',{rules:[{required:required===undefined?true:required,message:'必填'},{validator:v=>{
                if(!v)return true;
                return /^1[0-9]{10}$/.test(v);
              },message:'必须为1开头的11位手机号'}]})} />
        </>;
    }
}