import React, { Component } from 'react';
import { pca, pcaa } from 'area-data';
import {message,Input,Modal,Button,Form,Select,Collapse,Popconfirm,Tooltip,Popover,DatePicker,InputNumber,Tag } from 'antd';
import { emitter, util } from '../../../common/common';
// 地点改名
for(let provK in pca['86']){
    let provName=pca['86'][provK];
    if(provName.indexOf('市')>-1){
        pca['86'][provK]=provName.replace('市','');
        for(let cityK in pcaa[provK]){
            let cityName=pcaa[provK][cityK];
            if(cityName=='市辖区')pcaa[provK][cityK]=provName;
        }
    }
}
export default class BizComponent extends Component {
    constructor(...args) {
        super(...args);
        this.state = {
        };
    }

    render() {
        let {context,useName}=this.props;
        let {formValues}=context.state;
        if(!formValues)return null;
        let provs=pca['86'],citys={},countys={};
        let {prov,city,county}=formValues;
        if(prov){
            citys=pcaa[prov];
        }
        if(city){
            countys=pcaa[city];
        }
        return <div className="area-c" ref={el=>{
            this.areaUi = el;
        }}>
            <Select {...util.getField.call(context,'prov',{onChange:e=>{
                if(!e){
                    context.state.formValues.city=undefined;
                    context.state.formValues.county=undefined;
                    context.setState({});
                }
                this.props.onChange(e);
            }})} showSearch optionFilterProp="en" placeholder="省" allowClear size="small">{Object.keys(provs).map((it,idx)=><Select.Option key={'prov'+idx} value={it}>{provs[it]}</Select.Option>)}</Select>
            <Select {...util.getField.call(context,'city',{onChange:e=>{
                if(!e){
                    context.state.formValues.county=undefined;
                    context.setState({});
                }
                this.props.onChange(e);
            }})} showSearch optionFilterProp="children" placeholder="市" allowClear size="small">{Object.keys(citys).map((it,idx)=><Select.Option key={'city'+idx} value={it}>{citys[it]}</Select.Option>)}</Select>
            <Select {...util.getField.call(context,'county',{onChange:e=>{
                this.props.onChange(e);
            }})} showSearch optionFilterProp="children" placeholder="区" allowClear size="small">{Object.keys(countys).map((it,idx)=><Select.Option key={'county'+idx} value={it}>{countys[it]}</Select.Option>)}</Select>
        </div>;
    }
}
export let getDisplayText=(prov,city,county)=>{
    return pca['86'][prov]+(prov&&city?'-'+pcaa[prov][city]:'')+(prov&&city&&county?'-'+pcaa[city][county]:'');
}