     tuomin:function(data,count=1){
        if(data){
            data=data+'';
            if(this.isChinese(data)&&data.length > 1&&data.length<5){
                return data[0] + '*'.repeat(data.length-1);
            }
            if(data.length==3){
                return data[0]+'*'+data[2];
            }
            if(data.length<=count+1){
                return data[0]+'*';
            }
            let dotStr='.'.repeat(count);
            if(new RegExp(`^${dotStr}(.*)${dotStr}`,'g').test(data)){
                return data.replace(RegExp.$1, '*'.repeat(RegExp.$1.length));
            }
        }else{
            return data;
        }
     },
     isChinese:function(s){
        let patrn= /[\u4E00-\u9FA5]|[\uFE30-\uFFA0]/gi;
        return patrn.exec(s);
     },
     convertToChinaNum:function(num) {
        let arr1 = new Array('零', '一', '二', '三', '四', '五', '六', '七', '八', '九');
        let arr2 = new Array('', '十', '百', '千', '万', '十', '百', '千', '亿', '十', '百', '千','万', '十', '百', '千','亿');//可继续追加更高位转换值
        if(!num || isNaN(num)){
            return "零";
        }
        if((num+'').indexOf('.')==num.length-1)return '';
        let english = num.toString().split("")
        let result = "";
        for (let i = 0; i < english.length; i++) {
            let des_i = english.length - 1 - i;//倒序排列设值
            result = arr2[i] + result;
            let arr1_index = english[des_i];
            result = arr1[arr1_index] + result;
        }
        //将【零千、零百】换成【零】 【十零】换成【十】
        result = result.replace(/零(千|百|十)/g, '零').replace(/十零/g, '十');
        //合并中间多个零为一个零
        result = result.replace(/零+/g, '零');
        //将【零亿】换成【亿】【零万】换成【万】
        result = result.replace(/零亿/g, '亿').replace(/零万/g, '万');
        //将【亿万】换成【亿】
        result = result.replace(/亿万/g, '亿');
        //移除末尾的零
        result = result.replace(/零+$/, '')
        //将【零一十】换成【零十】
        //result = result.replace(/零一十/g, '零十');//貌似正规读法是零一十
        //将【一十】换成【十】
        result = result.replace(/^一十/g, '十');
        return result;
     },
