 function initXMLhttp() {
    let xmlhttp;
    if (window.XMLHttpRequest) {
        //code for IE7,firefox chrome and above
        xmlhttp = new XMLHttpRequest();
    } else if(window.ActiveXObject){
        //code for Internet Explorer
        xmlhttp = new window.ActiveXObject("Microsoft.XMLHTTP");
    }
    return xmlhttp;
}

function sign(str=''){
    return md5(str);
}

export function minAjax(config) {
    if (!config.url) {
        return;
    }
    if (!config.type) {
        config.type = 'GET';
    }
    if (!config.debugLog) {
        config.debugLog = false;
    }

    let xmlhttp = initXMLhttp();
    if ( config.xhrFields ) {
        for (let i in config.xhrFields ) {
            xmlhttp[ i ] = config.xhrFields[ i ];
        }
    }
    if (!config.contentType) {
        config.contentType = "application/x-www-form-urlencoded";
    }
    if (!config.dataType) {
        config.dataType = 'json';
    }
    
    xmlhttp.onreadystatechange = () => {
        if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
            if (config.success) {
                let response = xmlhttp.responseText;
                if(response&&config.dataType == 'json'){
                    response = JSON.parse(response);
                    // response._account=decodeURIComponent(xmlhttp.getResponseHeader('_account'));
                }
                config.success(response,xmlhttp);
            }
        }
        if(xmlhttp.status == 333){
            emitter.emit('goto-login',{config,body:xmlhttp.responseText});
            return;
        }
        if(xmlhttp.status == 403){// 自定义代码块
            return message.error("无权限!");
        }
        if(xmlhttp.status == 431){
            return message.error("服务查询失败!");
        }
        if(xmlhttp.status == 432){
            return message.error("服务不存在!");
        }
        if (xmlhttp.readyState == 4 && xmlhttp.status > 200) {
            let errorInfo = xmlhttp.responseText;
            let rspContentType = xmlhttp.getResponseHeader('Content-Type');
            if(rspContentType&&rspContentType.indexOf('json') > -1){
                let rsp = JSON.parse(xmlhttp.responseText);
                if(rsp.errors&&rsp.errors){
                    errorInfo = JSON.stringify(rsp.errors);
                }
            }
            message.info(errorInfo, 3);

            if (config.error) {
                let response = xmlhttp.responseText;
                config.error(response);
            }

            return;
        }
        if (xmlhttp.readyState == 4 && xmlhttp.status == 0) {
            message.error(`网络错误，请确认服务器地址 '${config.url}' 可用!`);
            if (config.error) {
                let response = xmlhttp.responseText;
                config.error(response);
            }
        }
    };
    let sendString = [],
        sendData = config.data;
    if( typeof sendData === "string" ){
        if(config.contentType == "application/x-www-form-urlencoded"){
            let tmpArr = String.prototype.split.call(sendData,'&');
            for(let i = 0, j = tmpArr.length; i < j; i++){
                let datum = tmpArr[i].split('=');
                sendString.push(datum[0] + "=" + encodeURIComponent(datum[1]));
            }
        } else {
            sendString.push(sendData);
        }
    }else if( typeof sendData === 'object' && !( sendData instanceof String || (FormData && sendData instanceof FormData) ) ){
        if(config.contentType == "application/x-www-form-urlencoded"){
            for (let k in sendData) {
                let datum = sendData[k];
                if(datum && typeof datum === 'string'){
                    datum = datum.trim();
                }
                if(datum || 0===datum){
                    if( Object.prototype.toString.call(datum) == "[object Array]" ){
                        for(let i = 0, j = datum.length; i < j; i++) {
                                sendString.push(k + "=" + encodeURIComponent(datum[i]));
                        }
                    }else{
                        sendString.push(k + "=" + encodeURIComponent(datum));
                    }
                }
            }
        }
    }
    sendString = sendString.join('&');
    let async = true;
    if ('async' in config) {
        async = config.async;
    }
    if(!window.isProd()&&config.url.indexOf('logFlowLogs=true')==-1){
        if(config.url.indexOf('?') > -1){
            config.url=config.url+'&logFlowLogs=true';
        }else{
            config.url=config.url+'?logFlowLogs=true';
        }
    }
    if (config.type == "GET") {
        if(config.url.indexOf('?') > -1){
            xmlhttp.open("GET", config.url + sendString, async);
        }else{
            xmlhttp.open("GET", config.url + "?" + sendString, async);
        }
        if(config.beforeSend){
            config.beforeSend(xmlhttp);
        }
        xmlhttp.send();
    }
    if (config.type == "POST") {
        xmlhttp.open("POST", config.url, async);
        xmlhttp.setRequestHeader("Content-type", config.contentType);
        let t=new Date().getTime();
        xmlhttp.setRequestHeader("Accept-Language", sign(sendString+t)+t);
        if(config.beforeSend){
            config.beforeSend(xmlhttp,sendString);
        }
        xmlhttp.send(sendString);
    }
}

export {util, emitter}