import React,{Component} from 'react';
import copy from 'copy-to-clipboard';
import {emitter,util} from '../../../common/common';
import {urlPrefix,domain,productDomain,isProductUrl} from '../../../interfaces/interfaces';
import Icon from '../../../components/icon';
import {message,Popover,Tooltip,Input,Button,Tabs, Popconfirm} from 'antd';
import {routeTabs} from './routeTabs';
import LoginModal from './modals/loginModal';
import Hotline from './modals/hotline';
import Menu from './Menu';
import RichEditorDrawer from './modals/richEditorDrawer';
import {websocket} from '../../../common/ws';
import './index.less';
// window.onbeforeunload=function(e){     
//     e = window.event||e;  
//     e.returnValue=("确定离开当前页面吗？");
//     return e.returnValue;
// }

export let forbiColumns=[];
export let filterColumns=(cols)=>{
    let filterCols= cols.filter(it=>{
        return forbiColumns.indexOf(it.key)==-1;
    });
    return filterCols;
}
export let tenant={},tenantConfig={},rooms,role,allRoles,allStaffs,menus,urls,coaches;
export let tenantId=1,name,phone;
export let accounts;
export let isAdmin;

const TabPane = Tabs.TabPane;
export default class IndexPage extends Component {
    constructor(...args) {
        super(...args);

        // 模拟route
        window.addEventListener('hashchange',()=>{
            if(window._menuClickProtect){
                delete window._menuClickProtect;
                return;
            }
            this.handleHash();
        });// end 模拟route

        emitter.on('show-login-user',user=>{
            this.setState({userName:user.name});
        });
        emitter.on('init-user-info',user=>{
            this.initUserInfo();
        });
        emitter.on('main-clickmenu', ({key,props})=>{
            this.indexMenu.changeCurrentMenu(key);
            this.onMenuClick(key,props,this.indexMenu.getTitleByKey(key));
        });
        emitter.on('main-closetab', ({key})=>{
            this.onTabEdit(key,'remove');
        });
        emitter.on('antdmessage-success',(args)=>{
            message.success(args[0],args[1]);
        });
        emitter.on('antdmessage-warn',(args)=>{
            message.warn(args[0],args[1]);
        });
        emitter.on('antdmessage-error',(args)=>{
            message.error(args[0],args[1]);
        });
        this.initUserInfo();
        this.state={
            panes:[],
            init:false,
            userName:'游客'
        };
    }
    componentDidMount(){
        
    }

    handleHash=()=>{
        let {hash}=window.location;
        if(hash&&hash.length>1){
            let hashKey=hash.substring(1);
            let qIdx=hashKey.indexOf('?');
            let props=undefined;
            if(qIdx>-1){
                props={};
                let queryStr=hashKey.substring(qIdx+1);
                let queryPairs=queryStr.split('&');
                for(let queryPair of queryPairs){
                    let entryPair=queryPair.split('=');
                    props[entryPair[0]]=entryPair[1];
                }
                hashKey=hashKey.substring(0,qIdx);
            }
            let title=this.indexMenu.getTitleByKey(hashKey);

            if(title){// 菜单
                this.indexMenu.changeCurrentMenu(hashKey);
            }else if(routeTabs[hashKey]){// 自定义的tab
                title=routeTabs[hashKey].title;
                this.indexMenu.changeCurrentMenu();
            }

            this.onMenuClick(hashKey,props,title);
        }

    }

    initUserInfo=()=>{
        util.ajaxJson(urlPrefix+'SysUserFramework?opt=getUserInfo',{},result=>{
            if(result.data){
                tenant=result.data.tenant;
                name=result.data.name;
                phone=result.data.phone;
                let tconfig=JSON.parse(tenant.config);
                if(!tconfig)tconfig={};// 租户默认配置
                if(!tconfig.majorCoachRate)tconfig.majorCoachRate=55;
                if(!tconfig.coachRate)tconfig.coachRate=50;
                tenantConfig=tconfig;
                rooms=result.data.rooms||[];
                role=result.data.role;
                allRoles=result.data.allRoles;
                allStaffs=result.data.allStaffs;
                menus=result.data.menus;
                urls=result.data.urls;
                coaches=result.data.coaches;
                tenantId=result.data.tenantId;
                isAdmin=role.indexOf(',-1,')>-1;
                emitter.emit('show-login-user',{name:result.data.name});
                emitter.emit('init-websocket');
                this.setState({init:true},()=>{// 初始化检索条件
                    setTimeout(()=>{
                        emitter.emit('do-indicates-search',{});
                    },500);
                    setTimeout(this.handleHash,10);
                });
            }else{
                message.error('请确认服务是否可用！');
            }
        });
    }

    onMenuClick=(key, props, title)=>{
        let comp = require(`../../../biz/${key}`);
        if(comp&&comp.default){
            let {panes,activeKey} = this.state;
            activeKey = key;
            let fps = panes.filter(p=>p.key == key);
            if(fps.length==0){
                panes.push({key,title,content:comp.default,props});
                this.setState({activeKey,panes},()=>{
                    emitter.emit(`menu-clicked-${key}`);
                });
            }else{
                // fps[0].content = undefined;
                // this.setState({activeKey,panes},()=>{
                //     fps[0].content = comp.default;
                //     this.setState({panes},()=>{
                //         emitter.emit(`menu-clicked-${key}`);
                //     });
                // });
                fps[0].content = comp.default;
                this.setState({activeKey},()=>{
                    emitter.emit(`menu-clicked-${key}`);
                });
            }
        }
    }

    onTabChange = (activeKey) => {
        this.setState({ activeKey },()=>{
            window._menuClickProtect=true;//打保护防止重复render
            window.location.hash=activeKey;
            setTimeout(()=>delete window._menuClickProtect,10);
            this.indexMenu.changeCurrentMenu(activeKey);
        });
    }

    onTabEdit = (targetKey, action) => {
        if(action == 'remove'){
            let {panes,activeKey} = this.state;
            panes = panes.filter(p=>p.key!=targetKey);
            if(panes.length > 0){
                if (activeKey == targetKey) {// 删除的是当前tab
                    activeKey = panes[0].key;
                }
            }
            window._menuClickProtect=true;//打保护防止重复render
            window.location.hash=activeKey;
            setTimeout(()=>delete window._menuClickProtect,10);
            this.indexMenu.changeCurrentMenu(activeKey);
            this.setState({panes,activeKey});
        }
    }

    render(){
        let {panes,activeKey,init} = this.state;
        if(!init)return <LoginModal />;
        return (
            <div className="fc">
                <div className="fc-main">
                  <Menu onClick={this.onMenuClick} ref={el=>this.indexMenu=el}/>
                  <div className="fc-container">
                    <div className="fc-header"></div>
                    <Tabs hideAdd
                      onChange={this.onTabChange}
                      activeKey={activeKey}
                      type={'editable-card'}
                      onEdit={this.onTabEdit}
                    >
                      {
                        panes.map(pane => {
                            let Comp = pane.content;
                            return <TabPane tab={pane.title} key={pane.key}>{Comp && <Comp {...pane.props}/>}</TabPane>;
                        })
                      }
                    </Tabs>
                    <div className="login-sec">欢迎，{this.state.userName} &nbsp;&nbsp; {tenant.name} <Popconfirm placement="leftTop" title="确认退出？" onConfirm={async ()=>{
                        await util.ajaxJsonAsync(urlPrefix+'loginout');
                        window.location.reload();
                    }}><a className="logout-a">退出</a></Popconfirm></div>
                  </div>
                </div>
                <LoginModal />
                <RichEditorDrawer />
		    </div>
	    );
    }
  
}

