import React, {Component} from 'react';
import {message,Button,Icon} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util, emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix, urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles, isAdmin, allStaffs} from '../../../entry/index/routes/IndexPage';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);

    this.config = {
      title: '商品管理',
      key: 'ItemMgr',
      dialog: {
        width: 1060
      },
      queryCond: {
        conditions: [
          [
            {
              key: 'name'
            }
          ]
        ],
        beforeQuery: queryParam => {
          queryParam = queryParam || {};
          this.queryParam = queryParam;
          return queryParam;
        },
        extButtons:()=>{
          let extBtns=[<Button key="1" type="primary" size="small" className="search-button" onClick={()=>window.location.hash='jxc/item/itemAdd'}><Icon type="plus" />新建商品</Button>];
          return extBtns.filter(it=>!!it);
        },
        size: 'small'
      },
      fields: [
        {
          key: 'id',
          rowKey: true,
          notInForm: true,
          notInTable: true,
          label: 'id'
        }, {
          key: 'name',
          width: '15%',
          rules: [
            {
              required: true,
              message: '必填'
            }
          ],
          label: '名称'
        }, {
          key: 'phone',
          width: '10%',
          props: {
            maxLength: 11
          },
          rules: [
            {
              required: false,
              message: '选填'
            },
            // {   validator: v => {     return v && /^1[0-9]{10}$/.test(v);   },   message:
            // '必须为1开头的11位手机号' }
          ],
          label: '手机号'
        }, {
          key: 'address',
          width: '20%',
          rules: [
            {
              required: true,
              message: 'false'
            }
          ],
          // type:'custom', getComponent:(props,context)=>{   return <Area useName
          // context={context} {...props}></Area>; },
          label: '区域'
        }, {
          key: 'score',
          width: '10%',
          label: '积分'
        }, {
          key: 'maxScore',
          width: '10%',
          label: '最高积分'
        }, {
          key: 'gmtModified',
          notInForm: true,
          width: '10%',
          render: (v, r) => util.formatDate(v),
          label: '修改时间'
        }, {
          key: 'gmtCreate',
          notInForm: true,
          width: '10%',
          render: (v, r) => util.formatDate(v),
          label: '创建时间'
        }
      ],
      // operationAddon:record=>{   return (<span><a onClick={()=>{ }}>设计</a></span>);
      // },
      form: {
        col: 1,
        beforeAdd: formData => {
          return {};
        },
        beforeModify: formData => {
          return formData;
        },
        beforeSubmit: formData => {
          // if (formData.address) {   formData.address = getDisplayText(formData.prov,
          // formData.city, formData.county); }
          return formData;
        }
      },
      table: {
        bordered: true,
        size: 'small',
        resultHandler: (result, queryParam) => {
          result.list = result.data.data;
          result
            .list
            .forEach(it => {});
          result.totalItem = result.data.totalItem;
        }
      },
      api: {
        queryInterface: {func: urlPrefix + 'ItemManager?opt=queryItem'}
      }
    };
    this.state = {};
  }

  render() {
    return (
      <div>
        <BizComp config={this.config}/>
      </div>
    );
  }
}