import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox, Popconfirm} from 'antd';
import moment from 'moment';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';
import MyUpload from '../../common/fields/upload';
import MoneyField from '../../common/fields/money';
import PhoneField from '../../common/fields/phone';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('modifyMemberModal', (rec)=>{
        let formValues={...rec};
        formValues.applyTime=moment(rec.applyTime);
        formValues.roomId=rec.roomId+'';
        delete formValues.recordCount;
        delete formValues.amount;
        this.setState({visible:true,formValues,rec});
      
    });
  	this.state={
      formValues:{},
      visible:false
  	};
  }

  handleOk=()=>{
    util.validateFields.call(this,values=>{
      if(values.billType=='按次'&&!values.remainTimes){
        document.querySelector('.form-custom-id-remainTimes .ant-input-number-input').focus();
        return message.info('按次计费必须填写会员次数');
      }
      if(!values.phone)return message.info('正式会员手机号必填');
      util.ajaxJson(urlPrefix+'MemberOperation?opt=updateMember', {rec:values}, res=>{
        if(res.success){
          message.success(`修改成功!`);
          this.handleCancel();
        }
      });
    });
  }

  handleCancel=()=>{
    emitter.emit(`queryMemberTable`, undefined, true);
    this.setState({visible:false});
  }

  render() {
    let {visible,formValues} = this.state;
  	return (<Modal title={`会员修改`} className="modal-form" visible={visible} width={1200} destroyOnClose onCancel={this.handleCancel} maskClosable={false} footer = {
        [<Popconfirm key="1" title="确认修改？对应的消费记录也将同步刷新" onConfirm={this.handleOk}>{!this.state.loading&&<Button type="primary" size="large">
            修改
         </Button>}</Popconfirm>,
         <Button key="2" type="ghost" size="large" onClick={this.handleCancel}>
            取消
         </Button>]
      }>
          <Form className="add-member-modal">
            <Form.Item className="memberName-f" label="会员名" required>
                <Input {...util.getField.call(this,'name',{rules:[{required:true,message:'必填'}]})} />
            </Form.Item>
            <Form.Item className="" label="手机号" required={formValues.isVip}>
                <PhoneField form={this} />
            </Form.Item>
            <Form.Item className="" label="消费类型" required>
              <Select disabled {...util.getField.bind(this)('billType',{rules:[{required:true,message:'必填'}]})}>
                {[{key:'按次',text:'按次'},{key:'包月',text:'包月'},{key:'包半年',text:'包半年'},{key:'包年',text:'包年'}].map(it=>{
                  return <Select.Option key={it.key} value={it.key}>{it.text}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            {formValues.billType=='按次'&&<Form.Item className="" label="会员次数" required>
                <InputNumber {...util.getField.call(this,'remainTimes')} min={1} precision={0}/>
            </Form.Item>}
            <Form.Item className="" label="开始时间" required>
                <DatePicker {...util.getField.call(this,'applyTime')} />
            </Form.Item>
            <Form.Item className="" label="照片">
              <MyUpload message="会员" {...util.getField.call(this,'avatar')} onLoading={loading=>this.setState({loading})}/>
            </Form.Item>
            <Form.Item className="" label="球馆" required>
              <Select {...util.getField.bind(this)('roomId',{})}>
                {rooms.map(it=>{
                  return <Select.Option key={it.id} value={it.id+''}>{it.name}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            
          </Form>
      </Modal>);
  }
}

export default BizComponent;