import React, {Component, PropTypes} from 'react';
import {Select,message } from 'antd';
import {util,emitter} from '../../common/common';
import {prefix,domain,productDomain,productPrefix} from '../../interfaces/interfaces';

export const allMetaData = {
  
};
export const allDictData = {};

export default class CommonSelect extends Component {
  static defaultProps = {
      appCode : '',
      allText : '请选择'
  };
  constructor(...args) {
    super(...args);
  }

  render(){
    let options = (allMetaData[this.props.dictCode] || []).filter(item=>{
      if(this.props.excludeValues && this.props.excludeValues.length>0 && this.props.excludeValues.indexOf(item.key) > -1){
        return false;
      }
      if(this.props.includeValues){
        if(this.props.includeValues.length>0 && this.props.includeValues.indexOf(item.key) > -1){
          return true;
        }else{
          return false;
        }
      }
      return true;
    }).map(item=>{
      let {desc} = item;
      let {showKey,renderDesc,withTitle} = this.props;
      if(showKey){
        desc = desc + `[${item.key}]`;
      }else if(renderDesc){
        desc = renderDesc(item);
      }
      return (<Select.Option title={desc} key={item.key + ''} value={util.getStringValue(item.key)}>{desc}</Select.Option>)
    });
    let value = util.getStringValue(this.props.value);
    if(this.props.mode === 'multiple'){
      value = this.props.value;
    }
    return (
        <Select {...Object.assign({},this.props,{value})}>  
          {this.props.withAll ? [<Select.Option key="" value="">{this.props.allText}</Select.Option>].concat(options) : options}
        </Select>);
  }
}