import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox} from 'antd';
import moment from 'moment';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,tenantConfig,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';
export let bodyDomains=['会员','学员','教练','比赛','器材'];
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('billModal', (rec,options)=>{
        let formValues={roomId:rooms.length>0?rooms[0].id+'':undefined,billTime:moment()};
        if(rec){
          formValues=rec;
          formValues.billTime=moment(rec.billTime);
          if(rec.roomId)formValues.roomId=rec.roomId+'';
        }
        this.setState({visible:true,formValues,rec,options});
      
    });
  	this.state={
      formValues:{},
      visible:false
  	};
  }

  handleOk=()=>{
    util.validateFields.call(this,values=>{
      let {rec}=this.state;
      util.ajaxJson(urlPrefix+'BillOperation?opt='+(rec?'fixBill':'addBill'), {rec:values}, res=>{
        if(res.success){
          message.success(`${rec?'调':'记'}账成功!`);
          this.handleCancel();
        }
      });
    });
  }

  handleCancel=()=>{
    emitter.emit(`queryBillTable`, undefined, true);
    this.setState({visible:false,rec:undefined});
  }

  render() {
    let {visible,formValues,rec,options} = this.state;
  	return (<Modal title={`手工${rec?'调':'记'}账`} className="modal-form" visible={visible} width={1200} destroyOnClose onCancel={this.handleCancel} maskClosable={false} footer = {
        [<Button key="1" type="primary" size="large" onClick={this.handleOk}>
            {rec?'调':'记'}账
         </Button>,
         <Button key="2" type="ghost" size="large" onClick={this.handleCancel}>
            取消
         </Button>]
      }>
          <Form className="add-member-modal">
            <Form.Item className="" label="主体域" required>
              <Select disabled={!options||!options.manual} {...util.getField.bind(this)('domain',{rules:[{required:true,message:'必填'}]})}>
                {bodyDomains.map(it=>{
                  return <Select.Option key={it} value={it}>{it}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            <Form.Item className="" label="进出账" required>
              <Select {...util.getField.bind(this)('io',{rules:[{required:true,message:'必填'}]})}>
                {['收入','支出'].map(it=>{
                  return <Select.Option key={it} value={it}>{it}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            <Form.Item className="" label="分类" required>
              <Select {...util.getField.bind(this)('cate',{rules:[{required:true,message:'必填'}]})}>
                {tenantConfig.billCates.map(it=>{
                  return <Select.Option key={it} value={it}>{it}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            <Form.Item className="" label="子分类">
                <Input {...util.getField.call(this,'subCate',{})} />
            </Form.Item>
            <Form.Item className="" label="金额(元)" required>
              <InputNumber {...util.getField.bind(this)('amount',{rules:[{required:true,message:'必填'}]})} min={0} precision={2}/>
            </Form.Item>
            <Form.Item className="" label="记账事由" required>
                <Input {...util.getField.call(this,'reason',{rules:[{required:true,message:'必填'}]})} />
            </Form.Item>
            <Form.Item className="" label="备注">
                <Input {...util.getField.call(this,'relInfo',{})} />
            </Form.Item>
            <Form.Item className="" label="开始时间">
                <DatePicker {...util.getField.call(this,'billTime')} showTime/>
            </Form.Item>
            <Form.Item className="" label="球馆" required>
              <Select {...util.getField.bind(this)('roomId',{rules:[{required:true,message:'必填'}]})}>
                {rooms.map(it=>{
                  return <Select.Option key={it.id} value={it.id+''}>{it.name}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            
          </Form>
      </Modal>);
  }
}

export default BizComponent;