import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Tabs, Form, Select,Tree, Popconfirm, InputNumber} from 'antd';
import '../index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../../common/common';
import {urlPrefix,urlProductPrefix} from '../../../../interfaces/interfaces';
import CateCrudModal from './cateCrudModal';
export const cateTypes=['商品','供应商'];
export let convertWithChildren=function (treeNodes){
  let array=[];
  if(!treeNodes || !treeNodes.length)return;
  let parent,i = 0,obj = {};
  while(i < treeNodes.length ){
    let node = treeNodes[i++];
    node.id=node.id+'';
    if(node.pid)node.pid=node.pid+'';
    if(node.isLeaf===undefined){
      node.isLeaf=true;
    }
    if(!obj[node.id]){
      obj[node.id]=node;
    }else{
      for(let k in node){
        obj[node.id][k]=node[k];
      }
    }
    
    if(node.pid&&node.pid!=='-1'){
      parent = obj[node.pid];
      if(parent===undefined){
        obj[node.pid]=parent={};
      }
      parent.isLeaf=false;
      if(parent.children){
        parent.children.push(obj[node.id]);
      }else{
        parent.children = [obj[node.id]];
      }
      parent.children.sort((a,b)=>a.num-b.num);
    }else{
      array.push(obj[node.id]);
    }
  }
  array.sort((a,b)=>a.num-b.num);
  return array;
};
export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on('refresh-cate-data-mgr',this.initCateData);
    this.state={
        config:{},
        formValues:{
          type:cateTypes[0],
          treeData:[]
        }
    };
  }

  componentDidMount(){
    this.initCateData();
  }

  initCateData=()=>{
    let {formValues}=this.state;
    util.ajaxJson(urlPrefix+'SysConfigManager?opt=categoryList',{type:formValues.type},res=>{
      let expandedKeys=res.data.map(it=>it.id+'');
      let treeData=convertWithChildren(res.data);
      this.setState({treeData,expandedKeys});
    });
  }

  onExpand=(expandedKeys)=>{
    this.setState({expandedKeys});
  }

  loop = data => {
    if(!data)return data;
    let {formValues}=this.state;
    return data.map((item,idx) => {
      let hasChildren=item.children && item.children.length > 0;
      let addSubEl=<a onClick={()=>{emitter.emit('show-category-crud-modal',{opt:'insert',type:formValues.type,rec:{pid:item.id}})}}>添加子类</a>;
      let editEl=<a onClick={()=>{emitter.emit('show-category-crud-modal',{opt:'update',type:formValues.type,rec:item})}}>编辑</a>;
      let deleteEl=<Popconfirm title="确定删除？" onConfirm={()=>{
        if(hasChildren){
          return message.warning('请先删除子类目！');
        }
        util.ajaxJson(urlPrefix+'SysConfigManager?opt=deleteCategory',{id:item.id},res=>{
          if(res.success)message.success('删除成功');
          this.initCateData();
        });
      }}><a>删除</a></Popconfirm>;
      let upEl=idx>0&&<a onClick={()=>{
        let otherItem=data[idx-1];
        util.ajaxJson(urlPrefix+'SysConfigManager?opt=moveCategory',{id1:item.id,num1:otherItem.num,id2:otherItem.id,num2:item.num},res=>{
          if(res.success)message.success('上移成功');
          this.initCateData();
        });
      }}>上移</a>;
      let downEl=idx<data.length-1&&<a onClick={()=>{
        let otherItem=data[idx+1];
        util.ajaxJson(urlPrefix+'SysConfigManager?opt=moveCategory',{id1:item.id,num1:otherItem.num,id2:otherItem.id,num2:item.num},res=>{
          if(res.success)message.success('下移成功');
          this.initCateData();
        });
      }}>下移</a>;
      if(hasChildren) {
        return <Tree.TreeNode title={<div className="cate-node-c"><span>{item.cateName}</span><div>{addSubEl} {editEl} {deleteEl} {upEl} {downEl}</div></div>} key={item.id} value={item.id}>{this.loop(item.children)}</Tree.TreeNode>;
      }
      return <Tree.TreeNode title={<div className="cate-node-c"><span>{item.cateName}</span><div>{addSubEl} {editEl} {deleteEl} {upEl} {downEl}</div></div>} key={item.id} value={item.id} isLeaf={item.isLeaf}/>;
    });
  }

  render() {
    let {treeData,expandedKeys,formValues}=this.state;
    let nodes = this.loop(treeData);
    return (
      <div className="sys-config-c">
        <Form.Item label="请选择类目域">
          <Select {...util.getField.call(this,'type',{onChange:()=>{
            this.initCateData();
          }})}>
            {cateTypes.map(it=><Select.Option key={it} value={it}>{it}</Select.Option>)}
          </Select>
        </Form.Item>
        <div>
          <Button type="primary" onClick={()=>emitter.emit('show-category-crud-modal',{opt:'insert',type:formValues.type})}>添加分类</Button>
          
        </div>
        
        <Tree className="tree-selector" showLine expandedKeys={expandedKeys} onExpand={this.onExpand}
          onSelect={this.onSelect}
        >
          {nodes}
        </Tree>
        <CateCrudModal />
      </div>
    );
  }
}