import React,{Component} from 'react';
import {message,Input,Modal,Button,Form,Select,Collapse,Popconfirm,Tooltip,Popover,Icon } from 'antd';
import {util,emitter} from '../../common/common';
import './index.less';
export default class CommonComp extends Component {
	static defaultProps={
        type:'Input'
    }
	constructor(...args) {
		super(...args);
		let {value}=this.props;
	    this.state={formValues:{value},status:'label'};
	}
	componentDidUpdate(preProps){
		if(this.props.value!=preProps.value){
			let {formValues}=this.state;
			formValues.value=this.props.value;
			this.setState({formValues});
		}
	}
	onEditClick=()=>{
		let {mask}=this.props;
		let {formValues}=this.state;
		if(mask&&formValues.value){
			formValues.value=formValues.value.substring(0,2);
		}
		this.setState({status:'edit',formValues});
		setTimeout(()=>{
			if(this.el){
				this.el.focus();
			}
		},20);
	}

	onBlur=()=>{
		let {formValues}=this.state;
		this.props.onBlur(formValues.value);
		// if(!this.props.silence){
		// 	message.success('保存成功！');
		// }
		this.setState({status:'label'});
	}
	onCall=()=>{
		let {formValues}=this.state;
		let callee=formValues.value;
		emitter.emit('call-out-ccc',callee,this);
	}
	render(){
		let {value,type,enums,mask,contactType}=this.props;
		if(contactType=='电话'&&mask===undefined)mask=true;
		let {status,formValues}=this.state;
	    return <span className="editable-field-c">
			{status=='edit'&&<>
				{type=='Input'&&<Input ref={el=>{
					this.el=el;
				}} {...util.getField.call(this,'value')} onBlur={this.onBlur} onPressEnter={this.onBlur}></Input>}
				{type=='Select'&&<Select {...util.getField.call(this,'value',{onChange:this.onBlur})}>
					{enums.map(it=><Select.Option key={it.key} value={it.key}>{it.text}</Select.Option>)}
				</Select>}
			</>}
			{status=='label'&&<span>{mask?<Popconfirm title={"确定发起外呼？"} okText="好" cancelText="取消" onConfirm={this.onCall}><a>{util.tuomin(formValues.value,2)}</a></Popconfirm>:(contactType=='钉钉'?<a href={`dingtalk://dingtalkclient/action/sendmsg?dingtalk_id=${formValues.value}`}>{formValues.value}</a>:(contactType=='旺旺'?<a href={`aliim:sendmsg?toid=cntaobao${formValues.value}`}>{formValues.value}</a>:formValues.value))}
				{mask&&<Popconfirm title={"确定修改？敏感字段不会记住原来的值"} onConfirm={this.onEditClick}><Icon type="highlight"/></Popconfirm>}
				{!mask&&<Icon type="highlight" onClick={this.onEditClick}/>}
			</span>}
		</span>;
	}
}